import { ConfigData } from '../../state/models/ConfigData';

export interface RoleData {
    pos: number;
    name: string;
}

export const initFromConfig = (input: ConfigData): RoleData[] => {
    const currentPrio = input.configValue.split(',');
    const result: RoleData[] = [];
    let nextPos: number = 1;
    currentPrio.forEach(role => {
        nextPos =
            result.push({
                pos: nextPos,
                name: role
            }) + 1;
    });
    return result;
};

export const mapArrayToString = (input: RoleData[]): string => {
    let result: string = '';
    input.forEach(roleData => {
        if (result !== '') {
            result += ',';
        }
        result += roleData.name;
    });
    return result;
};
