import React, { FormEvent, useState } from 'react';

import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import ForgotPassword from './password/ForgotPassword';
import { StateActionType } from '../state/StateAction';
import ValidatedTextfield from '../common/ValidatedTextfield';
import { getUserData } from '../state/models/UserData';
import { useAccountService } from './AccountService';
import { useStateValue } from '../state/State';
import { useTranslation } from 'react-i18next';

const LoginDialog = () => {
    const { t } = useTranslation();
    const [{ user }, dispatch] = useStateValue();
    const [errorMessage, setErrorMessage] = useState('');
    const [password, setPassword] = useState('');
    const [isPasswordValid, setPasswordValid] = useState(false);
    const service = useAccountService();

    function startLogin() {
        service.login(user.userName, password, {
            success: response => {
                const userData = getUserData(
                    user.userName,
                    response.data.displayName,
                    false,
                    false,
                    false,
                    response.data.authenticated,
                    response.data.authenticatedAgainstBackend,
                    response.data.remainingSessionSeconds
                );
                dispatch({
                    type: StateActionType.UPDATE_USER,
                    data: userData
                });
                dispatch({ type: StateActionType.TRIGGER_UPDATE, data: new Date() });
            },
            failure: error => {
                if (error.response !== undefined) {
                    setErrorMessage(error.response.data.message);
                }
            }
        });
    }

    function submit(event: FormEvent) {
        event.preventDefault();
        startLogin();
    }

    function onPwChange(isValid: boolean, input: string) {
        setPassword(input);
        setPasswordValid(isValid);
    }

    // PW is mandatory and can't be empty
    function validatePassword(input: string): boolean {
        return !(!input || /^\s*$/.test(input));
    }

    return (
        <Dialog
            id='dialog-login-online'
            className='blue'
            open={user.isAuthenticatedAgainstBackend && user.checkLocalPw && !user.isNewUser}
            disableBackdropClick
            disableEscapeKeyDown
        >
            <form onSubmit={submit}>
                <DialogTitle id='dialog-login-title' className='login-dialog-title blue'>
                    {t('login_dialog_title')}
                </DialogTitle>
                <DialogContent className='login-dialog-content'>
                    <div id='dialog-login-user-id' className='login-dialog-user'>
                        {t('login_dialog_user_id')}
                        {user.displayName}
                    </div>
                    <ValidatedTextfield
                        id='dialog-login-txt-pw'
                        label={t('login_dialog_label_pw')}
                        className='login-textfield'
                        errorMessage={t('login_dialog_error_pw')}
                        type='password'
                        validateInput={validatePassword}
                        onChange={onPwChange}
                    />
                    <ForgotPassword userName={user.userName} />
                    <div id='dialog-login-error-message' className='login-dialog-error-message'>
                        {errorMessage}
                    </div>
                </DialogContent>
                <DialogActions>
                    <a className='cancel-logout-link' href='/users/logout/oauth'>
                        <Button id='dialog-login-btn-cancel' color='primary' variant='outlined'>
                            {t('common_cancel')}
                        </Button>
                    </a>
                    <Button
                        id='dialog-login-btn-submit'
                        color='primary'
                        type='submit'
                        variant='contained'
                        onClick={submit}
                        disabled={!isPasswordValid}
                    >
                        {t('login_dialog_btn_submit')}
                    </Button>
                </DialogActions>
            </form>
        </Dialog>
    );
};

export default LoginDialog;
