/* eslint-disable eqeqeq */

import React from 'react';
import { Slide } from '@material-ui/core';

export interface DetailsProperties {
    currentSelection: any;
    renderDetails: (row: any) => JSX.Element;
}

export const DETAILS_PANEL_HEIGHT = 240;
export const DETAILS_PANEL_ANIMATION_DURATION = 300;

const DetailsPanel = (props: DetailsProperties) => {
    return (
        <div id='details-panel' style={{ overflowY: 'hidden' }}>
            <Slide
                direction='up'
                // eslint-disable-next-line eqeqeq
                in={props.currentSelection != undefined}
                mountOnEnter
                unmountOnExit
                {...(props.currentSelection != undefined ? { timeout: DETAILS_PANEL_ANIMATION_DURATION } : {})}
            >
                {props.renderDetails && props.renderDetails(props.currentSelection)}
            </Slide>
        </div>
    );
};

export default DetailsPanel;
