import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core';

import { ImportResult } from './ImportCertificates';
import React from 'react';
import { useTranslation } from 'react-i18next';

interface ImportCertificatesResultProps {
    messages: ImportResult[];
    dialogVisible: (open: boolean) => void;
}

class ImportResultText {
    fileName: string;
    message: string;
    isPKCS12: boolean;

    constructor(fileName: string, message: string, isPKCS12: boolean) {
        this.fileName = fileName;
        this.message = message;
        this.isPKCS12 = isPKCS12;
    }
}

const ImportCertificatesResultDialog = (props: ImportCertificatesResultProps) => {
    const { t } = useTranslation();

    const asText: ImportResultText[] = [];

    var failureCount = 0;
    var successCount = 0;

    var isPKCS12 = false;
    var fileName = ' ';
    for (var i = 0; i < props.messages.length; i++) {
        const m = props.messages[i];
        isPKCS12 = fileName === m.fileName;
        fileName = m.fileName;

        var text = ' ';
        if (m.success) {
            successCount++;
            text = t('certificates_import_success');
        } else {
            failureCount++;
            text = t('certificates_import_failure');
        }
        text += ' ' + m.message;

        asText.push(new ImportResultText(fileName, text, isPKCS12));
    }

    function renderCertificateParagraph(text: ImportResultText) {
        if (text.isPKCS12) {
            return <>{text.message}</>;
        }
        return (
            <>
                <br />
                {t('certificates_import_file') + ' "' + text.fileName + '"'} <br /> {text.message}
            </>
        );
    }

    function close(): void {
        props.dialogVisible(false);
    }

    return (
        <Dialog id='import-result-dialog' open={true} fullWidth maxWidth={'md'}>
            <DialogTitle id='import-result-header' className='blue'>
                {t('certificates_import_result_dialog_title')}
            </DialogTitle>
            <DialogContent id='import-result-summary'>
                <div id='import-result-summary-success' className='padding-no'>
                    <span className='bold'>
                        {t('certificates_import_success_count', {
                            number: successCount
                        })}
                    </span>
                </div>
                <div id='import-result-summary-failure' className='padding-no'>
                    <span className='bold'>
                        {t('certificates_import_failure_count', {
                            number: failureCount
                        })}
                    </span>
                </div>
            </DialogContent>
            <DialogContent id='import-result-list'>
                {asText.map((txt: ImportResultText, index: number) => {
                    return (
                        <span key={index}>
                            {renderCertificateParagraph(txt)} <br />
                        </span>
                    );
                })}
            </DialogContent>
            <DialogActions id='import-result-buttons'>
                <Button id='import-result-close-btn' onClick={close} color='primary' variant='outlined'>
                    {t('common_close')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default ImportCertificatesResultDialog;
