import React, { useEffect } from 'react';

import SessionTimeSingleton from './state/SessionTimeSingleton';
import { StateActionType } from './state/StateAction';
import { getUserData } from './state/models/UserData';
import { useAPI } from './common/API';
import { useAccountService } from './account/AccountService';
import { useStateValue } from './state/State';
import { useUserConfigService } from './configurations/store/UserConfigStore';

// Based on the useEffect hook this component's code is executed once on mounting.
const StartUpHandler = () => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [, dispatch] = useStateValue();
    const API = useAPI();
    const accountService = useAccountService();
    const userConfigService = useUserConfigService();

    useEffect(() => {
        userConfigService.refreshBackground();
        fetchLoginErrors();
        fetchAlertMessages();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    function fetchLoginErrors() {
        accountService.getLoginErrors({
            success: response => {
                if (response.data !== undefined && response.data.length > 0) {
                    dispatch({
                        type: StateActionType.UPDATE_MESSAGE,
                        data: {
                            content: response.data,
                            isError: true,
                            postHandler: postHandler
                        }
                    });
                } else {
                    fetchActiveProfiles();
                    fetchUserData();
                }
            }
        });
    }

    function postHandler() {
        accountService.onlineLogin();
    }

    function fetchUserData() {
        accountService.getUserData({
            success: response => {
                const displayName =
                    response.data.firstName + ' ' + response.data.lastName + '(ID: ' + response.data.userName + ')';
                const isAuthenticated = !response.data.checkLocalPassword && !response.data.defaultUser;
                SessionTimeSingleton.getInstance().notifyReset(response.data.remainingSessionSeconds);
                const userData = getUserData(
                    response.data.userName,
                    displayName,
                    response.data.checkLocalPassword,
                    response.data.defaultUser,
                    response.data.newUser,
                    isAuthenticated,
                    response.data.authenticationAgainstBackend,
                    response.data.remainingSessionSeconds
                );
                dispatch({
                    type: StateActionType.UPDATE_USER,
                    data: userData
                });
            }
        });
    }

    function fetchActiveProfiles() {
        API.getBackground('/configurations/activeProfiles', {
            success: response => {
                if (response.data.includes('AFTERSALES')) {
                    dispatch({
                        type: StateActionType.UPDATE_XENTRY,
                        data: true
                    });
                }
            }
        });
    }

    function fetchAlertMessages() {
        API.getBackground('/system/alertMessages', {
            success: response => {
                dispatch({
                    type: StateActionType.UPDATE_MESSAGE_ALERT,
                    data: { ids: response.data }
                });
            }
        });
    }

    return <span></span>;
};

export default StartUpHandler;
