import React, { useState } from 'react';

import CertificateValidationDialog from './CertificateValidationDialog';
import { StateActionType } from '../../state/StateAction';
import Switch from '@material-ui/core/Switch';
import SwitchConfigProps from './SwitchConfigProps';
import { useStateValue } from '../../state/State';
import { useUserConfigService } from '../store/UserConfigStore';

const SwitchConfigComponent = (props: SwitchConfigProps) => {
    // App state
    const [isDialogVisible, setDialogVisible] = useState<boolean>(false);
    const [{ config }, dispatch] = useStateValue();
    const appConfig = config[props.configKey];

    const userConfigService = useUserConfigService();

    var isChecked = (props.values ? props.values.get(true) : 'true') === appConfig.configValue;

    function updateConfig(checked: boolean) {
        appConfig.configValue = getParsedValue(checked);
        userConfigService.update(appConfig);
        dispatch({
            type: StateActionType.UPDATE_CONFIG,
            data: config
        });
    }

    function closeDialog(confirmed: boolean) {
        if (confirmed) {
            updateConfig(false);
        }
        setDialogVisible(false);
    }

    function getParsedValue(checked: boolean) {
        if (props.values) {
            return props.values.get(checked);
        } else {
            return Boolean(checked).toString();
        }
    }

    return (
        <>
            {props.dialogEnabled && isDialogVisible && <CertificateValidationDialog closeDialog={closeDialog} />}
            <div className='switch-config-component'>
                <Switch
                    id={'switch-' + appConfig.configKey.toLowerCase()}
                    className='config-switch'
                    checked={isChecked}
                    onChange={(event, checked) => {
                        if (props.dialogEnabled) {
                            if (!checked) {
                                setDialogVisible(true);
                            } else {
                                updateConfig(checked);
                            }
                        } else {
                            updateConfig(checked);
                        }
                    }}
                />
                <span id={'switch-heading-' + appConfig.configKey.toLowerCase()} className='config-heading'>
                    {props.title}
                </span>
                <div id={'switch-description-' + appConfig.configKey.toLowerCase()} className='config-description'>
                    {props.description}
                </div>
            </div>
        </>
    );
};

export default SwitchConfigComponent;
