import { Button } from '@material-ui/core';
import React from 'react';
import RefreshIcon from '@material-ui/icons/Refresh';
import { useTranslation } from 'react-i18next';

interface RefreshCertificatesProps {
    refreshCallBack: () => void;
}

const RefreshCertificates = (props: RefreshCertificatesProps) => {
    const { t } = useTranslation();

    return (
        <>
            <Button
                id='certificates-refresh-btn'
                color='primary'
                variant='outlined'
                className='certificates-toolbar-button'
                onClick={() => props.refreshCallBack()}
            >
                <RefreshIcon className='btn-icon-left' />
                {t('certificates_refresh_btn')}
            </Button>
        </>
    );
};

export default RefreshCertificates;
