export interface UserData {
    userName: String;
    displayName: String;
    checkLocalPw: boolean;
    isDefaultUser: boolean;
    isAuthenticated: boolean;
    isNewUser: boolean;
    isAuthenticatedAgainstBackend: boolean;
    remainingSessionSeconds: number;
}

export function isUserData(input: any): input is UserData {
    return (
        input.isDefaultUser !== undefined &&
        input.isAuthenticated !== undefined &&
        input.isAuthenticatedAgainstBackend !== undefined
    );
}

export function getUserData(
    userName: string,
    displayName: string,
    checkLocalPw: any,
    isDefaultUser: any,
    isNewUser: any,
    isAuthenticated: any,
    isAuthenticatedAgainstBackend: any,
    remainingSessionSeconds: number
): UserData {
    const userData = {
        userName: userName,
        displayName: displayName,
        checkLocalPw: checkLocalPw === true || checkLocalPw === 'true',
        isDefaultUser: isDefaultUser === true || isDefaultUser === 'true',
        isNewUser: isNewUser === true || isNewUser === 'true',
        isAuthenticated: isAuthenticated === true || isAuthenticated === 'true',
        isAuthenticatedAgainstBackend:
            isAuthenticatedAgainstBackend === true || isAuthenticatedAgainstBackend === 'true',
        remainingSessionSeconds: remainingSessionSeconds
    };
    return userData;
}
