export interface SelectableCertificate {
    id: string;
    subject: string;
    serialNo: string;
    uniqueECUID: string;
    issuer: string;
    description: string;
    validToDateTime: string;
    userRole: string;
    targetECU: string;
    targetVIN: string;
    nonce: string;
    targetSubjectKeyIdentifier: string;
    services: string;
    baseCertificateID: string;
    subjectKeyIdentifier: string;
}

export function isValidCert(input: any): input is SelectableCertificate {
    return input.id !== undefined && input.description !== undefined;
}
