import { useEffect, useState } from 'react';

import { ExportType } from './ExportDialog';
import { useCertificatesService } from '../CertificatesService';

/**
 * Hook that fetches the export values of a certificate. The certificate
 * is identified by its ID that is passed to this hook.
 */
export function useExportCertificate(certificateId: string) {
    const [typeToExportMap, setTypeToExportMap] = useState<Map<ExportType, string>>(new Map<ExportType, string>());
    const certificatesService = useCertificatesService();

    useEffect(() => {
        certificatesService.getExport(certificateId, {
            success: response => {
                const typeToExportMap = new Map<ExportType, string>();
                typeToExportMap.set(ExportType.CONFIG_5, response.data.configurator5);
                typeToExportMap.set(ExportType.C_ARRAY, response.data.cArray);
                typeToExportMap.set(ExportType.CA_NOE, response.data.canoe);
                setTypeToExportMap(typeToExportMap);
            }
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return typeToExportMap;
}
