import { CertificateData, CertificateType } from './CertificateData';
import { Column, Query } from '@material-table/core';
import React, { useEffect, useState } from 'react';
import { defaultTableHeaderStyle, getCommonTranslation } from '../common/table/ZenzefiTable';

import { APICallback } from '../common/API';
import CertificateDetailsPanel from './details/CertificateDetailsPanel';
import CertificatesToolbar from './CertificatesToolbar';
import SelectableTable from '../common/table/SelectableTable';
import { makeStyles } from '@material-ui/core';
import { useCertificatesService } from './CertificatesService';
import { useColumns } from './CertificatesColumns';
import { useStateValue } from '../state/State';
import { useTranslation } from 'react-i18next';
import { useUserConfigService } from '../configurations/store/UserConfigStore';

const CertificatesTable = () => {
    // Component state
    const [selectedRow, setSelectedRow] = useState<any>();
    const [table, setTable] = useState<any>(undefined);
    const [isDeleteKeyPressed, setDeleteKeyPressed] = useState<boolean>(false);

    const [isColumnShifted, setIsColumnShifted] = useState<boolean>(false);

    // App state
    const [{ lastUpdateTime, user }] = useStateValue();

    // Hook initialization
    const userConfigService = useUserConfigService();
    const certificatesService = useCertificatesService();
    const { t } = useTranslation();

    // Table column definitions
    const columns = useColumns({ isColumnShifted, setIsColumnShifted });

    // Update certificates when lastUpdateTime value changes
    useEffect(() => {
        refreshData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [lastUpdateTime]);

    useEffect(() => {
        if (user.userName === '') {
            removeFilters();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [user]);

    function updateCurrentSelection(newSelection: any) {
        setSelectedRow(newSelection);
    }

    function refreshCertificateData(query: Query<CertificateData>, resolve: Function) {
        const callback: APICallback = {
            success: response => {
                resolve({ data: response.data });
            }
        };
        certificatesService.getFilteredCerts(query, callback);
    }

    function removeFilters() {
        if (table && table.dataManager.columns) {
            var changed: boolean = false;
            for (var i = 0; i < table.dataManager.columns.length; i++) {
                if (table.dataManager.columns[i].tableData.filterValue) {
                    changed = true;
                    table.dataManager.changeFilterValue(i, undefined);
                }
            }
            if (changed) {
                table.setState({}, table.onFilterChangeDebounce);
            }
        }
    }

    function refreshData() {
        if (table) {
            table.onQueryChange();
        }
    }

    function deleteDataByKey(flag: boolean) {
        setDeleteKeyPressed(flag);
    }

    const useStyles = makeStyles(() => {
        const map = Array.from(Array(7).keys()).reduce((map: any, level: number) => {
            map[`& .MuiTable-root .MuiTableRow-root[level='${level + 1}']>td:nth-child(2)`] = {
                paddingLeft: 14 * (level + 1) + 'px !important'
            };
            return map;
        }, {});
        return {
            table: {
                ...map
            }
        };
    });

    return (
        <div id='certificate-table' className={useStyles().table}>
            <SelectableTable
                keyboardNavigation={true}
                modelIdentifier='id'
                renderDetails={CertificateDetailsPanel}
                tableRef={setTable}
                updateSelection={updateCurrentSelection}
                deleteDataByKey={deleteDataByKey}
                contextMenuType='CertificatesContextMenu'
                tableProps={{
                    title: (
                        <CertificatesToolbar
                            selection={selectedRow}
                            refreshCallBack={refreshData}
                            deleteDataByKey={deleteDataByKey}
                            deleteKeyPressed={isDeleteKeyPressed}
                        />
                    ),
                    parentChildData: (row: any, rows: any) => rows.find((a: { id: any }) => a.id === row.parentId),
                    columns: columns,
                    localization: getCommonTranslation(t),
                    onChangeColumnHidden: (column: Column<any>, hidden: boolean) =>
                        userConfigService.setCertsColumnVisibility(table),
                    onColumnDragged: (oldPos: number, newPos: number) => {
                        userConfigService.setCertsColumnOrder(table, oldPos, newPos, {
                            success: () => setIsColumnShifted(true)
                        });
                    },
                    options: {
                        search: false,
                        filtering: true,
                        draggable: true,
                        header: true,
                        sorting: true,
                        columnsButton: true,
                        showTitle: true,
                        toolbar: true,
                        paging: false,
                        defaultExpanded: (row: any) => row.type !== CertificateType.VIRTUAL_FOLDER,
                        maxBodyHeight: '700px',
                        headerStyle: defaultTableHeaderStyle,
                        filterCellStyle: {
                            backgroundColor: '#FFF'
                        },
                        debounceInterval: 600
                    },
                    data: (query: Query<CertificateData>) =>
                        new Promise((resolve, reject) => refreshCertificateData(query, resolve))
                }}
            />
        </div>
    );
};

export default CertificatesTable;
