import { UpdateType } from '../../certificates/update/UpdateCertificates';

export interface UpdateCertificatesProcessData {
    type?: UpdateType;
    detailsStep: string;
    details: string;
    updateCertificatesRetryInfo: {
        endpoint: string;
        maxRetries: number;
        currentRetry: number;
        nextRetryTimestamp: number;
    };
    errorMetrics: boolean;
    didFailAllRetries: boolean;
    running: boolean;
}

export function isUpdateProcessData(input: any): input is UpdateCertificatesProcessData {
    return input.running !== undefined;
}
