import React, { ErrorInfo, ReactNode } from 'react';

import Error500Component from './Error500Component';

interface ContentErrorBoundaryProps {
    callback: () => void;
    children: ReactNode;
}

interface ContentErrorBoundaryState {
    hasError: boolean;
    errorReason: string;
}

export class ContentErrorBoundary extends React.Component<ContentErrorBoundaryProps, ContentErrorBoundaryState> {
    constructor(props: ContentErrorBoundaryProps) {
        super(props);
        this.state = { hasError: false, errorReason: '' };
    }

    static getDerivedStateFromError(error: any) {
        // Update state so the next render will show the fallback UI
        return { hasError: true, errorReason: error.message };
    }

    componentDidCatch(error: Error, errorInfo: ErrorInfo) {
        console.log('Catched error: ' + error.message);
    }

    removeError() {
        this.setState({ hasError: false, errorReason: '' });
    }

    render() {
        if (this.state.hasError) {
            return <Error500Component reason={this.state.errorReason} callback={this.props.callback} />;
        }
        return this.props.children;
    }
}
