import { DateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import { Input, InputAdornment } from '@material-ui/core';

import { CSSProperties } from '@material-ui/core/styles/withStyles';
import DateFnsUtils from '@date-io/date-fns';
import DateRangeIcon from '@material-ui/icons/DateRange';
import React from 'react';
import deLocale from 'date-fns/locale/de';
import enLocale from 'date-fns/locale/en-US';
import { formatTimestamp } from '../../../common/DateFormat';
import { useTranslation } from 'react-i18next';

interface DateTimeLayoutProps {
    layoutId: string;
    title: string;
    selectedDate: string;
    updateSelectedDate: (selectedDate: any) => void;
    style: CSSProperties;
}

const DateTimeLayout = (props: DateTimeLayoutProps) => {
    const { i18n } = useTranslation();

    const localeMap: any = {
        en: enLocale,
        de: deLocale
    };

    function displayDate(date: any): string {
        if (date === undefined || date === '') {
            return '';
        }
        return formatTimestamp(date.getTime(), false);
    }

    return (
        <div id={props.layoutId} style={{ ...props.style }}>
            <span style={{ fontWeight: 'bold' }}>{props.title}:</span>
            <br />
            <Input
                value={displayDate(props.selectedDate)}
                readOnly={true}
                startAdornment={
                    <InputAdornment position='start'>
                        <DateRangeIcon />
                    </InputAdornment>
                }
            />
            <div style={{ display: 'table', border: '1px dotted' }}>
                <MuiPickersUtilsProvider utils={DateFnsUtils} locale={localeMap[i18n.language]}>
                    <DateTimePicker
                        open={true}
                        value={props.selectedDate}
                        onChange={props.updateSelectedDate}
                        variant='static'
                    />
                </MuiPickersUtilsProvider>
            </div>
        </div>
    );
};

export default DateTimeLayout;
