import MaterialTable, { Column, Query } from '@material-table/core';

import { APICallback } from '../../../common/API';
import { ActiveEnhancedRightsCert } from './ActiveEnhancedRightsCert';
import React from 'react';
import { getCommonTranslation } from '../../../common/table/ZenzefiTable';
import { useCertificatesService } from '../../CertificatesService';
import { useSelectionUpdate } from '../selection/SelectionUpdate';
import { useTranslation } from 'react-i18next';

enum ActiveEnhancedRightsColumn {
    Issuer = 'issuer',
    BaseCertId = 'baseCertificateID',
    Services = 'services',
    TargetEcu = 'targetECU',
    TargetVin = 'targetVIN',
    ValidToDateTime = 'validToDateTime'
}

interface ActiveEnhancedRightsTableProps {
    setTable: any;
    refreshView: () => void;
}
const ActiveEnhancedRightsTable = (props: ActiveEnhancedRightsTableProps) => {
    const { t } = useTranslation();
    const certificatesService = useCertificatesService();
    const selectionUpdate = useSelectionUpdate();

    const tableColumns: Column<ActiveEnhancedRightsCert>[] = [
        {
            title: t('certificates_issuer_title'),
            field: ActiveEnhancedRightsColumn.Issuer
        },
        {
            title: t('certificates_manual_selection_holder_title'),
            field: ActiveEnhancedRightsColumn.BaseCertId,
            width: '200px'
        },
        {
            title: t('certificates_services_title'),
            field: ActiveEnhancedRightsColumn.Services,
            width: '350px'
        },
        {
            title: t('certificates_target_ecu_title'),
            field: ActiveEnhancedRightsColumn.TargetEcu
        },
        {
            title: t('certificates_target_vin_title'),
            field: ActiveEnhancedRightsColumn.TargetVin
        },
        {
            title: t('certificates_valid_to_title'),
            field: ActiveEnhancedRightsColumn.ValidToDateTime
        }
    ];
    const refreshData = (query: Query<ActiveEnhancedRightsCert>, resolve: Function) => {
        const callback: APICallback = {
            success: response => {
                resolve({ data: response.data });
            }
        };
        certificatesService.getActiveEnhancedRightsCerts(callback);
    };

    /**
     * Returns the common table translation and removes the header of the 'actions' column.
     */
    function getTableTranslation(): object {
        const translation: any = getCommonTranslation(t);
        translation['header'] = { actions: '' };
        return translation;
    }

    /**
     * Deactivates the certificate and refreshes the table.
     *
     * @param rowData the deactivated certificate
     */
    function deactivate(rowData: ActiveEnhancedRightsCert) {
        selectionUpdate.deactivateCert(rowData.id, { success: response => props.refreshView() });
    }

    /**
     * Sets the passed table reference to this component's state and
     * to the parent component via properties.
     *
     * @param tableRef the references to the MaterialTable
     */
    function setTableRefs(tableRef: any) {
        props.setTable(tableRef);
    }

    return (
        <div>
            <MaterialTable
                columns={tableColumns}
                tableRef={setTableRefs}
                options={{
                    headerStyle: {
                        zIndex: 0
                    },
                    showTitle: false,
                    header: true,
                    search: false,
                    sorting: true,
                    toolbar: false,
                    paging: false,
                    draggable: false,
                    maxBodyHeight: '400px'
                }}
                localization={getTableTranslation()}
                data={query =>
                    new Promise((resolve, _reject) => {
                        refreshData(query, resolve);
                    })
                }
                actions={[
                    rowData => ({
                        icon: 'close',
                        tooltip: t('certificates_manual_selection_deactivate_tooltip'),
                        onClick: (event: any, rowData: any) => deactivate(rowData)
                    })
                ]}
            />
        </div>
    );
};

export default ActiveEnhancedRightsTable;
