import { Button } from '@material-ui/core';
import React from 'react';
import RefreshIcon from '@material-ui/icons/Refresh';
import { useTranslation } from 'react-i18next';

interface RefreshCertsContextMenuProps {
    closeMenu: (refresh: boolean) => void;
}

const RefreshCertificatesContextMenu = (props: RefreshCertsContextMenuProps) => {
    const { t } = useTranslation();

    function handleClick() {
        props.closeMenu(true);
    }

    return (
        <Button id='context-menu-refresh-certs' className='certificate-context-menu-item-border' onClick={handleClick}>
            <RefreshIcon />
            {t('certificates_refresh_btn')}
        </Button>
    );
};

export default RefreshCertificatesContextMenu;
