import { Button, Dialog, DialogActions, DialogContent, Input } from '@material-ui/core';
import React, { useState } from 'react';

import DateTimeLayout from './DateTimeLayout';
import { formatTimestamp } from '../../../common/DateFormat';
import { useTranslation } from 'react-i18next';

const RangeDateTimeFilter = ({ columnDef, onFilterChanged }: { columnDef: any; onFilterChanged: any }) => {
    const { t } = useTranslation();
    const [startDate, setStartDate] = useState<any>(undefined);
    const [endDate, setEndDate] = useState<any>(undefined);
    const [oldStartDate, setOldStartDate] = useState<any>(undefined);
    const [oldEndDate, setOldEndDate] = useState<any>(undefined);
    const [open, setOpen] = useState<boolean>(false);

    function updateStartDate(date: Date) {
        if (isValidDate(date)) {
            date.setSeconds(0, 0);
            setStartDate(date);
        }
    }

    function updateEndDate(date: Date) {
        if (isValidDate(date)) {
            date.setSeconds(0, 0);
            setEndDate(date);
        }
    }

    function handleClickOpen() {
        setOpen(true);
    }

    function handleClear() {
        updateSelectedDates('', '');
        updateOldSelectedDates('', '');
        setOpen(false);

        filter('', '');
    }

    function handleCancel() {
        updateSelectedDates(oldStartDate, oldEndDate);
        setOpen(false);
    }

    function handleOk() {
        if (isValidDate(startDate) && isValidDate(endDate) && startDate > endDate) {
            updateOldSelectedDates(endDate, startDate);
            updateSelectedDates(endDate, startDate);
            filter(endDate, startDate);
        } else {
            updateOldSelectedDates(startDate, endDate);
            filter(startDate, endDate);
        }
        setOpen(false);
    }

    function isValidDate(date: any) {
        return date && Object.prototype.toString.call(date) === '[object Date]' && !isNaN(date);
    }

    function updateSelectedDates(startDate: any, endDate: any) {
        setStartDate(startDate);
        setEndDate(endDate);
    }

    function updateOldSelectedDates(startDate: any, endDate: any) {
        setOldStartDate(startDate);
        setOldEndDate(endDate);
    }

    function filter(start: any, end: any) {
        const value = { ...columnDef.tableData.filterValue };
        value.startDate = start;
        value.endDate = end;
        onFilterChanged(columnDef.tableData.id, value);
    }

    function displayDate(date: any): string {
        if (isValidDate(date)) {
            return formatTimestamp(date.getTime(), false);
        }
        return '';
    }

    function displayRangeDate(): string {
        if (isValidDate(startDate) || isValidDate(endDate)) {
            return displayDate(startDate) + ' ' + t('range_date_time_to') + ' ' + displayDate(endDate);
        }
        return '';
    }

    return (
        <>
            <Input value={displayRangeDate()} onClick={handleClickOpen} />
            <Dialog open={open} maxWidth={false}>
                <DialogContent id='range-date-time-content' style={{ display: 'flex', padding: '20px 10px 10px 10px' }}>
                    <DateTimeLayout
                        layoutId='range-date-time-start-date'
                        title={t('range_date_time_start_date')}
                        selectedDate={startDate}
                        updateSelectedDate={updateStartDate}
                        style={{ display: 'table', textAlign: 'center', marginBottom: '10px' }}
                    />
                    <DateTimeLayout
                        layoutId='range-date-time-end-date'
                        title={t('range_date_time_end_date')}
                        selectedDate={endDate}
                        updateSelectedDate={updateEndDate}
                        style={{ display: 'table', textAlign: 'center', marginLeft: '5px' }}
                    />
                </DialogContent>
                <DialogActions>
                    <Button id='range-date-time-clear-btn' onClick={handleClear} color='primary' variant='outlined'>
                        {t('common_clear')}
                    </Button>
                    <Button id='range-date-time-cancel-btn' onClick={handleCancel} color='primary' variant='outlined'>
                        {t('common_cancel')}
                    </Button>
                    <Button id='range-date-time-ok-btn' onClick={handleOk} color='primary' variant='outlined'>
                        {t('common_ok')}
                    </Button>
                </DialogActions>
            </Dialog>
        </>
    );
};

export default RangeDateTimeFilter;
