import { AxiosError, AxiosResponse } from 'axios';
import React, { useState } from 'react';
import { useStateValue } from '../../state/State';
import { StateActionType } from '../../state/StateAction';
import ConfirmRecoveryDialog from './ConfirmRecoveryDialog';
import { useAccountService } from '../AccountService';
import { useTranslation } from 'react-i18next';

interface ForgotPasswordProps {
    userName: string;
    successcallback?: () => void;
}

const ForgotPassword = (props: ForgotPasswordProps) => {
    const [isConfirmDialogVisible, setConfirmDialogVisible] = useState<boolean>(false);
    const [isMessageShown, setMessageShown] = useState<boolean>(false);
    const [isErrorShown, setErrorShown] = useState<boolean>(false);
    const [, dispatch] = useStateValue();
    const { t } = useTranslation();
    const accountService = useAccountService();

    function closeConfirmDialog(isConfirmed: boolean) {
        setConfirmDialogVisible(false);

        if (isConfirmed) {
            accountService.deleteUserByUsername(props.userName, {
                success: (response: AxiosResponse) => {
                    setMessageShown(true);
                    setErrorShown(false);
                    if (props.successcallback) {
                        props.successcallback();
                    }
                    dispatch({ type: StateActionType.TRIGGER_UPDATE, data: new Date() });
                },
                failure: (error: AxiosError) => {
                    if (error.response && error.response.status === 404) {
                        setMessageShown(false);
                        setErrorShown(true);
                    }
                }
            });
        }
    }

    function determineClassOfButton() {
        return props.userName && props.userName.length > 0
            ? 'btn-forgot-password-enabled'
            : 'btn-forgot-password-disabled';
    }

    function determineClassOfMessage() {
        return isMessageShown ? 'green' : isErrorShown ? 'red' : '';
    }

    function getMessage(): string {
        if (isMessageShown) {
            return t('dialog_forgot_password_error_success');
        } else if (isErrorShown) {
            return t('dialog_forgot_password_error_not_found');
        } else {
            return '';
        }
    }

    return (
        <>
            <span
                id='btn-forgot-password'
                className={determineClassOfButton()}
                onClick={() => {
                    if (props.userName && props.userName.length > 0) {
                        setConfirmDialogVisible(true);
                    }
                }}
            >
                {t('forgot_password_btn')}
            </span>
            {(isErrorShown || isMessageShown) && (
                <span id='btn-forgot-message' className={determineClassOfMessage()}>
                    {getMessage()}
                </span>
            )}
            {isConfirmDialogVisible && <ConfirmRecoveryDialog closeCallback={closeConfirmDialog} />}
        </>
    );
};

export default ForgotPassword;
