import React, { useEffect, useState } from 'react';

import { Button } from '@material-ui/core';
import CircularProgress from '@material-ui/core/CircularProgress';
import DownloadIcon from '@material-ui/icons/CloudDownload';
import MenuItem from '@material-ui/core/MenuItem';
import Popover from '@material-ui/core/Popover';
import { StateActionType } from '../../state/StateAction';
import UpdateCanceledDialog from './UpdateCanceledDialog';
import UpdateProgressDialog from './UpdateProgressDialog';
import { useAccountService } from '../../account/AccountService';
import { useCertificatesService } from '../CertificatesService';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';
import { useUpdateCertificatesStatusHook } from './UpdateStatusHook';

export enum UpdateType {
    FULL = 'full',
    DIFFERENTIAL = 'differential'
}

const UPDATE_MENU_ID = 'certificates-update-menu';

interface UpdateCertificateProps {
    refreshCallBack: () => void;
}

const UpdateCertificates = (props: UpdateCertificateProps) => {
    const [{ user, certsUpdateProcess, isXentry }, dispatch] = useStateValue();
    const { t } = useTranslation();
    const certificatesService = useCertificatesService();
    const accountService = useAccountService();
    useUpdateCertificatesStatusHook();

    const [updateMenuOpen, setUpdateMenuOpen] = useState<boolean>(false);
    const [updateMenuAnchor, setUpdateMenuAnchor] = useState<any>(undefined);
    const [isUpdateProgressDialogVisible, setUpdateProgressDialogVisible] = useState(false);
    const [isUpdateCanceledDialogVisible, setUpdateCanceledDialogVisible] = useState(false);

    useEffect(() => {
        if (user.isAuthenticatedAgainstBackend && certsUpdateProcess.running) {
            setUpdateProgressDialogVisible(certsUpdateProcess.running);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [certsUpdateProcess.running, user.isAuthenticatedAgainstBackend]);

    /**
     * Opens the update menu if update is not running currently. If the system is updating
     * the certificates the progress dialog is showing.
     *
     * @param event the click event
     */
    function handleUpdateMenuClick(event: any) {
        setUpdateMenuAnchor(event.target);
        if (certsUpdateProcess.running) {
            setUpdateProgressDialogVisible(true);
        } else {
            setUpdateMenuOpen(!updateMenuOpen);
        }
    }

    function closeProgressDialog(shouldCancelUpdate: boolean) {
        setUpdateProgressDialogVisible(false);
        if (shouldCancelUpdate) {
            certificatesService.stopUpdate({
                success: response => {
                    setUpdateCanceledDialogVisible(true);
                    // Importing clear integrity check results
                    dispatch({
                        type: StateActionType.UPDATE_INTEGRITY_CHECK_EXISTENCE,
                        data: false
                    });
                }
            });
        }
    }

    function closeCanceledDialog() {
        setUpdateCanceledDialogVisible(false);
    }

    function closeMenuAndShowProgress() {
        setUpdateProgressDialogVisible(true);
        setUpdateMenuOpen(false);
    }

    function startUpdateProcess(type: UpdateType) {
        if (certsUpdateProcess.running) {
            closeMenuAndShowProgress();
        } else {
            // Remove step and error data from earlier update process before starting the new one
            dispatch({
                type: StateActionType.UPDATE_CERT_UPDATE_STATUS,
                data: { running: true, type: type, errorMetrics: false }
            });
            certificatesService.startUpdate(type, {
                success: res => closeMenuAndShowProgress(),
                failure: error => {
                    console.log(error);
                    if (error.response && error.response.status === 406) {
                        dispatchInfoMessage('Update process is still running. Please wait');
                    }
                    dispatch({
                        type: StateActionType.UPDATE_CERT_UPDATE_STATUS,
                        data: { running: false, type: type, errorMetrics: false }
                    });
                }
            });
        }
    }

    function dispatchInfoMessage(message: String) {
        dispatch({
            type: StateActionType.UPDATE_MESSAGE,
            data: {
                content: message,
                isError: false
            }
        });
    }

    function shouldShowProgressDialog(): boolean {
        return (
            isUpdateProgressDialogVisible &&
            (certsUpdateProcess.running ||
                certsUpdateProcess.didFailAllRetries ||
                certsUpdateProcess.errorMetrics === true)
        );
    }

    function renderMenuItems() {
        let isXentryENV = isXentry === true || isXentry === 'true';
        if (user.isAuthenticated && !user.isAuthenticatedAgainstBackend && !isXentryENV) {
            return (
                <MenuItem id='btn-online-login' key='btn-online-login' onClick={() => accountService.onlineLogin()}>
                    {t('navigation_btn_user_online_login')}
                </MenuItem>
            );
        } else {
            return (
                <>
                    <MenuItem
                        id='certificates-differential-update-btn'
                        onClick={() => startUpdateProcess(UpdateType.DIFFERENTIAL)}
                    >
                        <span className='bold'> {t('certificates_differential_update_btn')}</span>
                    </MenuItem>
                    <MenuItem id='certificates-full-update-btn' onClick={() => startUpdateProcess(UpdateType.FULL)}>
                        {t('certificates_full_update_btn')}
                    </MenuItem>
                </>
            );
        }
    }

    return (
        <span>
            {shouldShowProgressDialog() && (
                <UpdateProgressDialog refreshCallBack={props.refreshCallBack} closeDialog={closeProgressDialog} />
            )}
            {isUpdateCanceledDialogVisible && <UpdateCanceledDialog closeDialog={closeCanceledDialog} />}
            <Button
                id='certificates-update-btn'
                color='primary'
                variant='outlined'
                aria-owns={updateMenuAnchor ? UPDATE_MENU_ID : undefined}
                aria-haspopup='true'
                disableFocusRipple={false}
                className='certificates-toolbar-button'
                onClick={handleUpdateMenuClick}
                disabled={user !== undefined && !user.isAuthenticated}
            >
                {certsUpdateProcess !== undefined && certsUpdateProcess.running ? (
                    <CircularProgress style={{ width: '1em', height: '1em', marginRight: '10px' }} />
                ) : (
                    <DownloadIcon className='btn-icon-left' />
                )}
                {t('certificates_update_btn')}
            </Button>
            <Popover
                id={UPDATE_MENU_ID}
                anchorEl={updateMenuAnchor}
                open={updateMenuOpen}
                onClose={handleUpdateMenuClick}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center'
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center'
                }}
            >
                {renderMenuItems()}
            </Popover>
        </span>
    );
};

export default UpdateCertificates;
