import Button from '@material-ui/core/Button';
import HelpIcon from '@material-ui/icons/HelpOutlineSharp';
import React from 'react';
import { Tooltip } from '@material-ui/core';
import { VIEW_LINKS } from '../header/Navigation';
import { useTranslation } from 'react-i18next';
import { withRouter } from 'react-router';

export const HELP_LINKS = {
    CERTIFICATES: '../../help/index.htm#t=Function_description.htm%233.4_Certificates_tabbc-10&rhtocid=_3_3',
    LOGGING: '../../help/index.htm#t=Function_description.htm%23Logging_tabbc-17&rhtocid=_3_4',
    CONFIGURATION: '../../help/index.htm#t=Function_description.htm%23Settings_tabbc-20&rhtocid=_3_5',
    ENVIRONMENT: '../../help/index.htm#t=Function_description.htm%23Installation_detailsbc-28&rhtocid=_3_6'
};

const HelpButton = React.memo(function HelpButton(props: any) {
    const { location } = props;
    const { t } = useTranslation();

    var helpLink = HELP_LINKS.CERTIFICATES;
    switch (location.pathname) {
        case VIEW_LINKS.CERTIFICATES:
            helpLink = HELP_LINKS.CERTIFICATES;
            break;
        case VIEW_LINKS.CONFIGURATION:
            helpLink = HELP_LINKS.CONFIGURATION;
            break;
        case VIEW_LINKS.LOGGING:
            helpLink = HELP_LINKS.LOGGING;
            break;
        case VIEW_LINKS.ENVIRONMENT:
            helpLink = HELP_LINKS.ENVIRONMENT;
            break;
    }

    return (
        <Tooltip title={t('help_button_tooltip').valueOf()}>
            <Button
                id='navigation-btn-help'
                color='primary'
                variant='text'
                href={helpLink}
                target='_blank'
                className='help-button'
            >
                <HelpIcon />
            </Button>
        </Tooltip>
    );
});

export default withRouter(HelpButton);
