import { Button } from '@material-ui/core';
import React from 'react';
import ReportProblemIcon from '@material-ui/icons/ReportProblem';
import { useAPI } from '../API';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

const MessageAlert = () => {
    const API = useAPI();
    const [{ messageAlert }] = useStateValue();

    const { t } = useTranslation();

    const renderAlert = () => {
        return (
            <div id='message-alert'>
                <Button id='message-alert-remove-btn' variant='text' onClick={removeFirstElement}>
                    x
                </Button>
                {renderMessages()}
            </div>
        );
    };

    const renderMessages = () => {
        let msgIds = messageAlert.ids;
        if (msgIds.length === 0) {
            return null;
        } else {
            return (
                <>
                    <ReportProblemIcon id='message-alert-icon' />
                    <span id='message-alert-text'>
                        {msgIds.length === 1 ? t(msgIds[0]) : t(msgIds[0]) + ' (+' + (msgIds.length - 1) + ')'}
                    </span>
                </>
            );
        }
    };

    const removeFirstElement = () => {
        API.deleteBackground('/system/alertMessages', {
            success: response => {
                // do nothing
            }
        });
    };

    return <>{messageAlert && messageAlert.ids.length > 0 && renderAlert()}</>;
};

export default MessageAlert;
