import MaterialTable, { Column } from '@material-table/core';
import React, { useEffect, useState } from 'react';

import { useAPI } from '../common/API';
import { useTranslation } from 'react-i18next';

enum EnvironmentColumn {
    Property = 'property',
    Value = 'value'
}

const EnvironmentComponent = () => {
    const { t } = useTranslation();
    const API = useAPI();
    const [envData, setEnvData] = useState([]);

    const columns: Column<any>[] = [
        {
            title: t('environment_property_title'),
            field: EnvironmentColumn.Property,
            width: '33%'
        },
        { title: t('environment_value_title'), field: EnvironmentColumn.Value, width: 'auto' }
    ];

    useEffect(() => {
        API.get('/system/installationDetails', t('environment_load_process'), {
            success: response => {
                setEnvData(response.data);
            }
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <div id='installation-details-content'>
            <div className='configurations-heading'>{t('installation_details_heading')}</div>
            <MaterialTable
                columns={columns}
                data={envData}
                options={{
                    filtering: false,
                    search: false,
                    showTitle: false,
                    paging: false,
                    toolbar: false,
                    draggable: false,
                    maxBodyHeight: '80vh'
                }}
                style={{ tableLayout: 'auto' }}
            />
        </div>
    );
};

export default EnvironmentComponent;
