import CertSelect from './selection/CertSelect';
import CertSelectEcu from './selection/CertSelectEcu';
import { CertType } from './selection/CertType';
import React from 'react';
import { useSelectionUpdate } from './selection/SelectionUpdate';
import { useTranslation } from 'react-i18next';

interface ReplacementPackageSelectionProps {
    activeCertificates: any;
    activeEcuCert: string;
    shouldLoadOnStartUp: boolean;
}

const ReplacementPackageSelection = (props: ReplacementPackageSelectionProps) => {
    const selectionUpdate = useSelectionUpdate();
    const { t } = useTranslation();

    function getIdentifiersForRootCertificate(): Map<string, string> {
        let map = new Map();

        map.set(t('certificates_subject_title'), 'subject')
            .set(t('certificates_serial_number_title'), 'serialNo')
            .set(t('certificates_skid_title'), 'subjectKeyIdentifier');

        return map;
    }

    function getIdentifiersForBackendCertificate(): Map<string, string> {
        let map = new Map();

        map.set(t('certificates_subject_title'), 'subject')
            .set(t('certificates_issuer_title'), 'issuer')
            .set(t('certificates_serial_number_title'), 'serialNo')
            .set(t('certificates_skid_title'), 'subjectKeyIdentifier');

        return map;
    }

    function getIdentifiersForRootLinkCertificate(): Map<string, string> {
        let map = new Map();

        map.set(t('certificates_subject_new_root_ca_title'), 'subject')
            .set(t('certificates_issuer_preceding_root_ca_title'), 'issuer')
            .set(t('certificates_valid_to_title'), 'validToDateTime');

        return map;
    }

    function getIdentifiersForBackendLinkCertificate(): Map<string, string> {
        let map = new Map();

        map.set(t('certificates_subject_title'), 'subject')
            .set(t('certificates_issuer_title'), 'issuer')
            .set(t('certificates_valid_to_title'), 'validToDateTime');

        return map;
    }

    function getIdentifiersForECUCertificate(): Map<string, string> {
        let map = new Map();

        map.set(t('certificates_subject_title'), 'subject')
            .set(t('certificates_issuer_title'), 'issuer')
            .set(t('certificates_serial_number_title'), 'serialNo')
            .set(t('certificates_unique_ecu_id_title'), 'uniqueECUID')
            .set(t('certificates_valid_to_title'), 'validToDateTime');

        return map;
    }

    return (
        <div className='manual-selection-dialog-tab-content' id = 'replacement-package-content'>
            <CertSelect
                type={CertType.Root}
                title='certificates_type_root'
                activate={selectionUpdate.activateCert}
                deactivate={selectionUpdate.deactivateCert}
                currentlyActiveCerts={props.activeCertificates}
                modelidentifiers={getIdentifiersForRootCertificate()}
                shouldLoadOnStartUp={props.shouldLoadOnStartUp}
                doUpdate={false}
                updateNumber={0}
            />
            <CertSelect
                type={CertType.Backend}
                title='certificates_type_backend'
                activate={selectionUpdate.activateCert}
                deactivate={selectionUpdate.deactivateCert}
                currentlyActiveCerts={props.activeCertificates}
                modelidentifiers={getIdentifiersForBackendCertificate()}
                shouldLoadOnStartUp={props.shouldLoadOnStartUp}
                doUpdate={false}
                updateNumber={0}
            />
            <CertSelect
                type={CertType.RootLink}
                title='certificates_type_root_link'
                activate={selectionUpdate.activateCert}
                deactivate={selectionUpdate.deactivateCert}
                currentlyActiveCerts={props.activeCertificates}
                modelidentifiers={getIdentifiersForRootLinkCertificate()}
                shouldLoadOnStartUp={props.shouldLoadOnStartUp}
                doUpdate={false}
                updateNumber={0}
            />
            <CertSelect
                type={CertType.BackendLink}
                title='certificates_type_backend_link'
                activate={selectionUpdate.activateCert}
                deactivate={selectionUpdate.deactivateCert}
                currentlyActiveCerts={props.activeCertificates}
                modelidentifiers={getIdentifiersForBackendLinkCertificate()}
                shouldLoadOnStartUp={props.shouldLoadOnStartUp}
                doUpdate={false}
                updateNumber={0}
            />
            <CertSelectEcu
                type={CertType.Ecu}
                title='certificates_type_ecu'
                activate={selectionUpdate.activateCertForRpUsecase}
                deactivate={selectionUpdate.deactivateCertForRpUsecase}
                currentlyActiveCerts={props.activeEcuCert}
                modelidentifiers={getIdentifiersForECUCertificate()}
                shouldLoadOnStartUp={props.shouldLoadOnStartUp}
                doUpdate={false}
                updateNumber={0}
            />
        </div>
    );
};

export default ReplacementPackageSelection;
