export interface Pagination {
    currentPage: number;
    lastPage: number;
    lastItem: number;
}

// Expects input in format '<firstItem>-<lastItem>/<maxItems>' (e.g. items 0-10/731)
export function mapCurrentPage(input: string, pageSize: number) {
    const contentRange = input.split('/');
    const lastItem: number = +contentRange[1];
    const firstVisibleItem = contentRange[0].substring(
        contentRange[0].indexOf(' ') + 1,
        contentRange[0].lastIndexOf('-')
    );
    return {
        currentPage: +firstVisibleItem / pageSize,
        lastPage: Math.ceil(lastItem / pageSize),
        lastItem: lastItem
    };
}
