import { CertificateData } from '../CertificateData';
import { IntegrityCheckDataHolder } from '../../state/models/IntegrityCheckDataHolder';
import React from 'react';
import ReactTooltip from 'react-tooltip';
import { useTranslation } from 'react-i18next';

interface IntegrityCheckInfoProps {
    row: CertificateData;
    integrityCheckData?: IntegrityCheckDataHolder;
}

const IntegrityCheckInfo = (props: IntegrityCheckInfoProps) => {
    const { t } = useTranslation();

    function getIntegrityErrors(row: CertificateData, integrityCheckData?: IntegrityCheckDataHolder): JSX.Element[] {
        let result: JSX.Element[] = [];
        if (integrityCheckData && integrityCheckData[row.id]) {
            integrityCheckData[row.id].forEach((element: string) => {
                result.push(
                    <>
                        {t(element)}
                        <br />
                    </>
                );
            });
        }
        return result;
    }

    return (
        <div>
            <span className='certificate-integrity-error' data-tip data-for={props.row.id}>
                !
            </span>
            <ReactTooltip id={props.row.id} type='error'>
                {getIntegrityErrors(props.row, props.integrityCheckData)}
            </ReactTooltip>
        </div>
    );
};
export default IntegrityCheckInfo;
