import { Button, Link } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { Trans, useTranslation } from 'react-i18next';

import { useAPI } from '../common/API';
import { useGeneralConfigService } from '../configurations/store/GeneralConfigStore';

const Error401Component = () => {
    const [accessTokenUrl, setAccessTokenUrl] = useState('');
    const [proxyType, setProxyType] = useState('');
    const configService = useGeneralConfigService();
    const API = useAPI();
    const { t } = useTranslation();

    // Fetch proxy type
    useEffect(() => {
        configService.loadProxyType({
            success: response => {
                const type = response.data.message;
                setProxyType(type);
            }
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    // Fetch access token URI
    useEffect(() => {
        API.getMultipleBackground(['/system/version', '/system/installationDetails'], (acct: any, perms: any) => {
            // Current environment
            const currentEnv = acct.data.system;
            var tokenUriParam: string;
            if (currentEnv === 'PROD') {
                tokenUriParam = 'spring.security.oauth2.client.provider.PROD.token-uri';
            } else {
                tokenUriParam = 'spring.security.oauth2.client.provider.TEST.token-uri';
            }

            // Access token URL based on environment
            for (var i = 0; i < perms.data.length; i++) {
                if (perms.data[i].property === tokenUriParam) {
                    setAccessTokenUrl(perms.data[i]['value']);
                    return;
                }
            }
        });
    }, [API]);

    return (
        <>
            <h3 id='error_401_title' className='red bold'>
                {t('error_401_title')}
            </h3>
            <div id='error_401_message' className='error-page-message'>
                {t('error_401_message_1')}
                <ul>
                    <li style={{ marginBottom: '20px' }}>{t('error_401_message_2', { proxy: proxyType })}</li>
                    <li>
                        <Trans i18nKey='error_401_message_3' values={{ url: accessTokenUrl }}>
                            <>
                                before link
                                <Link className='blue' href={accessTokenUrl} />
                                after link
                            </>
                        </Trans>
                    </li>
                </ul>
            </div>
            <Button id='error_401_homepage_btn' href='/#/zenzefi/ui/certificates' color='primary' variant='contained'>
                <>{t('error_btn_link_to_homepage')}</>
            </Button>
        </>
    );
};

export default Error401Component;
