/**
 * Prepends a '0' if the passed number has only one digit.
 * @param number
 */
function pad(number: number) {
    return number < 10 ? '0' + number : number;
}

/**
 * Formats the passed timestamp in the format 'yyyy-DD-mm hh:mm:ss'.
 * @param timestamp
 */
export function formatTimestamp(timestamp: number | string, offset: boolean): string {
    const date = new Date();
    if (isNotValidDate(timestamp)) {
        return "";
    }

    if(typeof timestamp === "string" && isNumber(timestamp)) {
        timestamp = parseFloat(timestamp);
    }
    const dateFromTimestamp = new Date(timestamp);

    if (offset) {
        // Date is initialized in UTC and has to be converted to local system time
        const dateOffset = date.getTimezoneOffset();
        date.setTime(dateFromTimestamp.getTime() - dateOffset * 60 * 1000);
    } else {
        date.setTime(dateFromTimestamp.getTime());
    }

    const formattedDate = date.getFullYear() + '-' + pad(date.getMonth() + 1) + '-' + pad(date.getDate());
    const formattedTime = pad(date.getHours()) + ':' + pad(date.getMinutes()) + ':' + pad(date.getSeconds());
    return formattedDate + ' ' + formattedTime;
}

function isNotValidDate(input: any) {
    return (input == null || input === "" || input === 0 || input === undefined);
}

function isNumber(value: string) {
    return /^-?[\d.]+(?:e-?\d+)?$/.test(value);
}