import React, { useState } from 'react';

import ContextMenu from '../common/table/ContextMenu';
import DeleteCertificateContextMenu from './delete/DeleteCertificateContextMenu';
import ExportCertificateContextMenu from './export/ExportCertificateContextMenu';
import RefreshCertificatesContextMenu from './refresh/RefreshCertificatesContextMenu';

interface ContextMenuProps {
    selection: any;
    table: any;
    posX: number;
    posY: number;
    closeContextMenu: () => void;
}

const CertificatesContextMenu = (props: ContextMenuProps) => {
    // Can be used by childs to block closing of menu on click away
    const [isClickAwayBlocked, setClickAwayBlocked] = useState<boolean>(false);

    const items = [
        <RefreshCertificatesContextMenu closeMenu={closeMenuAndRefresh} />,
        <ExportCertificateContextMenu
            closeMenu={closeMenuAndRefresh}
            selection={props.selection}
            blockClickAway={blockClickAway}
        />,
        <DeleteCertificateContextMenu
            closeMenu={closeMenuAndRefresh}
            selection={props.selection}
            blockClickAway={blockClickAway}
        />
    ];

    function blockClickAway() {
        setClickAwayBlocked(true);
    }

    function closeMenuAndRefresh(refresh: boolean) {
        props.closeContextMenu();
        setClickAwayBlocked(false);
        if (refresh && props.table) {
            props.table.onQueryChange();
        }
    }

    return <ContextMenu items={items} posX={props.posX} posY={props.posY} clickAwayBlocked={isClickAwayBlocked} />;
};

export default CertificatesContextMenu;
