import { Button, Tooltip } from '@material-ui/core';
import React, { useEffect, useState } from 'react';

import { ConfigData } from '../../state/models/ConfigData';
import RefreshIcon from '@material-ui/icons/Refresh';
import SessionTimeSingleton from '../../state/SessionTimeSingleton';
import { USER_CONFIG } from '../../configurations/store/ConfigKeys';
import { useAccountService } from '../AccountService';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

const SessionTimer = () => {
    const [formattedTime, setFormattedTime] = useState<string>();
    const [hovering, setHovering] = useState<boolean>(false);
    const [{ config, user }] = useStateValue();
    const { t } = useTranslation();
    const accountService = useAccountService();

    var time: number = 0;
    const autologoutConfig: ConfigData = config[USER_CONFIG.AUTOLOGOUT];
    let sessionInstance = SessionTimeSingleton.getInstance();
    useEffect(() => {
        // Component is not rerendered based on this hook so value isn't lost and warning can be ignored
        // eslint-disable-next-line react-hooks/exhaustive-deps
        time = sessionInstance.remainingTime;
        const interval = setInterval(() => {
            time -= 1;
            if (time > 0) {
                setFormattedTime(formatRemainingTime());
            }
        }, 1000);
        return () => clearInterval(interval);
    }, [sessionInstance.resetCounter, autologoutConfig && autologoutConfig.configValue]);

    function formatRemainingTime(): string {
        var minutes = Math.floor(time / 60);
        var seconds = Math.floor(time % 60);
        return makeTwoDigits(minutes) + ':' + makeTwoDigits(seconds);
    }

    function makeTwoDigits(num: number) {
        return num < 10 ? '0' + num : num;
    }

    function renderSessionTimer() {
        if (autologoutConfig && user.isAuthenticated) {
            if (autologoutConfig.configValue !== 'true') {
                return (
                    <>
                        <span id='no-auto-logout-text'>{t('no_auto_logout')}</span>;
                    </>
                );
            } else {
                return (
                    <>
                        {user.remainingSessionSeconds >= 0 && (
                            <Tooltip title={t('user_session_refresh').valueOf()}>
                                <Button
                                    id='session-timer'
                                    onClick={accountService.refreshUserSession}
                                    onMouseEnter={() => setHovering(true)}
                                    onMouseLeave={() => setHovering(false)}
                                >
                                    <>{hovering ? <RefreshIcon /> : formattedTime}</>
                                </Button>
                            </Tooltip>
                        )}
                    </>
                );
            }
        } else {
            return <></>;
        }
    }

    return renderSessionTimer();
};

export default SessionTimer;
