/* eslint-disable eqeqeq */

import { Button, Dialog, DialogActions, DialogContent, DialogTitle, LinearProgress } from '@material-ui/core';

import React from 'react';
import ReactHtmlParser from 'react-html-parser';
import { UpdateType } from './UpdateCertificates';
import { getTranslationForStep } from './UpdateSteps';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

interface UpdateProgressDialogProps {
    closeDialog: (cancel: boolean) => void;
    refreshCallBack: () => void;
}

const UpdateProgressDialog = (props: UpdateProgressDialogProps) => {
    const { t } = useTranslation();
    const [{ certsUpdateProcess }] = useStateValue();

    function stopUpdate(): void {
        props.closeDialog(true);
        props.refreshCallBack();
    }

    function close(): void {
        props.closeDialog(false);
    }

    function composeAllRetriesFailed() {
        return (
            <div>
                {t('certificates_update_progress_dialog_all_retries_failed', {
                    endpoint: certsUpdateProcess.updateCertificatesRetryInfo.endpoint
                })}
            </div>
        );
    }

    /**
     * Composes the progress overview containing the current update step and detail information.
     */
    function composeCurrentProgress() {
        if (certsUpdateProcess.errorMetrics === true) {
            return composeErrorDetailsStopUpdate();
        }
        return (
            <>
                <div className='padding-small-vertical'>
                    <span className='bold'>{t('certificates_update_progress_dialog_step')}</span>
                    {t(getTranslationForStep(certsUpdateProcess.detailsStep))}
                </div>
                <div className='padding-small-vertical'>
                    <span className='bold'>{t('certificates_update_progress_dialog_details')}</span>
                    {t(getTranslationForStep(certsUpdateProcess.details))}
                </div>
                {composeRetrySection()}
                <LinearProgress style={{ marginTop: '50px' }} />
            </>
        );
    }

    function composeErrorDetailsStopUpdate() {
        return (
            <>
                <div
                    className={
                        certsUpdateProcess.errorMetrics ? 'padding-small-vertical red' : 'padding-small-vertical'
                    }
                >
                    <span className='bold'>{ReactHtmlParser(certsUpdateProcess.details)}</span>
                </div>
            </>
        );
    }

    /**
     * Composes the retry summary section if the update process is currently retrying requests.
     */
    function composeRetrySection() {
        if (
            certsUpdateProcess.updateCertificatesRetryInfo &&
            certsUpdateProcess.updateCertificatesRetryInfo.currentRetry !== 0
        ) {
            return (
                <>
                    <div
                        className={
                            certsUpdateProcess.errorMetrics ? 'padding-small-vertical red' : 'padding-small-vertical'
                        }
                    >
                        <span className='bold'>{t('certificates_update_progress_dialog_current_retry')}</span>
                        {t('certificates_update_progress_dialog_retry_info', {
                            currentRetry: certsUpdateProcess.updateCertificatesRetryInfo.currentRetry,
                            maxRetries: certsUpdateProcess.updateCertificatesRetryInfo.maxRetries,
                            endpoint: certsUpdateProcess.updateCertificatesRetryInfo.endpoint
                        })}
                    </div>
                    <div className='padding-small-vertical'>
                        <span className='bold'>{t('certificates_update_progress_dialog_retry_next_retry')}</span>
                        {formatTimestamp(certsUpdateProcess.updateCertificatesRetryInfo.nextRetryTimestamp)}
                    </div>
                </>
            );
        }
    }

    function formatTimestamp(timestamp: number): string {
        var date: Date;
        if (timestamp > 0) {
            date = new Date(timestamp);
        } else {
            date = new Date();
        }
        return date.toLocaleString();
    }

    function getUpdateType(type: string): string {
        let updateType: string = type ? type.toUpperCase() : type;
        switch (updateType) {
            case UpdateType.FULL.toUpperCase():
                return t('certificates_update_progress_dialog_title_full');
            case UpdateType.DIFFERENTIAL.toUpperCase():
                return t('certificates_update_progress_dialog_title_differential');
            default:
                return '';
        }
    }

    return (
        <Dialog id='update-progress-dialog' open={true} fullWidth maxWidth={'sm'}>
            <DialogTitle id='update-progress-dialog-header' className='blue'>
                {getUpdateType(certsUpdateProcess.type)}
            </DialogTitle>
            <DialogContent id='update-progress-dialog-content' className='update-certificates-dialog-content'>
                {certsUpdateProcess.didFailAllRetries ? composeAllRetriesFailed() : composeCurrentProgress()}
            </DialogContent>
            <DialogActions>
                <Button id='update-progress-dialog-close-btn' onClick={close} color='primary' variant='outlined'>
                    {t('common_close')}
                </Button>
                {certsUpdateProcess.didFailAllRetries || certsUpdateProcess.errorMetrics === true ? (
                    ''
                ) : (
                    <Button
                        id='update-progress-dialog-cancel-btn'
                        onClick={stopUpdate}
                        color='primary'
                        variant='contained'
                    >
                        {t('certificates_update_progress_dialog_cancel')}
                    </Button>
                )}
            </DialogActions>
        </Dialog>
    );
};

export default UpdateProgressDialog;
