import { Button } from '@material-ui/core';
import React from 'react';
import { useTranslation } from 'react-i18next';

const Error401Component = () => {
    const { t } = useTranslation();

    return (
        <>
            <h3 id='error_401_title' className='red bold'>
                {t('error_401_title')}
            </h3>
            <div id='error_401_message' className='error-page-message'>
                {t('error_401_message_clock')}
            </div>
            <Button id='error_401_homepage_btn' href='/#/zenzefi/ui/certificates' color='primary' variant='contained'>
                <>{t('error_btn_link_to_homepage')}</>
            </Button>
        </>
    );
};

export default Error401Component;
