import MaterialTable, { Column, Query } from '@material-table/core';
import React, { useEffect, useState } from 'react';
import { defaultTableHeaderStyle, getCommonTranslation } from '../common/table/ZenzefiTable';

import { APICallback } from '../common/API';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import LogItem from './LogItem';
import LoggingToolbar from './LoggingToolbar';
import RangeDateTimeFilter from '../common/table/filter/RangeDateTimeFilter';
import { formatTimestamp } from '../common/DateFormat';
import { mapCurrentPage } from '../common/PageRange';
import { useLoggingService } from './LoggingService';
import { useStateValue } from '../state/State';
import { useTranslation } from 'react-i18next';
import { useUserConfigService } from '../configurations/store/UserConfigStore';

export enum LoggingColumn {
    Timestamp = 'createTimestamp',
    Type = 'type',
    Valid = 'valid',
    Message = 'message'
}

const LoggingComponent = () => {
    const [pageSize, setPageSize] = useState(0);
    const [table, setTable] = useState<any>();
    const [{ lastUpdateTime }] = useStateValue();
    const { t } = useTranslation();

    const userConfigService = useUserConfigService();
    const loggingService = useLoggingService();

    // eslint-disable-next-line react-hooks/exhaustive-deps
    useEffect(() => loadTableSettings(), [lastUpdateTime]);

    const tableColumns: Column<any>[] = [
        {
            title: t('logging_table_time'),
            field: LoggingColumn.Timestamp,
            width: '150px',
            render: (row: LogItem) => formatTimestamp(row.createTimestamp, false),
            type: 'datetime',
            filterComponent: RangeDateTimeFilter
        },
        {
            title: t('logging_table_type'),
            field: LoggingColumn.Type,
            width: '100px',
            lookup: {
                INFO: 'INFO',
                WARNING: 'WARNING',
                SEVERE: 'SEVERE',
                CONFIG: 'CONFIG',
                FINE: 'FINE',
                FINER: 'FINER'
            },
            render: row => renderTypeCell(row)
        },
        {
            title: t('logging_table_valid'),
            field: LoggingColumn.Valid,
            width: '80px',
            hidden: true,
            render: rowData => renderValidityIcon(rowData),
            lookup: {
                true: t('logging_filter_integrity_true'),
                false: t('logging_filter_integrity_false')
            },
            type: 'boolean'
        },
        { title: t('logging_table_message'), field: LoggingColumn.Message }
    ];

    const loadTableSettings = () => {
        userConfigService.getPageSize({ success: response => setPageSize(response.data) });
    };

    /**
     * Renders a log type cell based on the log level.
     *
     * @param row the row
     */
    const renderTypeCell = (row: LogItem): JSX.Element => {
        var className: string = '';
        switch (row.type) {
            case 'SEVERE':
                className = 'red';
                break;
            case 'WARNING':
                className = 'orange';
                break;
            case 'INFO':
                className = 'black';
                break;
            default:
                className = 'blue';
        }
        return <span className={className}>{row.type}</span>;
    };

    const renderValidityIcon = (rowData: LogItem): JSX.Element => {
        if (rowData.valid) {
            return <CheckIcon className='green' />;
        } else {
            return <CloseIcon className='red' />;
        }
    };

    const refreshBtnClicked = () => {
        table.onQueryChange();
    };

    const refreshLoggingData = (query: Query<LogItem>, resolve: Function) => {
        const callback: APICallback = {
            success: response => {
                const page = mapCurrentPage(response.headers['content-range'], pageSize);
                resolve({
                    data: response.data,
                    page: page.currentPage,
                    totalCount: page.lastItem
                });
            }
        };
        loggingService.queryLogs(query, callback);
    };

    return (
        <div id='logging-table'>
            {pageSize !== 0 && (
                <MaterialTable
                    title={<LoggingToolbar refreshBtnClicked={refreshBtnClicked} />}
                    columns={tableColumns}
                    tableRef={setTable}
                    options={{
                        filtering: true,
                        header: true,
                        search: false,
                        sorting: true,
                        columnsButton: true,
                        toolbar: true,
                        draggable: false,
                        maxBodyHeight: '68vh',
                        overflowY: 'inherit',
                        pageSize: pageSize,
                        pageSizeOptions: [10, 25, 50, 100],
                        headerStyle: defaultTableHeaderStyle,
                        debounceInterval: 600
                    }}
                    localization={getCommonTranslation(t)}
                    onChangeRowsPerPage={selection => {
                        //setPageSize(selection); // setting the state does not seem to be needed
                        userConfigService.setPageSize(selection);
                    }}
                    data={query =>
                        new Promise((resolve, _reject) => {
                            refreshLoggingData(query, resolve);
                        })
                    }
                />
            )}
        </div>
    );
};

export default LoggingComponent;
