import { DUIThemeColors, colorsGrey, colorsPetrol } from '../DUIThemeColors';

import { Overrides } from '@material-ui/core/styles/overrides';

export const DUIMuiSwitch: Overrides['MuiSwitch'] = {
    track: {
        backgroundColor: colorsGrey[500]
    },
    colorPrimary: {
        '&$checked': {
            color: DUIThemeColors.highlight.main,
            '& + $track': {
                backgroundColor: colorsGrey[500]
            }
        },
        '&$disabled': {
            '& $thumb': {
                color: colorsPetrol[500]
            }
        }
    },
    colorSecondary: {
        '&$checked': {
            color: DUIThemeColors.highlight.main,
            '& + $track': {
                // Manually overwritten the background color in active mode so bar is not hidden
                backgroundColor: colorsGrey[500]
            }
        },
        '&$disabled': {
            '& $thumb': {
                color: colorsPetrol[500],
                opacity: 0.3
            }
        }
    }
};
