import backendCertIcon from '../../assets/certificates/backend_cert.png';
import backendCertLinkIcon from '../../assets/certificates/backend_link_cert.png';
import csrIcon from '../../assets/certificates/csr.png';
import diagnoseCertIcon from '../../assets/certificates/diagnose_cert.png';
import ecuCertIcon from '../../assets/certificates/ecu_cert.png';
import ecuCertRedIcon from '../../assets/certificates/ecu_cert_red.png';
import ecuFolderIcon from '../../assets/certificates/ecu_folder.png';
import enhancedRightsIcon from '../../assets/certificates/enhanced_rights_cert.png';
import expiredIcon from '../../assets/certificates/expired.png';
import rootCertIcon from '../../assets/certificates/root_cert.png';
import rootCertLinkIcon from '../../assets/certificates/root_link_cert.png';
import secOcisCertIcon from '../../assets/certificates/sec_ocis_cert.png';
import timeCertIcon from '../../assets/certificates/time_cert.png';
import validityGreenIcon from '../../assets/certificates/validity_green.png';
import validityRedIcon from '../../assets/certificates/validity_red.png';
import validityYellowIcon from '../../assets/certificates/validity_yellow.png';
import variantCodingIcon from '../../assets/certificates/variant_coding_cert.png';

export enum CertificateType {
    ROOT = 'ROOT_CA_CERTIFICATE',
    ROOT_LINK = 'ROOT_CA_LINK_CERTIFICATE',
    BACKEND = 'BACKEND_CA_CERTIFICATE',
    BACKEND_LINK = 'BACKEND_CA_LINK_CERTIFICATE',
    DIAGNOSTIC = 'DIAGNOSTIC_AUTHENTICATION_CERTIFICATE',
    TIME = 'TIME_CERTIFICATE',
    VIRTUAL_FOLDER = 'VIRTUAL_FOLDER',
    VARIANT_CODING = 'VARIANT_CODE_USER_CERTIFICATE',
    ENHANCED_RIGHTS = 'ENHANCED_RIGHTS_CERTIFICATE',
    ECU = 'ECU_CERTIFICATE',
    CSR = 'CSR'
}

export class CertificateData {
    id: string;
    subject: string;
    type: CertificateType;
    serialNo: string;
    validFrom: number;
    validTo: number;
    validityStrengthColor: string;
    userRole: string;
    pkirole: string;
    secOCISCert: boolean = false;
    forVsmSimulation: boolean = false;
    subjectKeyIdentifier: string;
    authorityKeyIdentifier: string;
    childrenIds?: string[];
    keyUsageText?: string;
    targetVIN?: string;
    subjectPublicKey?: string;
    targetECU?: string;
    signature?: string;
    version?: string;
    uniqueECUID?: string;
    specialECU?: string;
    baseCertificateID?: string;
    nonce?: string;
    issuer?: string;
    basicConstraintsText?: string;
    services?: string;
    issuerSerialNumber?: string;
    prodQualifier?: string;
    algorithmIdentifier?: string;
    status?: string;
    targetSubjectKeyIdentifier?: string;
    certsWithPrivateKey?: number;
    certsWithoutPrivateKey?: number;
    totalCerts?: number;
    activeForTesting?: boolean;
    zkNo: string;
    ecuPackageTs: string;
    linkCertTs: string;
    pkiKnown: boolean;
    state: string;
    pkiState: string;

    /**
     * Initializes the object. Validates the input and throws an error if invalid input.
     *
     * @param input object that should be used to initialize the CertificateData object
     */
    constructor(input: any) {
        if (!this.isValid(input)) {
            throw new Error('Invalid Certificate data passed to constructor: ' + JSON.stringify(input));
        }
        this.id = input.id;
        this.subject = input.subject;
        this.type = input.type;
        this.serialNo = input.serialNo;
        this.validFrom = input.validFrom;
        this.validTo = input.validTo;
        this.validityStrengthColor = input.validityStrengthColor;
        this.userRole = input.userRole;
        this.pkirole = input.pkirole;
        this.secOCISCert = input.secOCISCert;
        this.forVsmSimulation = input.forVsmSimulation;
        this.subjectKeyIdentifier = input.subjectKeyIdentifier;
        this.authorityKeyIdentifier = input.authorityKeyIdentifier;
        this.zkNo = input.zkNo;
        this.ecuPackageTs = input.ecuPackageTs;
        this.pkiKnown = input.pkiKnown;
        this.linkCertTs = input.linkCertTs;
        this.state = input.state;
        this.pkiState = input.pkiState;
    }

    /**
     * Checks whether the passed object is a valid CertificateData object.
     * All values that are not initialized by default have to be validated.
     *
     * @param input any object that should be validated
     */
    isValid(input: any): input is CertificateData {
        return (
            input.type === CertificateType.VIRTUAL_FOLDER ||
            (input.subject !== undefined &&
                input.type !== undefined &&
                input.validFrom !== undefined &&
                input.validTo !== undefined &&
                input.validityStrengthColor !== undefined)
        );
    }

    /**
     * Returns the icon for the certificate's type or "invalid type" if no
     * match has been found.
     */
    getIconForType(): string {
        if (this.state === 'SIGNING_REQUEST') {
            return csrIcon;
        }
        switch (this.type) {
            case CertificateType.ROOT:
                return rootCertIcon;
            case CertificateType.ROOT_LINK:
                return rootCertLinkIcon;
            case CertificateType.BACKEND:
                return backendCertIcon;
            case CertificateType.BACKEND_LINK:
                return backendCertLinkIcon;
            case CertificateType.DIAGNOSTIC:
                return diagnoseCertIcon;
            case CertificateType.TIME:
                return timeCertIcon;
            case CertificateType.VIRTUAL_FOLDER:
                return ecuFolderIcon;
            case CertificateType.VARIANT_CODING:
                return variantCodingIcon;
            case CertificateType.ENHANCED_RIGHTS:
                if (this.secOCISCert) {
                    return secOcisCertIcon;
                } else {
                    return enhancedRightsIcon;
                }
            case CertificateType.ECU:
                if (this.forVsmSimulation) {
                    return ecuCertRedIcon;
                } else {
                    return ecuCertIcon;
                }
            default:
                return 'invalid type';
        }
    }

    /**
     * Returns the green, yellow or red validity icon based on the value of
     * validityStrengthColor. If no match has been found an empty string is returned.
     */
    getValidityIcon(): string {
        switch (this.validityStrengthColor) {
            case 'green':
                return validityGreenIcon;
            case 'yellow':
                return validityYellowIcon;
            case 'red':
                return validityRedIcon;
            case 'warn':
                return expiredIcon;
            default:
                return '';
        }
    }
}
