import React, { useEffect, useState } from 'react';

import FormControlLabel from '@material-ui/core/FormControlLabel';
import { Paper } from '@material-ui/core';
import { ProxyType } from './ProxyData';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import ValidatedTextfield from '../../common/ValidatedTextfield';
import { useGeneralConfigService } from '../store/GeneralConfigStore';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

const ProxyComponent = (props: { initialHostValueState: false }) => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [{ lastUpdateTime, isXentry }] = useStateValue();
    const configService = useGeneralConfigService();
    const { t } = useTranslation();

    // State
    const [proxyType, setProxyType] = useState(ProxyType.SYSTEM);
    const [proxyHost, setProxyHost] = useState('');
    const [proxyPort, setProxyPort] = useState('');

    const [hostName, setHostName] = useState('');

    const [stateForHost, setStateForHost] = useState<any>(props.initialHostValueState);
    const [stateForPort, setStateForPort] = useState<any>(props.initialHostValueState);

    const [lastBlurEvent, setLastBlurEvent] = useState<number>();

    const protocolStyle = { color: proxyType === ProxyType.MANUAL ? '#414141' : '#9E9E9E' };

    // Update proxy when lastUpdateTime value changes
    useEffect(() => {
        if (!isXentry) {
            refreshProxyType();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [lastUpdateTime]);

    useEffect(() => {
        if (stateForPort && stateForHost) {
            let proxyTargetName: string[] = hostName.split(':');
            setProxyHost(proxyTargetName[0]);
            setProxyPort(proxyTargetName[1]);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [hostName]);

    function refreshProxyType() {
        configService.loadProxyType({
            success: response => {
                const type = response.data.message;
                setProxyType(type);
                if (ProxyType.MANUAL === type) {
                    refreshProxy();
                }
            }
        });
    }

    function refreshProxy() {
        configService.loadProxy({
            success: response => {
                setProxyHost(response.data.hostname);
                setProxyPort(response.data.port);
                setHostName(response.data.hostname + ':' + response.data.port);
            }
        });
    }

    function onProxyChange(isValid: boolean, value: string) {
        setHostName(value);
    }

    function proxyTypeChanged(selection: any) {
        const type = selection.currentTarget.value;
        setProxyType(type);
        configService.updateProxyType(type);
    }

    function updateProxyConfig(event: React.FocusEvent<HTMLInputElement>) {
        if (!lastBlurEvent || event.timeStamp - lastBlurEvent > 1000) {
            setLastBlurEvent(event.timeStamp);
            if (stateForPort && stateForHost) {
                configService.updateProxy(proxyHost, proxyPort);
            }
        }
    }

    function validateInput(input: string): boolean {
        let isMatching: boolean = new RegExp('(.*):(.*)').test(input);
        let tempStateForHost: boolean = false;
        let tempStateForPort: boolean = false;
        if (isMatching) {
            let proxyTargetName: string[] = input.split(':');
            tempStateForHost = validateHost(proxyTargetName[0]);
            tempStateForPort = validatePort(proxyTargetName[1]);
        } else {
            tempStateForHost = false;
            tempStateForPort = false;
        }
        setStateForHost(tempStateForHost);
        setStateForPort(tempStateForPort);
        return tempStateForHost && tempStateForPort;
    }

    function validateHost(input: string): boolean {
        return new RegExp(
            '^(([a-zA-Z0-9]|[a-zA-Z0-9][a-zA-Z0-9-]*[a-zA-Z0-9]).)*([A-Za-z0-9]|[A-Za-z0-9][A-Za-z0-9-]*[A-Za-z0-9])$'
        ).test(input);
    }

    function validatePort(input: string): boolean {
        var result: boolean = false;
        try {
            const port: number = +input;
            result = port > 0 && port < 65536;
        } catch (e) {}

        return result;
    }

    return (
        <div className='config-proxy-container'>
            <div className='config-heading'>{t('configurations_proxy_title')}</div>
            <Paper className='config-proxy-paper'>
                {isXentry && <span className='config-disable-desc'>{t('configurations_proxy_disabled_desc')}</span>}
                <RadioGroup
                    name='proxy-type'
                    className='config-proxy-type-group'
                    value={proxyType}
                    onChange={proxyTypeChanged}
                >
                    <FormControlLabel
                        id='config-proxy-radio-manual'
                        value={ProxyType.MANUAL}
                        control={<Radio />}
                        label={t('configurations_proxy_type_manual')}
                        disabled={isXentry}
                    />
                    <FormControlLabel
                        id='config-proxy-radio-system'
                        value={ProxyType.SYSTEM}
                        control={<Radio />}
                        label={t('configurations_proxy_type_system')}
                        disabled={isXentry}
                    />
                    <FormControlLabel
                        id='config-proxy-radio-none'
                        value={ProxyType.NONE}
                        control={<Radio />}
                        label={t('configurations_proxy_type_none')}
                        disabled={isXentry}
                    />
                </RadioGroup>
                <div className='config-proxy-input-container'>
                    <span className='config-proxy-protocol' style={protocolStyle}>
                        https://
                    </span>
                    <ValidatedTextfield
                        id='config-txt-proxy-host'
                        label={t('configurations_proxy_host_label') + ':' + t('configurations_proxy_port_label')}
                        className='config-proxy-host-txt'
                        errorMessage={t('configurations_proxy_and_host_error')}
                        type='text'
                        validateInput={input => validateInput(input)}
                        value={hostName}
                        disabled={proxyType !== ProxyType.MANUAL}
                        onChange={onProxyChange}
                        onBlur={updateProxyConfig}
                    />
                </div>
            </Paper>
        </div>
    );
};

export default ProxyComponent;
