import React, { FormEvent, useState } from 'react';

import { APICallback } from '../common/API';
import Button from '@material-ui/core/Button';
import { CallbackProps } from '../common/CallbackProps';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import ForgotPassword from './password/ForgotPassword';
import { StateActionType } from '../state/StateAction';
import ValidatedTextfield from '../common/ValidatedTextfield';
import { getUserData } from '../state/models/UserData';
import { useAccountService } from './AccountService';
import { useStateValue } from '../state/State';
import { useTranslation } from 'react-i18next';

const OfflineLoginDialog = (props: CallbackProps) => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [, dispatch] = useStateValue();
    const { t } = useTranslation();
    const service = useAccountService();

    const [userId, setUserId] = useState('');
    const [isUserIdValid, setUserIdValid] = useState(false);
    const [password, setPassword] = useState('');
    const [isPasswordValid, setPasswordValid] = useState(false);
    const [open, setOpen] = useState(true);
    const [errorMessage, setErrorMessage] = useState('');

    const callback: APICallback = {
        success: response => {
            const userData = getUserData(
                response.data.userName,
                response.data.displayName,
                false,
                false,
                false,
                response.data.authenticated,
                response.data.authenticatedAgainstBackend,
                response.data.remainingSessionSeconds
            );
            dispatch({
                type: StateActionType.UPDATE_USER,
                data: userData
            });
            dispatch({ type: StateActionType.TRIGGER_UPDATE, data: new Date() });
            close();
        },
        failure: error => {
            if (error.response !== undefined) {
                setErrorMessage(error.response.data.message);
            }
        }
    };

    function submit(event: FormEvent) {
        event.preventDefault();
        service.login(userId, password, callback);
    }

    function close() {
        setOpen(false);
        props.callback();
    }

    function onUserIdChange(isValid: boolean, input: string) {
        setUserId(input);
        setUserIdValid(isValid);
    }

    function onPwChange(isValid: boolean, input: string) {
        setPassword(input);
        setPasswordValid(isValid);
    }

    function isInputNotEmpty(input: string): boolean {
        return !(!input || /^\s*$/.test(input));
    }

    return (
        <Dialog id='dialog-login-offline' open={open} onClose={close} disableBackdropClick disableEscapeKeyDown>
            <form onSubmit={submit}>
                <DialogTitle id='dialog-offline-login-title' className='login-dialog-title blue'>
                    {t('login_offline_dialog_title')}
                </DialogTitle>
                <DialogContent className='login-offline-dialog-content'>
                    <ValidatedTextfield
                        id='dialog-login-offline-txt-userid'
                        label={t('login_offline_dialog_label_name')}
                        className='login-textfield'
                        errorMessage={t('login_offline_dialog_error_name')}
                        type='text'
                        validateInput={isInputNotEmpty}
                        onChange={onUserIdChange}
                    />
                    <ValidatedTextfield
                        id='dialog-login-offline-txt-pw'
                        label={t('login_offline_dialog_label_pw')}
                        className='login-textfield'
                        errorMessage={t('login_offline_dialog_error_pw')}
                        type='password'
                        validateInput={isInputNotEmpty}
                        onChange={onPwChange}
                    />
                    <ForgotPassword userName={userId} successcallback={close} />
                    <div id='dialog-offline-login-error-message' className='login-dialog-error-message'>
                        {errorMessage}
                    </div>
                </DialogContent>
                <DialogActions>
                    <Button id='dialog-login-offline-btn-cancel' onClick={close} color='primary' variant='outlined'>
                        {t('common_cancel')}
                    </Button>
                    <Button
                        id='dialog-login-offline-btn-submit'
                        color='primary'
                        variant='contained'
                        type='submit'
                        onClick={submit}
                        disabled={!isUserIdValid || !isPasswordValid}
                    >
                        {t('login_dialog_btn_submit')}
                    </Button>
                </DialogActions>
            </form>
        </Dialog>
    );
};

export default OfflineLoginDialog;
