import { Button, MenuItem, Popover, Tooltip } from '@material-ui/core';
import React, { useState } from 'react';

import CheckIcon from '@material-ui/icons/Check';
import IntegrityCheckResultDialog from './IntegrityCheckResultDialog';
import { StateActionType } from '../../state/StateAction';
import { useCertificatesService } from '../CertificatesService';
import { useIntegrityLogExistence } from './IntegrityCheckHook';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

const INTEGRITY_MENU_ID = 'certificates-integrity-menu';

const IntegrityCheck = () => {
    const [{ certsUpdateProcess, isIntegrityCheckExisting }, dispatch] = useStateValue();
    const { t } = useTranslation();
    const certificatesService = useCertificatesService();
    useIntegrityLogExistence();

    // Menu handling
    const [integrityMenuOpen, setIntegrityMenuOpen] = useState<boolean>(false);
    const [integrityMenuAnchor, setIntegrityMenuAnchor] = useState<any>(undefined);

    // Integrity check results
    const [isResultDialogOpen, setResultDialogOpen] = useState<boolean>(false);
    const [totalNumberCertificates, setTotalNumberCertificates] = useState<number>(0);
    const [numberFailedCertificates, setNumberFailedCertificates] = useState<number>(0);

    function toggleMenu(event: any) {
        setIntegrityMenuAnchor(event.target);
        setIntegrityMenuOpen(!integrityMenuOpen);
    }

    function closeResultDialog() {
        setResultDialogOpen(false);
    }

    /**
     * Checks the system integrity and toggles the menu afterwards.
     *
     * @param event the click event
     */
    function startSystemIntegrityCheck(event: any) {
        certificatesService.checkSystemIntegrity({
            success: response => {
                setTotalNumberCertificates(response.data.totalNumberOfCheckedCertificates);
                setNumberFailedCertificates(response.data.totalNumberOfFailedChecks);
                setResultDialogOpen(true);
                dispatch({
                    type: StateActionType.UPDATE_INTEGRITY_CHECK_EXISTENCE,
                    data: true
                });
                dispatch({
                    type: StateActionType.UPDATE_INTEGRITY_CHECK_DATA,
                    data: {
                        integritycheckerrormap: response.data.integrityCheckErrorMap
                    }
                });
            },
            finally: () => {
                toggleMenu(event);
            }
        });
    }

    function downloadReport(event: any) {
        certificatesService.downloadSystemIntegrityLog();
        toggleMenu(event);
    }

    return (
        <>
            {isResultDialogOpen && (
                <IntegrityCheckResultDialog
                    totalNumberCerts={totalNumberCertificates}
                    failedChecks={numberFailedCertificates}
                    closeDialog={closeResultDialog}
                />
            )}
            <Tooltip title={certsUpdateProcess.running ? t('certificates_update_blocks_action').valueOf() : ''}>
                <span>
                    <Button
                        id='certificates-integrity-btn'
                        color='primary'
                        variant='outlined'
                        aria-owns={integrityMenuAnchor ? INTEGRITY_MENU_ID : undefined}
                        aria-haspopup='true'
                        disableFocusRipple={false}
                        className='certificates-toolbar-button'
                        disabled={certsUpdateProcess.running}
                        onClick={toggleMenu}
                    >
                        <CheckIcon className='btn-icon-left' />
                        {t('certificates_integrity_btn')}
                    </Button>
                </span>
            </Tooltip>
            <Popover
                id={INTEGRITY_MENU_ID}
                anchorEl={integrityMenuAnchor}
                open={integrityMenuOpen}
                onClose={toggleMenu}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center'
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center'
                }}
            >
                <MenuItem id='certificates-check-integrity-btn' onClick={startSystemIntegrityCheck}>
                    {t('certificates_check_integrity_btn')}
                </MenuItem>
                <MenuItem
                    id='certificates-download-integrity-report-btn'
                    onClick={downloadReport}
                    disabled={!isIntegrityCheckExisting}
                >
                    {t('certificates_download_integrity_report_btn')}
                </MenuItem>
            </Popover>
        </>
    );
};

export default IntegrityCheck;
