/* eslint-disable eqeqeq */

import { Button, Tooltip } from '@material-ui/core';
import React, { useEffect, useState } from 'react';

import DeleteCertificateDialog from './DeleteCertificateDialog';
import DeleteIcon from '@material-ui/icons/Delete';
import { StateActionType } from '../../state/StateAction';
import { useCertificatesService } from '../CertificatesService';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

interface DeleteCertificateProps {
    selection: any;
    refreshCallBack: () => void;
    deleteKeyPressed: boolean;
    deleteDataByKey: (flag: boolean) => void;
}

const DeleteCertificate = (props: DeleteCertificateProps) => {
    const [isDialogVisible, setDialogVisible] = useState<boolean>(false);
    const [{ certsUpdateProcess }, dispatch] = useStateValue();
    const { t } = useTranslation();
    const certificatesService = useCertificatesService();

    useEffect(() => {
        setDialogVisible(props.deleteKeyPressed);
    }, [props.deleteKeyPressed]);

    function closeDialog(confirmed: boolean) {
        if (confirmed) {
            certificatesService.delete(props.selection, {
                success: response => {
                    props.refreshCallBack();
                    setDialogVisible(false);
                    props.deleteDataByKey(false);
                    clearIntegrityCheck();
                }
            });
        } else {
            setDialogVisible(false);
            props.deleteDataByKey(false);
        }
    }

    function clearIntegrityCheck() {
        // Importing clear integrity check results
        dispatch({
            type: StateActionType.UPDATE_INTEGRITY_CHECK_EXISTENCE,
            data: false
        });
        // Importing clearing integrity check data.
        dispatch({
            type: StateActionType.UPDATE_INTEGRITY_CHECK_DATA,
            data: {
                integrityCheckErrorMap: []
            }
        });
    }

    return (
        <>
            {isDialogVisible && <DeleteCertificateDialog closeDialog={closeDialog} />}
            <Tooltip title={certsUpdateProcess.running ? t('certificates_update_blocks_action').valueOf() : ''}>
                <span>
                    <Button
                        id='certificates-delete-btn'
                        color='primary'
                        variant='outlined'
                        className='certificates-toolbar-button'
                        onClick={() => setDialogVisible(true)}
                        disabled={certsUpdateProcess.running || props.selection == undefined} // ignore warning for null check
                    >
                        <DeleteIcon className='btn-icon-left' />
                        {t('certificates_delete_btn')}
                    </Button>
                </span>
            </Tooltip>
        </>
    );
};

export default DeleteCertificate;
