// Singleton for holding time of the registed user session
// This is an alternative in state management, which is done usualy with REACT reducers.
// It is ment to be used by SessionTimer component
export default class SessionTimeSingleton {
    private static instance: SessionTimeSingleton;
    public remainingTime: number;
    public resetCounter: number;
    private constructor() {
        this.remainingTime = 0;
        this.resetCounter = 0;
    }
    static getInstance() {
        if (!SessionTimeSingleton.instance) {
            SessionTimeSingleton.instance = new SessionTimeSingleton();
        }
        return SessionTimeSingleton.instance;
    }
    increaseCounter() {
        this.resetCounter = this.resetCounter + 1;
    }

    notifyReset(remainingSessionSeconds: number) {
        this.remainingTime = remainingSessionSeconds;
        this.increaseCounter();
    }
}