import { StateActionType } from '../../state/StateAction';
import { useCertificatesService } from '../CertificatesService';
import { useEffect } from 'react';
import { useStateValue } from '../../state/State';

export function useIntegrityLogExistence() {
    const [, dispatch] = useStateValue();
    const certificatesService = useCertificatesService();

    useEffect(() => {
        certificatesService.checkSystemIntegrityLogExistence({
            success: response => {
                dispatch({
                    type: StateActionType.UPDATE_INTEGRITY_CHECK_EXISTENCE,
                    data: response.data
                });
            }
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
}

export function useClearIntegrityCheck() {
    const [, dispatch] = useStateValue();
    // Clearing existence
    dispatch({
        type: StateActionType.UPDATE_INTEGRITY_CHECK_EXISTENCE,
        data: false
    });
    //clearing integrity check data.    
    dispatch({
        type: StateActionType.UPDATE_INTEGRITY_CHECK_DATA,
        data: {
            integrityCheckErrorMap: []
        }
    });

}
