import axios, { AxiosError, AxiosResponse } from 'axios';
import { hideProgressDialog, showProgressDialog } from './dialogs/ProgressDialog';

import { StateActionType } from '../state/StateAction';
import { getFilenameFromHeaders } from './ParameterMapper';
import i18n from 'i18next';
import { useStateValue } from '../state/State';
import { useTranslation } from 'react-i18next';

/** Response callbacks after Axios requests */
export interface APICallback {
    success?: (response: AxiosResponse) => void;
    failure?: (error: AxiosError) => void;
    finally?: () => void;
}

/* Timeout used in requests, Tomcat's default is 60 seconds */
const timeout: number = 60000;

function getCurrentLanguage(): string {
    if (i18n.language === 'en') {
        return 'en';
    } else {
        return 'de';
    }
}

/**
 * Triggers the download of the logs archive. A workaround implementation is needed as axios is based on XHR.
 *  The download can't be based on the 'content-disposition' header.
 *
 * @param path the download path
 */
function downloadFileFrom(path: string) {
    axios({
        url: path,
        method: 'GET',
        responseType: 'blob'
    }).then(response => {
        const fileName: string = getFilenameFromHeaders(response.headers);
        if (!window.navigator.msSaveOrOpenBlob) {
            // BLOB NAVIGATOR
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('Download', fileName);
            document.body.appendChild(link);
            link.click();
        } else {
            // BLOB FOR EXPLORER 11
            window.navigator.msSaveOrOpenBlob(new Blob([response.data]), fileName);
        }
    });
}

async function getBackground(path: string, showError: (error: AxiosError) => void, callback?: APICallback) {
    get(path, '', showError, callback);
}

async function get(path: string, action: string, showError: (error: AxiosError) => void, callback?: APICallback) {
    getWithHeaders(path, action, {}, showError, callback);
}

async function getNoProgress(
    path: string,
    action: string,
    showError: (error: AxiosError) => void,
    callback?: APICallback
) {
    getWithHeadersNoProgress(path, action, {}, showError, callback);
}

async function getWithHeaders(
    path: string,
    action: string,
    headers: Object,
    showError: (error: AxiosError) => void,
    callback?: APICallback
) {
    if (0 !== action.length) {
        showProgressDialog(action);
    }

    await axios
        .create({
            timeout: timeout,
            headers: {
                Locale: getCurrentLanguage(),
                ...headers
            }
        })
        .get(path)
        .then(response => {
            if (callback && callback.success) {
                callback.success(response);
            }
        })
        .catch(error => {
            showError(error);
            if (callback && callback.failure) {
                callback.failure(error);
            }
        })
        .finally(() => {
            if (callback && callback.finally) {
                callback.finally();
            }
            hideProgressDialog();
        });
}

async function getWithHeadersNoProgress(
    path: string,
    action: string,
    headers: Object,
    showError: (error: AxiosError) => void,
    callback?: APICallback
) {
    await axios
        .create({
            timeout: timeout,
            headers: {
                Locale: getCurrentLanguage(),
                ...headers
            }
        })
        .get(path)
        .then(response => {
            if (callback && callback.success) {
                callback.success(response);
            }
        })
        .catch(error => {
            showError(error);
            if (callback && callback.failure) {
                callback.failure(error);
            }
        })
        .finally(() => {
            if (callback && callback.finally) {
                callback.finally();
            }
        });
}

/**
 * Get call with special handling for logout case: don't add default header and do not show error popup on failure.
 */
async function logoutFromDaimlerBackend(path: string) {
    fetch(path, {
        headers: {
            'sec-fetch-mode': 'no-cors'
        },
        credentials: 'include',
        mode: 'no-cors'
    })
        .then(function (res) {})
        .catch(function (ex) {
            console.log('Log out failed', ex);
        });
}

/**
 * Calls multiple URLs with HTTP GET. The passed callback is called after all requests are executed.
 *
 * @param urls array of URLs to request
 * @param callback called when all requests are done
 */
async function getMultipleBackground(urls: string[], callback: (acct: any, perms: any) => void) {
    const requests: Promise<AxiosResponse<any>>[] = [];
    urls.forEach(url => requests.push(axios.get(url)));
    axios.all(requests).then(
        axios.spread(function (acct, perms) {
            callback(acct, perms);
        })
    );
}

async function postBackground(
    path: string,
    data: any,
    callback: APICallback,
    showError: (error: AxiosError) => void,
    headers?: Object
) {
    post(path, '', data, callback, showError, headers);
}

async function post(
    path: string,
    action: string,
    data: any,
    callback: APICallback,
    showError: (error: AxiosError, statusCodeHandledElsewhere?: number[]) => void,
    headers?: Object,
    statusCodeHandledElsewhere?: number[]
) {
    if (0 !== action.length) {
        showProgressDialog(action);
    }
    await axios
        .create({
            timeout: timeout,
            headers: {
                Locale: getCurrentLanguage(),
                ...headers
            }
        })
        .post(path, data)
        .then(response => {
            if (callback && callback.success) {
                callback.success(response);
            }
        })
        .catch(error => {
            showError(error, statusCodeHandledElsewhere);
            if (callback && callback.failure) {
                callback.failure(error);
            }
        })
        .finally(() => {
            if (callback && callback.finally) {
                callback.finally();
            }
            if (0 !== action.length) {
                hideProgressDialog();
            }
        });
}

async function deleteBackground(
    path: string,
    showError: (error: AxiosError, statusCodeHandledElsewhere?: number[]) => void,
    callback: APICallback,
    statusCodeHandledElsewhere?: number[]
) {
    await axios
        .create({
            timeout: timeout,
            headers: {
                Locale: getCurrentLanguage()
            }
        })
        .delete(path)
        .then(response => {
            if (callback.success) {
                callback.success(response);
            }
        })
        .catch(error => {
            showError(error, statusCodeHandledElsewhere);
            if (callback.failure) {
                callback.failure(error);
            }
        })
        .finally(() => {
            if (callback.finally) {
                callback.finally();
            }
            hideProgressDialog();
        });
}

async function putBackground(
    path: string,
    data: object,
    showError: (error: AxiosError) => void,
    callback?: APICallback
) {
    await axios
        .create({
            timeout: timeout,
            headers: {
                Locale: getCurrentLanguage()
            }
        })
        .put(path, data)
        .then(response => {
            if (callback && callback.success) {
                callback.success(response);
            }
        })
        .catch(error => {
            showError(error);
            if (callback && callback.failure) {
                callback.failure(error);
            }
        })
        .finally(() => {
            if (callback && callback.finally) {
                callback.finally();
            }
        });
}

export interface API {
    // HTTP GET
    getBackground: (path: string, callback?: APICallback) => Promise<void>;
    get: (path: string, action: string, callback?: APICallback) => Promise<void>;
    getNoProgress: (path: string, action: string, callback?: APICallback) => Promise<void>;
    getWithHeaders: (path: string, action: string, headers: Object, callback?: APICallback) => Promise<void>;
    getWithHeadersNoProgress: (path: string, action: string, headers: Object, callback?: APICallback) => Promise<void>;
    logoutFromDaimlerBackend: (path: string) => Promise<void>;

    // HTTP POST
    postBackground: (path: string, data: any, callback: APICallback, headers?: Object) => Promise<void>;
    post: (path: string, action: string, data: any, callback: APICallback, headers?: Object) => Promise<void>;
    postHandledErrorCodes: (
        path: string,
        action: string,
        data: any,
        callback: APICallback,
        statusCodeHandledElsewhere?: number[]
    ) => Promise<void>;
    // HTTP DELETE
    deleteBackground: (path: string, callback: APICallback) => Promise<void>;
    deleteHandledErrorCodes: (
        path: string,
        callback: APICallback,
        statusCodeHandledElsewhere: number[]
    ) => Promise<void>;
    // HTTP PUT
    putBackground: (path: string, data: object, callback?: APICallback) => Promise<void>;

    // Others
    downloadFileFrom: (path: string) => void;
    getMultipleBackground: (urls: string[], callback: (acct: any, perms: any) => void) => void;
}

function errorCodeToHandle(httpStatus: number, statusCodeHandledElsewhere?: number[]) {
    if (statusCodeHandledElsewhere && statusCodeHandledElsewhere.includes(httpStatus)) {
        return false;
    }
    return httpStatus > 399 && httpStatus < 407;
}

export const useAPI = (): API => {
    const { t } = useTranslation();
    const [, dispatch] = useStateValue();

    /**
     * Shows information about the passed error in the {@link MessageDialog}.
     *
     * @param error the {@link AxiosError}
     */
    const showError = (error: AxiosError, statusCodeHandledElsewhere?: number[]) => {
        var errorMessage = '';
        if (error.response) {
            if (errorCodeToHandle(error.response.status, statusCodeHandledElsewhere)) {
                // Response status code other than 2xx
                if (error.response.data.message) {
                    errorMessage = 'HTTP ' + error.response.status + ': ' + error.response.data.message;
                } else if (error.response.data) {
                    errorMessage = 'HTTP ' + error.response.status + ': ' + error.response.data;
                } else if (error.message) {
                    errorMessage = 'HTTP ' + error.response.status + ': ' + error.message;
                }
            } else {
                console.log('Non default http code received: ' + error.response.status);
                console.log(error);
            }
        } else if (error.request) {
            // No response for `error.request` (XMLHttpRequest)
            errorMessage = t('error_no_http_response', { request: JSON.stringify(error.request) });
        } else {
            // Setting up request failed
            errorMessage = error.message;
        }

        // Prevent empty error dialog poping up
        if (errorMessage !== '') {
            dispatch({
                type: StateActionType.UPDATE_MESSAGE,
                data: { content: errorMessage, isError: true }
            });
        }
    };

    return {
        // HTTP GET
        getBackground: (path: string, callback?: APICallback) => getBackground(path, showError, callback),
        get: (path: string, action: string, callback?: APICallback) => get(path, action, showError, callback),
        getNoProgress: (path: string, action: string, callback?: APICallback) =>
            getNoProgress(path, action, showError, callback),
        getWithHeaders: (path: string, action: string, headers: Object, callback?: APICallback) =>
            getWithHeaders(path, action, headers, showError, callback),
        getWithHeadersNoProgress: (path: string, action: string, headers: Object, callback?: APICallback) =>
            getWithHeadersNoProgress(path, action, headers, showError, callback),

        // HTTP POST
        postBackground: (path: string, data: any, callback: APICallback, headers?: Object) =>
            postBackground(path, data, callback, showError, headers),
        post: (path: string, action: string, data: any, callback: APICallback, headers?: Object) =>
            post(path, action, data, callback, showError, headers),
        postHandledErrorCodes: (
            path: string,
            action: string,
            data: any,
            callback: APICallback,
            statusCodeHandledElsewhere?: number[]
        ) => post(path, action, data, callback, showError, undefined, statusCodeHandledElsewhere),

        // HTTP DELETE
        deleteBackground: (path: string, callback: APICallback) => deleteBackground(path, showError, callback),
        deleteHandledErrorCodes: (path: string, callback: APICallback, statusCodeHandledElsewhere: number[]) =>
            deleteBackground(path, showError, callback, statusCodeHandledElsewhere),
        // HTTP PUT
        putBackground: (path: string, data: object, callback?: APICallback) =>
            putBackground(path, data, showError, callback),

        // Others
        downloadFileFrom: (path: string) => downloadFileFrom(path),
        getMultipleBackground: (urls: string[], callback: (acct: any, perms: any) => void) =>
            getMultipleBackground(urls, callback),
        logoutFromDaimlerBackend: (path: string) => logoutFromDaimlerBackend(path)
    };
};
