import { Button } from '@material-ui/core';
import React from 'react';
import { useTranslation } from 'react-i18next';

const Error404Component = () => {
    const { t } = useTranslation();

    return (
        <>
            <h3 id='error_404_title' className='red bold'>
                {t('error_404_title')}
            </h3>
            <div id='error_404_message' className='error-page-message'>
                {t('error_404_message')}
            </div>
            <Button id='error_404_homepage_btn' href='/#/zenzefi/ui/certificates' color='primary' variant='contained'>
                {t('error_btn_link_to_homepage')}
            </Button>
        </>
    );
};

export default Error404Component;
