import { ActionMeta, ValueType } from 'react-select/src/types';
import React, { useEffect, useState } from 'react';
import { SelectableCertificate, isValidCert } from './SelectableCertificate';

import { CertType } from './CertType';
import Select from 'react-select';
import { components } from 'react-select';
import { useCertificatesService } from '../../CertificatesService';
import { useTranslation } from 'react-i18next';

interface CertSelectProps {
    type: CertType;
    title: string;
    currentlyActiveCerts: any | string;
    activate: (selectionId: string) => void;
    deactivate?: (selectionId: string) => void;
    updateKey?: any;
    modelidentifiers: Map<string, string>;
    shouldLoadOnStartUp: boolean;
    doUpdate: boolean;
    updateNumber: number;
}

const CertSelectEcu = (props: CertSelectProps) => {
    const [options, setOptions] = useState<SelectableCertificate[]>([]);
    const [isLoaded, setLoaded] = useState<boolean>(false);
    const [loading, setLoading] = useState<boolean>(false);
    const certificatesService = useCertificatesService();
    const { t } = useTranslation();

    // Placeholder used in the select options
    const noValuePlaceholder = t('certificates_manual_selection_no_value_placeholder');

    // Current selection has to be handled so ID is available if item is deselected
    const [selection, setSelection] = useState<SelectableCertificate>();

    // Overwrite the style of the AsyncSelect
    const componentStyle = {
        option: (styles: any, { data, isFocused }: any) => {
            return {
                ...styles,
                fontSize: '0.8rem',
                color: isFocused || isSelected(data) ? '#ffffff' : '#000000',
                backgroundColor: isFocused ? '#79AEBF' : isSelected(data) ? '#00677F' : '#ffffff'
            };
        },
        noOptionsMessage: (styles: any) => ({ ...styles, fontSize: '0.8rem' }),
        placeholder: (styles: any) => ({ ...styles, fontSize: '0.8rem' }),
        singleValue: (styles: any) => ({ ...styles, fontSize: '0.8rem' })
    };

    const loadInitially = () => {
        if (shouldLoadEcu(props) && !isLoaded && props.shouldLoadOnStartUp) {
            load();
        }
        function shouldLoadEcu(props: CertSelectProps) {
            return props.currentlyActiveCerts;
        }
    };

    /**
     * Loading options
     */
    const loadOptions = () => {
        if (!isLoaded && props.shouldLoadOnStartUp) {
            load();
        }
    };

    /**
     * Loading data for component
     */
    const load = () => {
        setLoading(true);
        const requestCallback = {
            success: (response: any) => {
                setOptions(response.data);
                setLoaded(true);
                setLoading(false);
            }
        };
        certificatesService.getManualSelectionOptions(props.type, '', requestCallback);
    };

    /**
     * On mount load the options if necessery.
     */
    useEffect(() => {
        loadInitially();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.currentlyActiveCerts, props.shouldLoadOnStartUp]);

    /**
     * The selected certificates and the available options are loaded asynchronously.
     * The current selection can only be shown if both actions are done.
     */
    useEffect(() => {
        let selectedCertId = '';
        if (props.currentlyActiveCerts && !isLoaded) {
            selectedCertId = props.currentlyActiveCerts;
            const selectedCert = options.find(cert => cert.id === selectedCertId);
            setSelection(selectedCert);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.currentlyActiveCerts, options, props.shouldLoadOnStartUp]);

    /**
     * Forcing update from outside via props
     */
    useEffect(() => {
        if (props.doUpdate) {
            load();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.updateNumber]);

    /**
     * Handles selection changes. Triggers the activation / deactivation of the
     * certificate based on the event.
     *
     * @param value the selected value or null
     * @param action the action event
     */
    function onChange(value: ValueType<SelectableCertificate, boolean>, action: ActionMeta<any>) {
        switch (action.action) {
            case 'select-option':
                if (value && isValidCert(value)) {
                    setSelection(value);
                    props.activate(value.id);
                }
                break;
            case 'clear':
                if (selection && props.deactivate) {
                    props.deactivate(selection.id);
                }
                setSelection(undefined);
                break;
            default:
                break;
        }
    }

    /**
     * Validates whether the passed row is currently selected based
     * on 'selection' in the component's state.
     *
     * @param row the current row
     */
    function isSelected(row: SelectableCertificate): boolean {
        return row === selection;
    }

    /**
     * Returns the passed value or a placeholder if it's undefined or empty.
     *
     * @param value a value
     */
    function valueOrPlaceholder(value: string): string {
        if (value && value.length > 0) {
            return value;
        } else {
            return noValuePlaceholder;
        }
    }

    /**
     * The menu list with a table like header row.
     *
     * @param menuProps any
     */
    const MenuListWithHeader = (menuProps: any) => {
        return (
            <>
                <div className='zz-divtable-scrollcontainer'>
                    <div className='zz-divtable-table'>
                        <div className='zz-divtable-heading'>
                            <div className='zz-divtable-row'>{renderHeaderForMenuList().filter(item => item)}</div>
                        </div>
                        <components.MenuList {...menuProps}>{menuProps.children}</components.MenuList>
                    </div>
                </div>
            </>
        );
    };

    function renderHeaderForMenuList(): JSX.Element[] {
        let header: JSX.Element[] = [];
        if (props.modelidentifiers && props.modelidentifiers.size > 0) {
            for (let i: number = 0; i < props.modelidentifiers.size; i++) {
                header.push(<div className='zz-divtable-head'>{Array.from(props.modelidentifiers.keys())[i]}</div>);
            }
        }
        return header;
    }

    /**
     * Returns a element displaying the data of the passed certificate.
     *
     * @param certificate the data object
     */
    const renderCertificateOption = (certificate: SelectableCertificate) => {
        return renderCertificateOptionByCertificate(certificate).filter(item => item);
    };

    function filter(option: any, rawInput: string) {
        if (rawInput) {
            return (
                option.data.subject.includes(rawInput) ||
                option.data.serialNo.includes(rawInput) ||
                option.data.issuer.includes(rawInput) ||
                option.data.subject.includes(rawInput) ||
                option.data.uniqueECUID.includes(rawInput) ||
                option.data.validToDateTime.includes(rawInput)
            );
        }
        return true;
    }

    function renderCertificateOptionByCertificate(certificate: any): JSX.Element[] {
        let options: JSX.Element[] = [];
        if (props.modelidentifiers && props.modelidentifiers.size > 0) {
            for (let i: number = 0; i < props.modelidentifiers.size; i++) {
                options.push(
                    <div className='zz-divtable-cell'>
                        {valueOrPlaceholder(certificate[Array.from(props.modelidentifiers.values())[i]])}
                    </div>
                );
            }
        }
        return options;
    }

    return (
        <div className='certificate-selection'>
            <span id={'select_' + props.type.toLocaleLowerCase() + '_title'} className='cert-select-title'>
                {t(props.title)}
            </span>
            <Select
                id={'select_' + props.type.toLocaleLowerCase()}
                isClearable
                onChange={onChange}
                key={props.updateKey}
                options={options}
                components={{ MenuList: MenuListWithHeader }}
                styles={componentStyle}
                value={selection}
                formatOptionLabel={renderCertificateOption}
                className='cert-select'
                classNamePrefix='zz-divtable-elem'
                maxMenuHeight={150}
                menuPlacement='auto'
                isLoading={loading}
                filterOption={filter}
                onMenuOpen={loadOptions}
                noOptionsMessage={input => t('certificates_manual_selection_no_options_message')}
                placeholder={t('certificates_manual_selection_dialog_no_selection')}
                theme={theme => ({
                    ...theme,
                    colors: {
                        ...theme.colors,
                        primary25: '#79AEBF',
                        primary: '#00566A'
                    }
                })}
            />
        </div>
    );
};

export default React.memo(CertSelectEcu);
