import React from 'react';
import SessionTimeSingleton from './state/SessionTimeSingleton';
import SockJsClient from 'react-stomp';
import { StateActionType } from './state/StateAction';
import { getUserData } from './state/models/UserData';
import { showMessageDialog } from './common/dialogs/MessageDialog';
import { useAPI } from './common/API';
import { useStateValue } from './state/State';
import { useTranslation } from 'react-i18next';

const WebsocketConnection = function WebsocketConnection() {
    const API = useAPI();
    const WEBSOCKET_URL = '/ws';
    const TOPIC_LOGIN = '/topic/login';
    const TOPIC_LOGOUT = '/topic/logout';
    const TOPIC_TRIGGER_UPDATE = '/topic/triggerUpdate';
    const TOPIC_START_CERTS_UPDATE = '/topic/certsUpdateStarted';
    const TOPIC_STOP_CERTS_UPDATE = '/topic/certsUpdateStopped';
    const TOPIC_NEW_CERTS_UPDATE_STEP = '/topic/newCertsUpdateStep';
    const TOPIC_CERTS_UPDATE_RETRY = '/topic/certsUpdateRetry';
    const TOPIC_TIMER_RESET = '/topic/sessionTimerReset';
    const TOPIC_UPDATE_ALERT_MESSAGES = '/topic/updateAlertMessages';

    const TOPICS = [
        TOPIC_LOGIN,
        TOPIC_LOGOUT,
        TOPIC_TRIGGER_UPDATE,
        TOPIC_START_CERTS_UPDATE,
        TOPIC_STOP_CERTS_UPDATE,
        TOPIC_NEW_CERTS_UPDATE_STEP,
        TOPIC_CERTS_UPDATE_RETRY,
        TOPIC_TIMER_RESET,
        TOPIC_UPDATE_ALERT_MESSAGES
    ];

    const [{ certsUpdateProcess, isServerOffline }, dispatch] = useStateValue();
    const { t } = useTranslation();

    function dispatchMessage(msg, topic) {
        switch (topic) {
            case TOPIC_LOGIN:
                userLogin(msg);
                break;
            case TOPIC_LOGOUT:
                userLoggedOut(msg);
                break;
            case TOPIC_TRIGGER_UPDATE:
                triggerRefresh();
                break;
            case TOPIC_START_CERTS_UPDATE:
                updateCertificatesStarted();
                break;
            case TOPIC_STOP_CERTS_UPDATE:
                updateCertificatesStopped(msg);
                break;
            case TOPIC_NEW_CERTS_UPDATE_STEP:
                updateCertificatesStep(msg);
                break;
            case TOPIC_CERTS_UPDATE_RETRY:
                updateCertificatesRetries(msg);
                break;
            case TOPIC_TIMER_RESET:
                // other type of handling
                SessionTimeSingleton.getInstance().notifyReset(msg.remainingSessionSeconds);
                break;
            case TOPIC_UPDATE_ALERT_MESSAGES:
                updateAlertMessages(msg);
                break;
            default:
                break;
        }
    }

    function userLogin(message) {
        // Trigger differential certificate update after online login
        if (message.startUpdateAfterLogin === 'true') {
            // Reset integrity check existence
            dispatch({ type: StateActionType.UPDATE_INTEGRITY_CHECK_EXISTENCE, data: false });

            // Show certificate update process indicator in UI
            dispatch({ type: StateActionType.UPDATE_CERT_UPDATE_STATUS, data: { running: true } });
        }
        // Update local user data in global app context
        const userData = getUserData(
            message.userName,
            message.displayName,
            false,
            false,
            false,
            message.authenticated,
            message.authenticatedAgainstBackend,
            message.remainingSessionSeconds
        );
        dispatch({
            type: StateActionType.UPDATE_USER,
            data: userData
        });
        triggerRefresh();
    }

    function userLoggedOut(message) {
        if (message.isAuthenticatedAgainstBackend || message.isAuthenticatedAgainstBackend === 'true') {
            API.getBackground('/configurations/pkiLogoutUrl', {
                success: response => {
                    window.location.replace(response.data);
                }
            });
        } else {
            showMessageDialog(t('logout_message_dialog'), false);
            const userData = getUserData('', '', false, true, false, false, false, 0);
            dispatch({
                type: StateActionType.UPDATE_USER,
                data: userData
            });
            triggerRefresh();
        }
    }

    /**
     * Triggers the refresh of whatever component is shown by updating the updateTimestamp
     * in the global app context.
     */
    function triggerRefresh() {
        dispatch({ type: StateActionType.TRIGGER_UPDATE, data: new Date() });
    }

    function updateCertificatesStarted() {
        // Reset integrity check existence
        dispatch({ type: StateActionType.UPDATE_INTEGRITY_CHECK_EXISTENCE, data: false });

        // Show certificate update process indicator in UI
        dispatch({ type: StateActionType.UPDATE_CERT_UPDATE_STATUS, data: { running: true } });
    }

    function updateCertificatesStopped(message) {
        // Importing clear integrity check results
        dispatch({
            type: StateActionType.UPDATE_INTEGRITY_CHECK_EXISTENCE,
            data: false
        });

        // Update progress status
        dispatch({
            type: StateActionType.UPDATE_CERT_UPDATE_STATUS,
            data: {
                running: false,
                detailsStep: message.details.step,
                details: message.details.message,
                errorMetrics: message.details.error,
                type: message.type,
                updateCertificatesRetryInfo: certsUpdateProcess.updateCertificatesRetryInfo,
                didFailAllRetries: message.didFailAllRetries
            }
        });

        const userData = getUserData(
            message.userName,
            message.displayName,
            false,
            false,
            false,
            message.isAuthenticated,
            message.isAuthenticatedAgainstBackend,
            message.remainingSessionSeconds
        );
        dispatch({
            type: StateActionType.UPDATE_USER,
            data: userData
        });

        // Trigger data update after login if update successful
        if (message) {
            triggerRefresh();
        }
    }

    function updateCertificatesStep(message) {
        // Only update the step if not already stopped because of error
        if (certsUpdateProcess.running) {
            dispatch({
                type: StateActionType.UPDATE_CERT_UPDATE_STATUS,
                data: {
                    running: true,
                    detailsStep: message.details.step,
                    details: message.details.message,
                    errorMetrics: message.details.error,
                    type: message.type,
                    updateCertificatesRetryInfo: certsUpdateProcess.updateCertificatesRetryInfo
                }
            });
        }
    }

    function updateCertificatesRetries(message) {
        dispatch({
            type: StateActionType.UPDATE_CERT_UPDATE_STATUS,
            data: {
                running: true,
                type: certsUpdateProcess.type,
                errorMetrics: message.details.error,
                detailsStep: message.details.step,
                details: message.details.message,
                updateCertificatesRetryInfo: {
                    endpoint: message.endpoint,
                    currentRetry: message.currentRetry,
                    maxRetries: message.maxRetries,
                    nextRetryTimestamp: message.nextRetryTimestamp
                }
            }
        });
    }

    /**
     * Update Alert Messages
     */
    function updateAlertMessages(message) {
        dispatch({
            type: StateActionType.UPDATE_MESSAGE_ALERT,
            data: {
                ids: message.ids
            }
        });
    }

    /**
     * Sets the application back in an interactive mode if the server is online.
     * Also triggers an update of the current page.
     */
    function triggerServerOnlineState() {
        if (isServerOffline) {
            // Remove modal dialog to allow user action
            dispatch({
                type: StateActionType.UPDATE_SERVER_OFFLINE,
                data: false
            });

            // Trigger update of current page
            dispatch({
                type: StateActionType.TRIGGER_UPDATE,
                data: new Date()
            });
        }
    }

    /**
     * Sets the application in a non interactive mode if the server is offline by showing
     * a not dismissable, modal dialog.
     */
    function triggerServerOfflineState() {
        dispatch({
            type: StateActionType.UPDATE_SERVER_OFFLINE,
            data: true
        });
    }

    /**
     * Determines the interval before the next retry based on the current number of retries.
     *
     * @param retryCount number of executed retries
     */
    function determineNextRetry(retryCount) {
        let nextRetry = 2000;
        if (retryCount === 1) {
            triggerServerOfflineState();
        } else if (retryCount >= 3) {
            nextRetry = 5000;
        }
        console.log("Can't connect to websocket. Next retry in " + nextRetry + 'ms');
        return nextRetry;
    }

    return (
        <SockJsClient
            url={WEBSOCKET_URL}
            topics={TOPICS}
            onConnect={triggerServerOnlineState}
            getRetryInterval={determineNextRetry}
            onMessage={(msg, topic) => dispatchMessage(msg, topic)}
        />
    );
};

export default WebsocketConnection;
