import { UserData, getUserData } from './models/UserData';

import { ConfigData } from './models/ConfigData';
import { IntegrityCheckDataHolder } from './models/IntegrityCheckDataHolder';
import { MessageAlertData } from './models/MessageAlertData';
import { MessageData } from './models/MessageData';
import { ProcessData } from './models/ProcessData';
import { UpdateCertificatesProcessData } from './models/UpdateProcessData';

export interface AppState {
    user: UserData;
    process: ProcessData;
    message: MessageData;
    messageAlert: MessageAlertData;
    config?: {
        CERT_SELECTION: ConfigData;
        VALIDATE_CERTS: ConfigData;
        USER_ROLE: ConfigData;
        DELETE_EXPIRED_CERTS: ConfigData;
        AUTOLOGOUT: ConfigData;
        AUTO_CERT_UPDATE: ConfigData;
        DELETE_CERTS_SCHEDULE: ConfigData;
    };
    isXentry: boolean;
    isServerOffline: boolean;
    isIntegrityCheckExisting?: boolean;
    lastUpdateTime: Date;
    certsUpdateProcess: UpdateCertificatesProcessData;
    integrityCheckData: IntegrityCheckDataHolder;
}

export function initAppState(): AppState {
    const userData = getUserData('', '', false, true, false, false, false, 0);
    return {
        user: userData,
        process: {
            processName: '',
            showProcessDialog: false
        },
        message: {
            content: '',
            isError: false
        },
        messageAlert: {
            ids: []
        },
        config: {
            CERT_SELECTION: { id: '', configKey: '', configValue: '' },
            VALIDATE_CERTS: { id: '', configKey: '', configValue: '' },
            USER_ROLE: { id: '', configKey: '', configValue: '' },
            DELETE_EXPIRED_CERTS: { id: '', configKey: '', configValue: '' },
            AUTOLOGOUT: { id: '', configKey: '', configValue: '' },
            AUTO_CERT_UPDATE: { id: '', configKey: '', configValue: '' },
            DELETE_CERTS_SCHEDULE: { id: '', configKey: '', configValue: '' }
        },
        isXentry: false,
        isServerOffline: false,
        isIntegrityCheckExisting: false,
        lastUpdateTime: new Date(),
        certsUpdateProcess: {
            details: '',
            detailsStep: '',
            updateCertificatesRetryInfo: {
                endpoint: '',
                maxRetries: 0,
                currentRetry: 0,
                nextRetryTimestamp: 0
            },
            errorMetrics: false,
            didFailAllRetries: false,
            running: false
        },
        integrityCheckData: {}
    };
}
