import { CertificateData, CertificateType } from '../CertificateData';
import React, { useEffect, useState } from 'react';

import { APICallback } from '../../common/API';
import { DETAILS_PANEL_ANIMATION_DURATION } from '../../common/table/DetailsPanel';
import { formatTimestamp } from '../../common/DateFormat';
import { renderValidity } from '../CertificatesColumns';
import { useCertificatesService } from '../CertificatesService';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

const CertificateDetailsPanel = (data: CertificateData) => {
    const [certificate, setCertificate] = useState<CertificateData>(data);
    const [{ integrityCheckData }] = useStateValue();
    const certificatesService = useCertificatesService();
    const { t } = useTranslation();

    useEffect(() => {
        retrieveDetails(data);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [data]);

    function retrieveDetails(data: any) {
        // eslint-disable-next-line eqeqeq
        if (data == undefined) {
            setTimeout(() => setCertificate(data), DETAILS_PANEL_ANIMATION_DURATION);
        } else {
            setCertificate(data);
            if (data.type !== CertificateType.VIRTUAL_FOLDER) {
                const callback: APICallback = {
                    success: response => {
                        setCertificate(response.data);
                    }
                };
                certificatesService.getDetails(data.id, callback);
            }
        }
    }

    function renderCertificateDetailsForVirtualFolder(): JSX.Element {
        return (
            <div id='certificate-details-content'>
                <table className='certificate-details-table'>
                    <tbody>
                        {renderCertificateDetailsRowForVirtualFolder(
                            t('certificate_details_folder_a_kid'),
                            certificate.authorityKeyIdentifier
                        )}
                        {renderCertificateDetailsRowForVirtualFolder(
                            t('certificate_details_folder_with_pk'),
                            certificate.certsWithPrivateKey
                        )}
                        {renderCertificateDetailsRowForVirtualFolder(
                            t('certificate_details_folder_without_pk'),
                            certificate.certsWithoutPrivateKey
                        )}
                        {renderCertificateDetailsRowForVirtualFolder(
                            t('certificate_details_folder_total'),
                            certificate.totalCerts
                        )}
                    </tbody>
                </table>
            </div>
        );
    }

    function renderIntegrityCheckErrors(id: string): JSX.Element {
        if (integrityCheckData !== undefined && integrityCheckData[id] !== undefined) {
            return <div id='certificate-details-error'>{getIntegrityCheckData(id)}</div>;
        }
        return <></>;
    }

    function getIntegrityCheckData(id: string): JSX.Element[] {
        let result: JSX.Element[] = [];
        integrityCheckData[id].forEach((element: string) => {
            result.push(
                <>
                    {t(element)}
                    <br />
                </>
            );
        });

        return result;
    }

    function renderCertificateDetails(): JSX.Element {
        return (
            <div id='certificate-details-content'>
                {renderIntegrityCheckErrors(certificate.id)}
                <table className='certificate-details-table'>
                    <tbody>
                        {renderCertificateDetailsRow(
                            t('certificates_details_subject'),
                            certificate.subject,
                            t('certificates_details_target_vin'),
                            certificate.targetVIN,
                            t('certificates_details_subject_public_key'),
                            certificate.subjectPublicKey
                        )}
                        {renderCertificateDetailsRow(
                            t('certificates_details_serial_no'),
                            certificate.serialNo,
                            t('certificates_details_target_ecu'),
                            certificate.targetECU,
                            t('certificates_details_key_usage'),
                            certificate.keyUsageText
                        )}
                        {renderCertificateDetailsRow(
                            t('certificates_details_valid_from'),
                            formatTimestamp(certificate.validFrom, false),
                            t('certificates_details_valid_to'),
                            formatTimestamp(certificate.validTo, false),
                            t('certificates_details_signature_value'),
                            certificate.signature
                        )}
                        {renderCertificateDetailsRow(
                            t('certificates_details_version'),
                            certificate.version,
                            t('certificates_details_unique_ecu_id'),
                            certificate.uniqueECUID,
                            t('certificates_details_special_ecu'),
                            certificate.specialECU
                        )}
                        {renderCertificateDetailsRow(
                            t('certificates_details_user_role'),
                            certificate.userRole,
                            t('certificates_details_base_certificate_id'),
                            certificate.baseCertificateID,
                            t('certificates_details_nonce'),
                            certificate.nonce
                        )}
                        {renderCertificateDetailsRow(
                            t('certificates_details_issuer'),
                            certificate.issuer,
                            t('certificates_details_basic_constraints'),
                            certificate.basicConstraintsText,
                            t('certificates_details_subject_key_identifier'),
                            certificate.subjectKeyIdentifier
                        )}
                        {renderCertificateDetailsRow(
                            t('certificates_details_services'),
                            certificate.services,
                            t('certificates_details_pki_role'),
                            certificate.pkirole,
                            t('certificates_details_authority_key_identifier'),
                            certificate.authorityKeyIdentifier
                        )}
                        {renderCertificateDetailsRow(
                            t('certificates_details_issuer_serial_number'),
                            certificate.issuerSerialNumber,
                            t('certificates_details_prod_qualifier'),
                            certificate.prodQualifier,
                            t('certificates_details_algorithm_identifier'),
                            certificate.algorithmIdentifier
                        )}
                        {renderCertificateDetailsRow(
                            t('certificates_details_validity'),
                            renderValidity(certificate),
                            t('certificates_details_status'),
                            certificate.status,
                            t('certificates_details_target_subject_key_identifier'),
                            certificate.targetSubjectKeyIdentifier
                        )}
                        {renderCertificateDetailsRow(
                            t('certificate_details_zk_no'),
                            certificate.zkNo,
                            t('certificate_details_ecu_package_ts'),
                            formatTimestamp(certificate.ecuPackageTs, false),
                            t('certificate_details_link_cert_ts'),
                            formatTimestamp(certificate.linkCertTs, false)
                        )}
                        {renderCertificateDetailsRow(
                            t('certificate_details_pki_known'),
                            certificate.pkiKnown != null && String(certificate.pkiKnown),
                            t('certificate_details_pki_state'),
                            certificate.pkiState
                        )}
                    </tbody>
                </table>
            </div>
        );
    }

    function renderCertificateDetailsRowForVirtualFolder(key1: string, value1: any): JSX.Element {
        return (
            <tr className='certificate-details-table-row'>
                <td>
                    <b>{key1}:</b> {value1}
                </td>
            </tr>
        );
    }

    function renderCertificateDetailsRow(
        key1?: string,
        value1?: any,
        key2?: string,
        value2?: any,
        key3?: string,
        value3?: any
    ): JSX.Element {
        return (
            <tr className='certificate-details-table-row'>
                {key1 && (
                    <td>
                        <b>{key1}:</b> {value1}
                    </td>
                )}
                {key2 && (
                    <td>
                        <b>{key2}:</b> {value2}
                    </td>
                )}
                {key3 && (
                    <td>
                        <b>{key3}:</b> {value3}
                    </td>
                )}
            </tr>
        );
    }

    if (certificate) {
        return (
            <div id='certificate-details' style={{ zIndex: 1000 }}>
                {certificate.type === CertificateType.VIRTUAL_FOLDER
                    ? renderCertificateDetailsForVirtualFolder()
                    : renderCertificateDetails()}
            </div>
        );
    } else {
        return <div></div>;
    }
};

export default CertificateDetailsPanel;
