import React, { FormEvent, useState } from 'react';

import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { StateActionType } from '../state/StateAction';
import ValidatedTextfield from '../common/ValidatedTextfield';
import { getUserData } from '../state/models/UserData';
import { useAccountService } from './AccountService';
import { useStateValue } from '../state/State';
import { useTranslation } from 'react-i18next';

const LoginDialog = () => {
    const [{ user }, dispatch] = useStateValue();
    const { t } = useTranslation();
    const accountService = useAccountService();

    const [password, setPassword] = useState('');
    const [isPasswordValid, setPasswordValid] = useState(false);
    const [isSecondPasswordValid, setSecondPasswordValid] = useState(false);

    function startRegistration() {
        accountService.register(user.userName, password, {
            success: response => {
                const userData = getUserData(
                    response.data.userName,
                    response.data.displayName,
                    false,
                    false,
                    false,
                    response.data.authenticated,
                    response.data.authenticatedAgainstBackend,
                    response.data.remainingSessionSeconds
                );
                dispatch({
                    type: StateActionType.UPDATE_USER,
                    data: userData
                });
                dispatch({ type: StateActionType.TRIGGER_UPDATE, data: new Date() });
            }
        });
    }

    function submit(event: FormEvent) {
        event.preventDefault();
        startRegistration();
    }

    function onPasswordChange(isValid: boolean, input: string) {
        setPassword(input);
        setPasswordValid(isValid);
    }

    function onSecPasswordChange(isValid: boolean, input: string) {
        setSecondPasswordValid(isValid);
    }

    function validatePassword(input: string): boolean {
        const regexp = new RegExp(
            '(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!#\\$%&\\*@\\-\\"\'()+,/;:=?_>\\.])(?=.{10,})'
        );
        return regexp.test(input);
    }

    function validateSecPassword(input: string) {
        const pwMatch = password.localeCompare(input) === 0;
        setSecondPasswordValid(pwMatch);
        return pwMatch;
    }

    return (
        <Dialog open={user.isAuthenticatedAgainstBackend && user.isNewUser} disableBackdropClick disableEscapeKeyDown>
            <form onSubmit={submit}>
                <DialogTitle id='registration-dialog-title' className='blue'>
                    {t('register_dialog_title')}
                </DialogTitle>
                <DialogContent className='registration-dialog-content'>
                    <div id='registration-dialog-user' className='registration-dialog-user'>
                        {t('login_dialog_user_id')} {user.userName}
                    </div>
                    <span id='registration-dialog-desc'>{t('register_dialog_desc')}</span>
                    <ValidatedTextfield
                        id='dialog-registration-txt-pw'
                        label={t('register_dialog_label_pw')}
                        className='registration-textfield'
                        errorMessage={t('register_dialog_error_pw')}
                        type='password'
                        validateInput={validatePassword}
                        onChange={onPasswordChange}
                    />
                    <ValidatedTextfield
                        id='dialog-registration-txt-second-pw'
                        label={t('register_dialog_label_second_pw')}
                        className='registration-textfield second-password'
                        errorMessage={t('register_dialog_error_second_pw')}
                        type='password'
                        validateInput={validateSecPassword}
                        onChange={onSecPasswordChange}
                    />
                </DialogContent>
                <DialogActions>
                    <a className='cancel-logout-link' href='/users/logout/oauth'>
                        <Button id='dialog-registration-btn-cancel' color='primary' variant='outlined'>
                            {t('common_cancel')}
                        </Button>
                    </a>
                    <Button
                        id='dialog-registration-btn-submit'
                        color='primary'
                        variant='contained'
                        onClick={submit}
                        type='submit'
                        disabled={!(isPasswordValid && isSecondPasswordValid)}
                    >
                        {t('login_dialog_btn_submit')}
                    </Button>
                </DialogActions>
            </form>
        </Dialog>
    );
};

export default LoginDialog;
