import MaterialTable, { Column } from '@material-table/core';
import React, { useEffect, useState } from 'react';
import { defaultTableHeaderStyle, getCommonTranslation } from '../../common/table/ZenzefiTable';

import { AccountData } from './AccountData';
import ConfirmDeletionDialog from './ConfirmDeletionDialog';
import { useGeneralConfigService } from '../store/GeneralConfigStore';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

const AccountsComponent = () => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [{ lastUpdateTime }] = useStateValue();
    const { t } = useTranslation();
    const configService = useGeneralConfigService();

    const [accounts, setAccounts] = useState<AccountData[]>([]);
    const [accountToDelete, setAccountToDelete] = useState<AccountData>();
    const [showConfirmDialog, setShowConfirmDialog] = useState<boolean>(false);

    const columns: Column<any>[] = [
        { title: t('configurations_accounts_table_id'), field: 'userName', width: '165px' },
        { title: t('configurations_accounts_table_name'), field: 'name' }
    ];

    // Update accounts when lastUpdateTime value changes
    useEffect(() => {
        refreshData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [lastUpdateTime]);

    function refreshData() {
        configService.loadAccounts({ success: response => setAccounts(response.data) });
    }

    function confirmDeletion(account: AccountData) {
        setAccountToDelete(account);
        setShowConfirmDialog(true);
    }

    function confirmDialogCallback(isConfirmed: boolean) {
        setShowConfirmDialog(false);
        if (isConfirmed && accountToDelete) {
            configService.deleteAccount(accountToDelete, { success: response => refreshData() });
        }
    }

    function getTableTranslation(): object {
        const translation: any = getCommonTranslation(t);
        translation['header'] = { actions: '' };
        return translation;
    }

    return (
        <div>
            {showConfirmDialog && (
                <ConfirmDeletionDialog
                    userName={accountToDelete ? accountToDelete.userName : ''}
                    isDeletionConfirmed={confirmDialogCallback}
                />
            )}
            <div className='config-heading'>{t('configurations_accounts_title')}</div>
            <div className='accounts-table'>
                <MaterialTable
                    columns={columns}
                    options={{
                        filtering: false,
                        search: false,
                        sorting: false,
                        showTitle: false,
                        paging: false,
                        toolbar: false,
                        draggable: false,
                        actionsColumnIndex: -1,
                        rowStyle: rowData => ({ height: '40px' }),
                        headerStyle: defaultTableHeaderStyle
                    }}
                    localization={getTableTranslation()}
                    data={accounts}
                    actions={[
                        rowData => ({
                            icon: 'delete',
                            tooltip: t('configurations_accounts_delete_tooltip'),
                            onClick: (event, rowData) => confirmDeletion(rowData)
                        })
                    ]}
                />
            </div>
        </div>
    );
};

export default AccountsComponent;
