import { StateActionType } from '../../state/StateAction';
import { useCertificatesService } from '../CertificatesService';
import { useEffect } from 'react';
import { useStateValue } from '../../state/State';

/**
 * Hook that fetches the current status of the certificates update process.
 */
export function useUpdateCertificatesStatusHook() {
    const [{ user }, dispatch] = useStateValue();
    const certificatesService = useCertificatesService();

    useEffect(() => {
        if (user.isAuthenticatedAgainstBackend) {
            certificatesService.getUpdateProcessMetrics({
                success: response => {
                    if (response.data.updateCertificatesRetryInfo !== null) {
                        var formattedRetry = '';
                        formattedRetry = new Date(
                            response.data.updateCertificatesRetryInfo.nextRetryTimestamp
                        ).toLocaleString();
                        dispatch({
                            type: StateActionType.UPDATE_CERT_UPDATE_STATUS,
                            data: {
                                running: response.data.running,
                                // detailsStep and details are updated via websocket
                                // detailsStep: response.data.detailsStep,
                                // details: response.data.details,
                                updateCertificatesRetryInfo: {
                                    endpoint: response.data.updateCertificatesRetryInfo.endpoint,
                                    currentRetry: response.data.updateCertificatesRetryInfo.currentRetry,
                                    maxRetries: response.data.updateCertificatesRetryInfo.maxRetries,
                                    nextRetryTimestamp: formattedRetry
                                },
                                errorMetrics: response.data.errorMetrics,
                                didFailAllRetries: response.data.didFailAllRetries
                            }
                        });
                    }
                }
            });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [user.isAuthenticatedAgainstBackend]);
}
