import { Button } from '@material-ui/core';
import React from 'react';
import { useTranslation } from 'react-i18next';

interface Error500ComponentProps {
    reason?: string;
    callback: () => void;
}

const Error500Component = (props: Error500ComponentProps) => {
    const { t } = useTranslation();

    function getErrorMessage() {
        return props.reason ? t('error_500_message', { reason: props.reason }) : '';
    }

    return (
        <>
            <h3 id='error_500_title' className='red bold'>
                {t('error_500_title')}
            </h3>
            <div id='error_500_message' className='error-page-message'>
                {getErrorMessage()}
            </div>
            <Button id='error_500_homepage_btn' onClick={props.callback} color='primary' variant='contained'>
                {t('error_btn_link_to_homepage')}
            </Button>
        </>
    );
};

export default Error500Component;
