import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core';

import React from 'react';
import { useTranslation } from 'react-i18next';

interface CertificateValidationProps {
    closeDialog: (confirmed: boolean) => void;
}

const CertificateValidationDialog = (props: CertificateValidationProps) => {
    const { t } = useTranslation();

    function confirmDisabling(): void {
        props.closeDialog(true);
    }

    function close(): void {
        props.closeDialog(false);
    }

    return (
        <Dialog open={true}>
            <DialogTitle id='certificate-validation-dialog-title' className='blue'>
                {t('certificates_validation_confirm_dialog_title')}
            </DialogTitle>
            <DialogContent id='certificate-validation-dialog-content'>
                {t('certificates_validation_confirm_dialog_content')}
            </DialogContent>
            <DialogActions>
                <Button
                    id='certificate-validation-dialog-cancel-btn'
                    onClick={close}
                    color='primary'
                    variant='outlined'
                >
                    {t('common_cancel')}
                </Button>
                <Button
                    id='certificate-validation-dialog-ok-btn'
                    onClick={confirmDisabling}
                    color='primary'
                    variant='contained'
                    ref={element => (element != null ? element.focus() : false)}
                >
                    {t('certificate-validation_btn')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default CertificateValidationDialog;
