import React, { useEffect, useState } from 'react';

import { Button } from '@material-ui/core';
import { ConfigData } from '../../state/models/ConfigData';
import ManualSelectionDialog from './ManualSelectionDialog';
import StarIcon from '@material-ui/icons/Star';
import { StateActionType } from '../../state/StateAction';
import { USER_CONFIG } from '../../configurations/store/ConfigKeys';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';
import { useUserConfigService } from '../../configurations/store/UserConfigStore';

const ManualSelection = () => {
    const [isDialogVisible, setDialogVisible] = useState<boolean>(false);
    const [{ config, lastUpdateTime, user }, dispatch] = useStateValue();
    const userConfigService = useUserConfigService();
    const { t } = useTranslation();

    const [manualSelectionConfig, setManualSelectionConfig] = useState<ConfigData>();

    useEffect(() => {
        // load configurations from API
        userConfigService.refreshBackground();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [t, dispatch, lastUpdateTime, user.isAuthenticated]);

    useEffect(() => {
        setManualSelectionConfig(config[USER_CONFIG.CERT_SELECTION]);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [config[USER_CONFIG.CERT_SELECTION]]);

    /**
     * Closes the dialog and triggers a refresh of data.
     * This is needed to show the updated activeForSelection status.
     */
    function closeDialog() {
        setDialogVisible(false);
        dispatch({ type: StateActionType.TRIGGER_UPDATE, data: new Date() });
    }

    return (
        <>
            {manualSelectionConfig && manualSelectionConfig.configValue === 'manual' && (
                <>
                    <Button
                        id='manual-selection-btn'
                        onClick={() => setDialogVisible(true)}
                        color='primary'
                        variant='outlined'
                        className='certificates-toolbar-button'
                    >
                        <StarIcon className='btn-icon-left' />
                        {t('certificates_manual_selection_btn')}
                    </Button>
                    {isDialogVisible && <ManualSelectionDialog closeDialog={closeDialog} />}
                </>
            )}
        </>
    );
};

export default ManualSelection;
