import React, { EventHandler, useState } from 'react';

import TextField from '@material-ui/core/TextField';

export interface ValidatedTextfieldProps {
    id: string;
    type: string;
    label: string;
    className: string;
    disabled?: boolean;
    errorMessage: string;
    onBlur?: EventHandler<any>;
    value?: string;
    validateInput: (input: string) => boolean;
    onChange?: (isValid: boolean, input: string) => void;
}

const ValidatedTextfield = (props: ValidatedTextfieldProps) => {
    const [value, setValue] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const [showError, setShowError] = useState(false);

    function onChange(event: any) {
        const input = event.currentTarget.value;
        const isValid = props.validateInput(input);
        if (isValid) {
            setErrorMessage('');
            setShowError(false);
        } else {
            setErrorMessage(props.errorMessage);
            setShowError(true);
        }
        setValue(input);

        if (props.onChange) {
            props.onChange(isValid, input);
        }
    }

    return (
        <TextField
            id={props.id}
            type={props.type}
            label={props.label}
            className={props.className}
            error={showError && !props.disabled}
            value={props.value ? props.value : value}
            helperText={errorMessage}
            onChange={onChange}
            disabled={props.disabled}
            onBlur={props.onBlur}
        />
    );
};

export default ValidatedTextfield;
