import { APICallback, useAPI } from '../../common/API';

import { AccountData } from '../accounts/AccountData';

export interface GeneralConfigService {
    loadProxyType: (callback: APICallback) => Promise<void>;
    loadProxy: (callback: APICallback) => Promise<void>;
    updateProxyType: (type: string) => Promise<void>;
    updateProxy: (host: string, port: string) => Promise<void>;
    loadAccounts: (callback: APICallback) => Promise<void>;
    deleteAccount: (account: AccountData, callback: APICallback) => Promise<void>;
    loadPkiScopeSwitch: (callback: APICallback) => Promise<void>;
    updatePkiScopeSwitch: (scope: boolean) => Promise<void>;
}

export const useGeneralConfigService = (): GeneralConfigService => {
    const API = useAPI();

    return {
        loadProxyType: (callback: APICallback) => API.getBackground('/configurations/proxyType', callback),
        loadProxy: (callback: APICallback) => API.getBackground('/configurations/proxy', callback),
        updateProxyType: (type: string) => API.postBackground('/configurations/proxyType', { proxyType: type }, {}),
        updateProxy: (host: string, port: string) =>
            API.postBackground('/configurations/proxy', { hostname: host, port: port }, {}),
        loadAccounts: (callback: APICallback) => API.getBackground('/users', callback),
        deleteAccount: (account: AccountData, callback: APICallback) =>
            API.deleteBackground('/users/' + account.id, callback),
        loadPkiScopeSwitch: (callback: APICallback) => API.getBackground('/configurations/pkiScope', callback),
        updatePkiScopeSwitch: (scope: boolean) => API.postBackground('/configurations/pkiScope', { scope: scope }, {})
    };
};
