import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core';

import React from 'react';
import { useTranslation } from 'react-i18next';

interface DeleteDialogProps {
    closeDialog: (confirmed: boolean) => void;
}

const DeleteCertificateDialog = (props: DeleteDialogProps) => {
    const { t } = useTranslation();

    function deleteCertificate(): void {
        props.closeDialog(true);
    }

    function close(): void {
        props.closeDialog(false);
    }

    return (
        <Dialog open={true}>
            <DialogTitle id='delete-certificate-dialog-title' className='blue'>
                {t('certificates_delete_confirm_dialog_title')}
            </DialogTitle>
            <DialogContent id='delete-certificate-dialog-content'>
                {t('certificates_delete_confirm_dialog_content')}
            </DialogContent>
            <DialogActions>
                <Button id='delete-certificate-dialog-cancel-btn' onClick={close} color='primary' variant='outlined'>
                    {t('common_cancel')}
                </Button>
                <Button
                    id='delete-certificate-dialog-delete-btn'
                    onClick={deleteCertificate}
                    color='primary'
                    variant='contained'
                    ref={element => (element != null ? element.focus() : false)}
                >
                    {t('certificates_delete_btn')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default DeleteCertificateDialog;
