import Language from './Language';
import LanguageDetector from 'i18next-browser-languagedetector';
import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import translationDE from './de/translation_de.json';
import translationEN from './en/translation_en.json';

const resources = {
    en: {
        translation: translationEN
    },
    de: {
        translation: translationDE
    }
};

i18n.use(initReactI18next) // passes i18n down to react-i18next
    .use(LanguageDetector)
    .init({
        resources,
        keySeparator: false,
        interpolation: {
            escapeValue: false
        },
        detection: {
            // order and from where user language should be detected
            order: ['querystring', 'cookie', 'localStorage', 'navigator', 'htmlTag', 'path', 'subdomain'],

            // cache user language on
            caches: ['localStorage', 'cookie'],
            excludeCacheFor: ['cimode'] // languages to not persist (cookie, localStorage)
        }
    })
    .then(function() {
        if (i18n.language.startsWith(Language.german)) {
            i18n.changeLanguage(Language.german);
        } else {
            i18n.changeLanguage(Language.english);
        }
    });

export default i18n;
