import { LoggingService, useLoggingService } from './LoggingService';

import ArchiveIcon from '@material-ui/icons/Archive';
import Button from '@material-ui/core/Button';
import React from 'react';
import RefreshIcon from '@material-ui/icons/Refresh';
import { StateActionType } from '../state/StateAction';
import { useStateValue } from '../state/State';
import { useTranslation } from 'react-i18next';

export interface LoggingToolbarProps {
    refreshBtnClicked: () => void;
}

const LoggingToolbar = (props: LoggingToolbarProps) => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [user, dispatch] = useStateValue();
    const { t } = useTranslation();
    const service: LoggingService = useLoggingService();

    function startDownload() {
        service.getLogIntegrity({
            success: response => {
                let message;
                if (response.data === true) {
                    message = t('logging_integrity_passed');
                } else {
                    message = t('logging_integrity_failed');
                }
                dispatch({
                    type: StateActionType.UPDATE_MESSAGE,
                    data: { content: message, isError: false }
                });
                service.downloadLogArchive();
            }
        });
    }

    return (
        <div className='toolbar-btn-container'>
            <Button
                id='logging-btn-refresh'
                color='primary'
                variant='outlined'
                disableFocusRipple={true}
                onClick={props.refreshBtnClicked}
            >
                <RefreshIcon className='btn-icon-left' />
                {t('logging_refresh_btn')}
            </Button>
            <Button
                id='logging-btn-download'
                color='primary'
                variant='outlined'
                disableFocusRipple={true}
                onClick={startDownload}
            >
                <ArchiveIcon className='btn-icon-left' />
                {t('logging_download_btn')}
            </Button>
        </div>
    );
};

export default LoggingToolbar;
