export interface ConfigData {
    id: string,
    configKey: string,
    configValue: string
}

export function isConfigDataArray(input: any): input is Array<ConfigData> {
    const propsList = Object.keys(input);
    let result = propsList.length > 0 ? true : false;
    propsList.forEach(function (key, i) {
        if (!isConfigData(input[key])) {
            result = false;
        }
    })
    return result;
}

export function isConfigData(input: any): input is ConfigData {
    return input.id !== undefined && input.configValue !== undefined;
}