import {
    AppBar,
    Box,
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    Tab,
    Tabs,
    Typography
} from '@material-ui/core';
import React, { useEffect, useState } from 'react';

import { CertType } from './selection/CertType';
import EnhancedRightsSelection from './enhancedrights/EnhancedRightsSelection';
import ReplacementPackageSelection from './ReplacementPackageSelection';
import SingleCertificateSelection from './SingleCertificateSelection';
import { useCertificatesService } from '../CertificatesService';
import { useTranslation } from 'react-i18next';

interface ManualSelectionDialogProps {
    closeDialog: () => void;
}

function TabPanel(props: any) {
    return (
        <Typography
            component='div'
            role='tabpanel'
            hidden={props.value !== props.index}
            id={`manual-selection-dialog-tab-${props.index}`}
            aria-labelledby={`manual-selection-dialog-tab-${props.index}`}
            {...props.other}
        >
            <Box p={3} style={{ padding: 0 }}>
                {props.children}
            </Box>
        </Typography>
    );
}

const ManualSelectionDialog = (props: ManualSelectionDialogProps) => {
    const [activeCertificates, setActiveCertificates] = useState<any>({});
    const [activeEcuCertForVsm, setActiveEcuCertForVsm] = useState<string>('');
    const [activeEcuCertForRp, setActiveEcuCertForRp] = useState<string>('');
    const [currentTab, setCurrentTab] = useState<number>(0);
    const { t } = useTranslation();
    const certificatesService = useCertificatesService();

    /**
     * Load currently active certificates and pass to childs via component's state.
     */
    useEffect(() => {
        certificatesService.getActive({ success: response => setActiveCertificates(response.data.data) });
        certificatesService.getActiveForUsecases(CertType.Ecu, {
            success: response => {
                // global use case or different selection for VSM and RP use cases
                if (response.data.data.G.length === 1) {
                    setActiveEcuCertForVsm(response.data.data.G[0]);
                    setActiveEcuCertForRp(response.data.data.G[0]);
                } else {
                    setActiveEcuCertForVsm(response.data.data.VSM[0]);
                    setActiveEcuCertForRp(response.data.data.RP[0]);
                }
            }
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <Dialog id='manual-selection-dialog' open={true} fullWidth maxWidth={'lg'}>
            <DialogTitle id='manual-selection-dialog-title' className='blue'>
                {t('certificates_manual_selection_dialog_title')}
            </DialogTitle>
            <DialogContent id='manual-selection-dialog-content'>
                <AppBar position='static' elevation={1} style={{ color: 'black', backgroundColor: 'white' }}>
                    <Tabs
                        value={currentTab}
                        onChange={(event, value) => setCurrentTab(value)}
                        aria-label='manual-selection-dialog-tabs'
                    >
                        <Tab id='single_cert_tab' label={t('certificates_manual_selection_dialog_tab_1_heading')} />
                        <Tab id='enhanced_rights_tab' label={t('certificates_manual_selection_dialog_tab_2_heading')} />
                        <Tab
                            id='replacement_package_tab'
                            label={t('certificates_manual_selection_dialog_tab_3_heading')}
                        />
                    </Tabs>
                </AppBar>
                <TabPanel value={currentTab} index={0}>
                    <SingleCertificateSelection
                        activeCertificates={activeCertificates}
                        activeEcuCert={activeEcuCertForVsm}
                        shouldLoadOnStartUp={true}
                    />
                </TabPanel>
                <TabPanel value={currentTab} index={1}>
                    <EnhancedRightsSelection
                        activeCertificates={activeCertificates}
                        shouldLoadOnStartUp={currentTab === 1}
                    />
                </TabPanel>
                <TabPanel value={currentTab} index={2}>
                    <ReplacementPackageSelection
                        activeCertificates={activeCertificates}
                        activeEcuCert={activeEcuCertForRp}
                        shouldLoadOnStartUp={currentTab === 2}
                    />
                </TabPanel>
            </DialogContent>
            <DialogActions>
                <Button
                    id='manual-selection-dialog-close-btn'
                    onClick={props.closeDialog}
                    color='primary'
                    variant='contained'
                >
                    {t('common_close')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default ManualSelectionDialog;
