import React, { useEffect, useState } from 'react';

import Switch from '@material-ui/core/Switch';
import { useGeneralConfigService } from '../store/GeneralConfigStore';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

const PkiScopeConfigComponent = () => {
    // App state
    const [{ lastUpdateTime, isXentry, user }] = useStateValue();
    const configService = useGeneralConfigService();
    const [isChecked, setScope] = useState(false);
    const { t } = useTranslation();

    // Update proxy when lastUpdateTime value changes
    useEffect(() => {
        if (!isXentry) {
            refreshScope();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [lastUpdateTime]);

    function updateConfig(checked: boolean) {
        configService.updatePkiScopeSwitch(checked);
        setScope(checked);
    }

    function refreshScope() {
        configService.loadPkiScopeSwitch({
            success: response => {
                const scope = response.data.message;
                setScope(scope);
            }
        });
    }

    return (
        <>
            <div className='switch-config-component'>
                {isXentry && <span className='config-disable-desc'>{t('configurations_pkiscope_disabled_desc')}</span>}
                {!isXentry && user.isAuthenticated && (
                    <span className='config-disable-desc'>{t('configurations_pkiscope_user_disabled_desc')}</span>
                )}
                <Switch
                    id={'switch-PkiScope'}
                    className='config-switch'
                    checked={isChecked}
                    onChange={(event, checked) => {
                        updateConfig(checked);
                    }}
                    disabled={isXentry || user.isAuthenticated}
                />
                <span id={'switch-heading-PkiScope'} className='config-heading'>
                    {t('configurations_pkiscope_title')}
                </span>
                <div id={'switch-description-PkiScope'} className='config-description'>
                    {t('configurations_pkiscope_desc')}
                </div>
            </div>
        </>
    );
};

export default PkiScopeConfigComponent;
