import { APICallback, useAPI } from '../common/API';
import { mapPageHeader, mapUrlParams } from '../common/ParameterMapper';

import LogItem from './LogItem';
import { Query } from '@material-table/core';
import { useTranslation } from 'react-i18next';

export interface LoggingService {
    queryLogs: (query: Query<LogItem>, callback: APICallback) => Promise<void>;
    getLogIntegrity: (callback: APICallback) => Promise<void>;
    downloadLogArchive: () => void;
}

export const useLoggingService = (): LoggingService => {
    const { t } = useTranslation();
    const API = useAPI();

    return {
        queryLogs: (query: Query<LogItem>, callback: APICallback) =>
            API.getWithHeadersNoProgress(
                '/logs/filter' + mapUrlParams(query.filters),
                t('common_placeholder_loading'),
                mapPageHeader(query),
                callback
            ),
        getLogIntegrity: (callback: APICallback) =>
            API.get('/logs/logsIntegrity?cached=false', t('common_placeholder_loading'), callback),
        downloadLogArchive: () => API.downloadFileFrom('/logs/archive')
    };
};
