import './App.css';
import './components/account/account.css';
import './components/errors/errors.css';
import './components/header/header.css';
import './components/logging/logging.css';
import './components/certificates/certificates.css';
import './components/configurations/configurations.css';

import { AppState, initAppState } from './components/state/AppState';
import { HashRouter, Redirect, Route, Switch } from 'react-router-dom';
import React, { useState } from 'react';

import CertificatesTableComponent from './components/certificates/CertificatesTableComponent';
import ConfigComponent from './components/configurations/ConfigComponent';
import { ContentErrorBoundary } from './components/errors/ContentErrorBoundary';
import { DUITheme } from './components/theme/DUITheme';
import EnvironmentComponent from './components/environment/EnvironmentComponent';
import Error401Component from './components/errors/Error401Component';
import Error401ClockComponent from './components/errors/Error401ClockComponent';
import Error404Component from './components/errors/Error404Component';
import Error500Component from './components/errors/Error500Component';
import Header from './components/header/Header';
import LoggingComponent from './components/logging/LoggingComponent';
import MessageDialog from './components/common/dialogs/MessageDialog';
import { MuiThemeProvider } from '@material-ui/core/styles';
import ProgressDialog from './components/common/dialogs/ProgressDialog';
import ServerOfflineDialog from './components/common/dialogs/ServerOfflineDialog';
import StartUpHandler from './components/StartUpHandler';
import { StateProvider } from './components/state/State';
import { StateReducer } from './components/state/StateReducer';
import { VIEW_LINKS } from './components/header/Navigation';
import WebsocketConnection from './components/WebsocketConnection';

const App = () => {
    const [errorBoundaryKey, setErrorBoundaryKey] = useState<number>(0);
    const appState: AppState = initAppState();

    function hideErrorBoundary() {
        setErrorBoundaryKey(errorBoundaryKey + 1);
    }

    return (
        <StateProvider initialState={appState} reducer={StateReducer}>
            <MuiThemeProvider theme={DUITheme}>
                <HashRouter>
                    <div className='app'>
                        <StartUpHandler />
                        <ProgressDialog />
                        <MessageDialog />
                        <WebsocketConnection />
                        <ServerOfflineDialog />
                        <Header />
                        <div className='content'>
                            <ContentErrorBoundary key={errorBoundaryKey} callback={hideErrorBoundary}>
                                <Switch>
                                    <Route exact path='/'>
                                        <Redirect to={VIEW_LINKS.CERTIFICATES} />
                                    </Route>
                                    <Route
                                        path={VIEW_LINKS.CERTIFICATES}
                                        exact
                                        component={CertificatesTableComponent}
                                    />
                                    <Route path={VIEW_LINKS.LOGGING} exact component={LoggingComponent} />
                                    <Route path={VIEW_LINKS.CONFIGURATION} exact component={ConfigComponent} />
                                    <Route path={VIEW_LINKS.ENVIRONMENT} exact component={EnvironmentComponent} />
                                    <Route path={VIEW_LINKS.ERROR_401} exact component={Error401Component} />
                                    <Route path={VIEW_LINKS.ERROR_401_CLOCK} exact component={Error401ClockComponent} />
                                    <Route path={VIEW_LINKS.ERROR_500} exact component={Error500Component} />
                                    <Route component={Error404Component} />
                                </Switch>
                            </ContentErrorBoundary>
                        </div>
                    </div>
                </HashRouter>
            </MuiThemeProvider>
        </StateProvider>
    );
};

export default App;
