/* eslint-disable eqeqeq */

import React, { useState } from 'react';

import { Button } from '@material-ui/core';
import DeleteCertificateDialog from './DeleteCertificateDialog';
import DeleteIcon from '@material-ui/icons/Delete';
import { useCertificatesService } from '../CertificatesService';
import { useTranslation } from 'react-i18next';

interface DeleteCertificateProps {
    selection: any;
    blockClickAway: () => void;
    closeMenu: (refresh: boolean) => void;
}

const DeleteCertificateContextMenu = (props: DeleteCertificateProps) => {
    const [isDialogVisible, setDialogVisible] = useState<boolean>(false);
    const { t } = useTranslation();
    const certificatesService = useCertificatesService();

    /**
     * Handles the deletion of certificates and closes the context menu.
     *
     * @param confirmed true, if the deletion has been confirmed.
     */
    function closeDialog(confirmed: boolean) {
        if (confirmed) {
            certificatesService.delete(props.selection, {
                success: response => {
                    props.closeMenu(true);
                }
            });
        } else {
            props.closeMenu(false);
        }
    }

    /**
     * Opens the {@link DeleteCertificateDialog} and blocks the {@link ClickAwayListener} via properties.
     */
    function openDialog() {
        props.blockClickAway();
        setDialogVisible(true);
    }

    return (
        <>
            {isDialogVisible && <DeleteCertificateDialog closeDialog={closeDialog} />}
            <Button
                id='context-menu-delete-cert'
                className='certificate-context-menu-item'
                onClick={openDialog}
                disabled={props.selection == undefined}
            >
                <DeleteIcon />
                {t('certificates_delete_btn')}
            </Button>
        </>
    );
};

export default DeleteCertificateContextMenu;
