import { CertColumn } from '../certificates/CertificatesColumns';
import { LoggingColumn } from '../logging/LoggingComponent';
import { Query } from '@material-table/core';

export function mapPageHeader(query: Query<any>): object {
    const requestedPage = query.page + 1;
    const lastEntry = query.pageSize * requestedPage;
    const firstEntry = lastEntry - query.pageSize + 1;
    return { Range: 'items=' + firstEntry + '-' + lastEntry };
}

export function mapUrlParams(filters: any[]): string {
    var result = '';
    if (filters != null) {
        result += '?';

        filters.forEach(filter => {
            if (isRangeDateTimeColumn(filter.column.field)) {
                result += mapRangeDateTime(filter);
            } else if (Array.isArray(filter.value)) {
                if (filter.value.length > 0) {
                    result += mapArray(filter);
                }
            } else {
                result += mapString(filter);
            }
            if (result.length > 1) {
                result += '&';
            }
        });
    }
    return result;
}

/**
 * Maps passed filter string to an range date time query param.
 *
 * @param filter date as range date time object
 */
function mapRangeDateTime(filter: any): string {
    let columnName: string = filter.column.field;
    let startDate: Date = filter.value.startDate;
    let endDate: Date = filter.value.endDate;

    if (!isValidDate(startDate) && !isValidDate(endDate)) {
        return '';
    } else if (isTimestampColumn(columnName)) {
        if (!isValidDate(endDate)) {
            return columnName + '=gte=' + startDate.getTime();
        } else if (!isValidDate(startDate)) {
            return columnName + '=lte=' + endDate.getTime();
        }
        return columnName + '=gte=' + startDate.getTime() + '&' + columnName + '=lte=' + endDate.getTime();
    } else {
        if (!isValidDate(endDate)) {
            return columnName + '=gte=' + getISOStringByOffset(startDate, true);
        } else if (!isValidDate(startDate)) {
            return columnName + '=lte=' + getISOStringByOffset(endDate, true);
        }
        return (
            columnName +
            '=gte=' +
            getISOStringByOffset(startDate, true) +
            '&' +
            columnName +
            '=lte=' +
            getISOStringByOffset(endDate, true)
        );
    }
}

function getISOStringByOffset(date: Date, offset: boolean): string {
    const result = new Date();
    if (offset) {
        // Date is initialized in UTC and has to be converted to local system time
        const dateOffset = date.getTimezoneOffset();
        result.setTime(date.getTime() - dateOffset * 60 * 1000);
    } else {
        result.setTime(date.getTime());
    }
    return result.toISOString();
}

function isValidDate(date: any) {
    return date && Object.prototype.toString.call(date) === '[object Date]' && !isNaN(date);
}

/**
 * Creates a request filter param based on an array of filter values.
 *
 * @param filter filter value(s)
 */
function mapArray(filter: any): string {
    var result = '';
    if (filter.column.type !== 'boolean' || filter.value.length === 1) {
        // Column
        result += filter.column.field + '=in=(';

        // Value
        filter.value.forEach((filter: string) => {
            result += filter + ',';
        });
        result = result.substring(0, result.length - 1);
        result += ')';
    }
    return result;
}

/**
 * Specifies which columns contain dates in timestamp format.
 *
 * @param columnName the name of the column
 */
function isTimestampColumn(columnName: string): boolean {
    return columnName === LoggingColumn.Timestamp;
}

/**
 * Specifies which columns contain dates in ISO format.
 *
 * @param columnName the name of the column
 */
function isRangeDateTimeColumn(columnName: string): boolean {
    return isValidTo(columnName) || isValidFrom(columnName) || isTimestampColumn(columnName);
}

function isValidTo(columnName: string): boolean {
    return columnName === CertColumn.ValidTo;
}

function isValidFrom(columnName: string): boolean {
    return columnName === CertColumn.ValidFrom;
}

/**
 * Creates a new text filter request param.
 *
 * @param filter filter text as string
 */
function mapString(filter: any): string {
    // Ignore case of filter string (/i)
    return filter.column.field + '=match=/' + filter.value + '/i';
}

export function getFilenameFromHeaders(headers: any): string {
    const contentDispositionHeader: string = headers['content-disposition'];
    if (contentDispositionHeader) {
        var fileName = contentDispositionHeader.substring(
            contentDispositionHeader.indexOf('filename=') + 10,
            contentDispositionHeader.length - 1
        );
        return fileName;
    } else {
        return 'content-disposition not found!';
    }
}
