import CertSelect from './selection/CertSelect';
import CertSelectEcu from './selection/CertSelectEcu';
import { CertType } from './selection/CertType';
import React from 'react';
import { useSelectionUpdate } from './selection/SelectionUpdate';
import { useTranslation } from 'react-i18next';

interface SingleCertificateSelectionProps {
    activeCertificates: any;
    activeEcuCert: string;
    shouldLoadOnStartUp: boolean
}

const SingleCertificateSelection = (props: SingleCertificateSelectionProps) => {
    const selectionUpdate = useSelectionUpdate();
    const { t } = useTranslation();

    function getIdentifiersForDiagnosticCertificate(): Map<string, string> {
        let map = new Map();

        map.set(t('certificates_subject_title'), 'subject')
            .set(t('certificates_user_role_title'), 'userRole')
            .set(t('certificates_issuer_title'), 'issuer')
            .set(t('certificates_serial_number_title'), 'serialNo')
            .set(t('certificates_target_ecu_title'), 'targetECU')
            .set(t('certificates_target_vin_title'), 'targetVIN')
            .set(t('certificates_valid_to_title'), 'validToDateTime');

        return map;
    }

    function getIdentifiersForCodingCertificate(): Map<string, string> {
        let map = new Map();

        map.set(t('certificates_subject_title'), 'subject')
            .set(t('certificates_issuer_title'), 'issuer')
            .set(t('certificates_serial_number_title'), 'serialNo')
            .set(t('certificates_target_ecu_title'), 'targetECU')
            .set(t('certificates_target_vin_title'), 'targetVIN')
            .set(t('certificates_valid_to_title'), 'validToDateTime');

        return map;
    }

    function getIdentifiersForECUCertificate(): Map<string, string> {
        let map = new Map();

        map.set(t('certificates_subject_title'), 'subject')
            .set(t('certificates_issuer_title'), 'issuer')
            .set(t('certificates_serial_number_title'), 'serialNo')
            .set(t('certificates_unique_ecu_id_title'), 'uniqueECUID')
            .set(t('certificates_valid_to_title'), 'validToDateTime');

        return map;
    }

    function getIdentifiersForTimeCertificate(): Map<string, string> {
        let map = new Map();

        map.set(t('certificates_subject_title'), 'subject')
            .set(t('certificates_user_role_title'), 'userRole')
            .set(t('certificates_issuer_title'), 'issuer')
            .set(t('certificates_serial_number_title'), 'serialNo')
            .set(t('certificates_nonce_title'), 'nonce')
            .set(t('certificates_target_ecu_title'), 'targetECU')
            .set(t('certificates_target_vin_title'), 'targetVIN')
            .set(t('certificates_valid_to_title'), 'validToDateTime');

        return map;
    }

    function getIdentifiersForSecocisCertificate(): Map<string, string> {
        let map = new Map();

        map.set(t('certificates_issuer_title'), 'issuer')
            .set(t('certificates_holder_title'), 'baseCertificateID')
            .set(t('certificates_target_skid_title'), 'targetSubjectKeyIdentifier')
            .set(t('certificates_target_ecu_title'), 'targetECU')
            .set(t('certificates_target_vin_title'), 'targetVIN')
            .set(t('certificates_valid_to_title'), 'validToDateTime');

        return map;
    }

    return (
        <div className='manual-selection-dialog-tab-content' id = 'single-certificate-content'>
            <CertSelect
                type={CertType.Diagnostic}
                title='certificates_type_diagnostic'
                activate={selectionUpdate.activateCert}
                deactivate={selectionUpdate.deactivateCert}
                currentlyActiveCerts={props.activeCertificates}
                modelidentifiers={getIdentifiersForDiagnosticCertificate()}
                shouldLoadOnStartUp={props.shouldLoadOnStartUp}
                doUpdate={false}
                updateNumber={0}
            />
            <CertSelect
                type={CertType.VariantCoding}
                title='certificates_type_variant_coding'
                activate={selectionUpdate.activateCert}
                deactivate={selectionUpdate.deactivateCert}
                currentlyActiveCerts={props.activeCertificates}
                modelidentifiers={getIdentifiersForCodingCertificate()}
                shouldLoadOnStartUp={props.shouldLoadOnStartUp}
                doUpdate={false}
                updateNumber={0}
            />
            <CertSelectEcu
                type={CertType.Ecu}
                title='certificates_manual_selection_ecu_vsm_label'
                activate={selectionUpdate.activateCertForVsmUsecase}
                deactivate={selectionUpdate.deactivateCertForVsmUsecase}
                currentlyActiveCerts={props.activeEcuCert}
                modelidentifiers={getIdentifiersForECUCertificate()}
                shouldLoadOnStartUp={props.shouldLoadOnStartUp}
                doUpdate={false}
                updateNumber={0}
            />
            <CertSelect
                type={CertType.Time}
                title='certificates_type_time'
                activate={selectionUpdate.activateCert}
                deactivate={selectionUpdate.deactivateCert}
                currentlyActiveCerts={props.activeCertificates}
                modelidentifiers={getIdentifiersForTimeCertificate()}
                shouldLoadOnStartUp={props.shouldLoadOnStartUp}
                doUpdate={false}
                updateNumber={0}
            />
            <CertSelect
                type={CertType.Secocis}
                title='certificates_type_secocis'
                activate={selectionUpdate.activateCert}
                deactivate={selectionUpdate.deactivateCert}
                currentlyActiveCerts={props.activeCertificates}
                modelidentifiers={getIdentifiersForSecocisCertificate()}
                shouldLoadOnStartUp={props.shouldLoadOnStartUp}
                doUpdate={false}
                updateNumber={0}
            />
        </div>
    );
};

export default SingleCertificateSelection;
