/* eslint-disable @typescript-eslint/no-unused-vars */

import { API, useAPI } from '../common/API';
import { CertificateData, CertificateType } from './CertificateData';

import { APICallback } from '../common/API';
import { Query } from '@material-table/core';
import { UpdateType } from './update/UpdateCertificates';
import { UseCase } from './manualselection/selection/UseCase';
import { mapUrlParams } from '../common/ParameterMapper';
import { useTranslation } from 'react-i18next';

export interface CertificatesService {
    getDetails: (certificateId: string, callback: APICallback) => Promise<void>;
    getFilteredCerts: (query: Query<CertificateData>, callback: APICallback) => Promise<void>;
    getExport: (certificateId: string, callback: APICallback) => Promise<void>;
    restore: (callback: APICallback) => Promise<void>;
    delete: (certificate: any, callback: APICallback) => void;
    import: (files: FileList, callback: APICallback) => void;

    // Update
    startUpdate: (type: UpdateType, callback: APICallback) => Promise<void>;
    stopUpdate: (callback: APICallback) => Promise<void>;
    getUpdateProcessMetrics: (callback: APICallback) => Promise<void>;

    // System integrity
    checkSystemIntegrity: (callback: APICallback) => Promise<void>;
    checkSystemIntegrityLogExistence: (callback: APICallback) => Promise<void>;
    downloadSystemIntegrityLog: () => void;

    // Manual selection
    activateForUsecase: (id: string, usecase: UseCase) => Promise<void>;
    deactivateForUsecase: (id: string, usecase: UseCase) => Promise<void>;
    getActiveForUsecases: (type: string, callback: APICallback) => Promise<void>;
    getActive: (callback: APICallback) => Promise<void>;
    getActiveEnhancedRightsCerts: (callback: APICallback) => Promise<void>;
    getManualSelectionOptions: (type: string, filter: string, callback: APICallback) => Promise<void>;
    activateForTesting: (id: string, callback?: APICallback) => Promise<void>;
    deactivateForTesting: (id: string, callback?: APICallback) => Promise<void>;
}

export const useCertificatesService = (): CertificatesService => {
    const { t } = useTranslation();
    const API = useAPI();

    return {
        getDetails: (certificateId: string, callback: APICallback) =>
            API.getBackground('/certificates/details/' + certificateId, callback),
        getExport: (certificateId: string, callback: APICallback) =>
            API.getBackground('/certificates/export/' + certificateId, callback),
        getFilteredCerts: (query: Query<CertificateData>, callback: APICallback) =>
            API.getBackground('/certificates/filter' + mapUrlParams(query.filters), callback),
        restore: (callback: APICallback) => API.postBackground('/certificates/restore', {}, callback),
        delete: (certificate: any, callback: APICallback) => deleteCertificates(API, certificate, callback),
        import: (files: FileList, callback: APICallback) =>
            importCertificates(API, t('certificates_import_process_runs'), files, callback),

        // Update
        startUpdate: (type: UpdateType, callback: APICallback) =>
            API.postHandledErrorCodes('/certificates/update/' + type, 'Update start', {}, callback, [406]),
        stopUpdate: (callback: APICallback) =>
            API.postHandledErrorCodes('/certificates/update/cancel', 'Update cancel', {}, callback, [406]),
        getUpdateProcessMetrics: (callback: APICallback) => API.getBackground('/certificates/update/metrics', callback),

        // System integrity
        checkSystemIntegrity: (callback: APICallback) =>
            API.getBackground('/certificates/checkSystemIntegrityReport', callback),
        checkSystemIntegrityLogExistence: (callback: APICallback) =>
            API.getBackground('/certificates/checkSystemIntegrityLogExistance', callback),
        downloadSystemIntegrityLog: () => API.downloadFileFrom('/certificates/checkSystemIntegrityLog'),

        // Manual selection
        activateForUsecase: (id: string, usecase: UseCase) =>
            API.putBackground(`/certificates/activeForTesting/activate/${id}/${usecase}`, {}, {}),
        deactivateForUsecase: (id: string, usecase: UseCase) =>
            API.putBackground(`/certificates/activeForTesting/deactivate/${id}/${usecase}`, {}, {}),
        getActive: (callback: APICallback) => API.getBackground('/certificates/activeForTesting', callback),
        getActiveForUsecases: (type: string, callback: APICallback) =>
            API.getBackground(`/certificates/activeForTesting/usecases/${type}`, callback),
        getActiveEnhancedRightsCerts: (callback: APICallback) =>
            API.getBackground('/certificates/activeForTesting/enhanced', callback),
        getManualSelectionOptions: (type: string, filter: string, callback: APICallback) =>
            API.getBackground(
                `/certificates/activeForTesting/options/${type}?q=${filter}&limit=0&fieldValue=id&fieldText=description&value=`,
                callback
            ),
        activateForTesting: (id: string, callback?: APICallback) =>
            API.putBackground('/certificates/activeForTesting/activate/' + id, {}, callback),
        deactivateForTesting: (id: string, callback?: APICallback) =>
            API.putBackground('/certificates/activeForTesting/deactivate/' + id, {}, callback)
    };
};

function deleteCertificates(API: API, certificate: any, callback: APICallback) {
    let IDs: string[] = [];
    if (Array.isArray(certificate)) {
        for (var i = 0; i < certificate.length; i++) {
            addIDs(certificate[i], IDs);
        }
    } else {
        addIDs(certificate, IDs);
    }

    API.postBackground('/certificates/remove', IDs, callback);
}

function addIDs(certificate: CertificateData, IDs: string[]) {
    if (CertificateType.VIRTUAL_FOLDER === certificate.type && certificate.childrenIds) {
        // If folder is selected all childs need to be deleted
        certificate.childrenIds.forEach(function (childId) {
            IDs.push(childId);
        });
    } else {
        IDs.push(certificate.id);
    }
}

function importCertificates(API: API, message: string, files: FileList, callback: APICallback) {
    var formData = new FormData();
    if (files) {
        for (var i = 0; i < files.length; i++) {
            formData.append('files', files[i]);
        }
    }
    API.post('/certificates/import/files', message, formData, callback);
}
