import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import React from 'react';
import { StateActionType } from '../../state/StateAction';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

// Dispatch function to update global app state
var dispatchFunction: Function;

const ProgressDialog = () => {
    const [{ process }, dispatch] = useStateValue();
    dispatchFunction = dispatch;

    const { t } = useTranslation();

    return (
        <Dialog open={process && process.showProcessDialog} disableBackdropClick disableEscapeKeyDown>
            <DialogTitle className='blue'>{process.processName}</DialogTitle>
            <DialogContent className='progress-dialog-content'>
                <CircularProgress />
                <span className='progress-dialog-message'>{t('common_wait')}</span>
            </DialogContent>
        </Dialog>
    );
};
export default ProgressDialog;

// Shows the progress dialog with the passed action name
export function showProgressDialog(action: String) {
    dispatchFunction({
        type: StateActionType.UPDATE_PROCESS,
        data: {
            processName: action,
            showProcessDialog: true
        }
    });
}

// Hides the progress dialog and resets the action name
export function hideProgressDialog() {
    dispatchFunction({
        type: StateActionType.UPDATE_PROCESS,
        data: {
            processName: '',
            showProcessDialog: false
        }
    });
}
