// The useTranslation (react-i18next) has to be passed to this function
export function getCommonTranslation(t: Function): object {
    return {
        body: {
            emptyDataSourceMessage: t('common_table_hint_no_data'),
            filter: {
                filterTooltip: t('common_table_filter_tooltip')
            }
        },
        toolbar: {
            searchTooltip: t('common_table_search_tooltip'),
            searchPlaceholder: t('common_table_search_hint'),
            showColumnsTitle: t('common_table_tooltip_show_hide_columns'),
            addRemoveColumns: t('common_table_show_hide_columns')
        },
        pagination: {
            labelRowsSelect: t('common_table_pagination_rows'),
            labelDisplayedRows: t('common_table_pagination_displayed_rows'),
            nextTooltip: t('common_table_tooltip_pagination_next'),
            previousTooltip: t('common_table_tooltip_pagination_previous'),
            firstTooltip: t('common_table_tooltip_pagination_first'),
            lastTooltip: t('common_table_tooltip_pagination_last')
        }
    };
}

export const defaultTableHeaderStyle = {
    fontSize: '0.9rem',
    color: '#707070',
    backgroundColor: '#F2F2F2',
    padding: '5px 14px'
};
