import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import React from 'react';
import { StateActionType } from '../../state/StateAction';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

// Dispatch function to update global app state
var dispatchFunction: Function;

const MessageDialog = () => {
    const [{ message }, dispatch] = useStateValue();
    dispatchFunction = dispatch;

    const { t } = useTranslation();

    return (
        <Dialog open={0 !== message.content.length} disableBackdropClick disableEscapeKeyDown>
            <DialogTitle id='message-dialog-title' className={message.isError ? 'red' : 'blue'}>
                {message.isError ? t('common_error') : t('common_info')}
            </DialogTitle>
            <DialogContent id='message-dialog-content'>{message.content}</DialogContent>
            <DialogActions>
                <Button
                    id='message-dialog-cancel-btn'
                    onClick={message.postHandler ? message.postHandler : hideMessageDialog}
                    color='primary'
                    variant='outlined'
                >
                    {t('common_close')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
export default MessageDialog;

// Shows the message dialog with the passed content
export function showMessageDialog(messageContent: string, isErrorMessage: boolean) {
    dispatchFunction({
        type: StateActionType.UPDATE_MESSAGE,
        data: {
            content: messageContent,
            isError: isErrorMessage
        }
    });
}

// Hides the progress dialog and resets the action name
export function hideMessageDialog() {
    dispatchFunction({
        type: StateActionType.UPDATE_MESSAGE,
        data: {
            content: '',
            isError: false
        }
    });
}
