import { Button, Tooltip } from '@material-ui/core';
import React, { ChangeEvent, useState } from 'react';

import ImportCertificateComponent from './ImportCertificateComponent';
import ImportCertificatesResultDialog from './ImportCertificatesResultDialog';
import InputIcon from '@material-ui/icons/Input';
import { StateActionType } from '../../state/StateAction';
import { useCertificatesService } from '../CertificatesService';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

interface ImportCertificateProps {
    refreshCallBack: () => void;
}

export interface ImportResult {
    fileName: string;
    authorityKeyIdentifier: string;
    message: string;
    subjectKeyIdentifier: string;
    success: boolean;
}

const ImportCertificates = (props: ImportCertificateProps) => {
    const [{ certsUpdateProcess }, dispatch] = useStateValue();
    const [isDialogVisible, setDialogVisible] = useState<boolean>(false);
    const [messages, setMessages] = useState<any>();
    const [ref, setRef] = useState<any>();
    const certificatesService = useCertificatesService();
    const { t } = useTranslation();

    function handleFileSelection(event: ChangeEvent<HTMLInputElement>) {
        const files = event.target.files;
        if (files) {
            certificatesService.import(files, {
                success: response => {
                    setMessages(response.data);
                    setDialogVisible(true);
                    clearIntegrityCheck();
                },
                finally: () => {
                    props.refreshCallBack();
                }
            });
        }
    }

    function clearIntegrityCheck() {
        // Importing clear integrity check results
        dispatch({
            type: StateActionType.UPDATE_INTEGRITY_CHECK_EXISTENCE,
            data: false
        });
        // Importing clearing integrity check data.
        dispatch({
            type: StateActionType.UPDATE_INTEGRITY_CHECK_DATA,
            data: {
                integrityCheckErrorMap: []
            }
        });
    }

    return (
        <>
            {isDialogVisible && <ImportCertificatesResultDialog messages={messages} dialogVisible={setDialogVisible} />}
            <Tooltip title={certsUpdateProcess.running ? t('certificates_update_blocks_action').valueOf() : ''}>
                <span>
                    <ImportCertificateComponent onChangeCallback={handleFileSelection} refCallback={setRef} />
                    <Button
                        id='certificates-import-btn'
                        color='primary'
                        variant='outlined'
                        className='certificates-toolbar-button'
                        onClick={() => ref.click()}
                        disabled={certsUpdateProcess.running}
                    >
                        <InputIcon className='btn-icon-left' />
                        {t('certificates_import_btn')}
                    </Button>
                </span>
            </Tooltip>
        </>
    );
};

export default ImportCertificates;
