import MaterialTable, { MaterialTableProps } from '@material-table/core';
import React, { KeyboardEvent, MouseEvent, useEffect, useState } from 'react';

import CertificatesContextMenu from '../../certificates/CertificatesContextMenu';
import { DETAILS_PANEL_HEIGHT } from './DetailsPanel';
import DetailsPanel from './DetailsPanel';

/** Object to map context menu name to React component. */
const contextMenuComponents: any = {
    CertificatesContextMenu: CertificatesContextMenu
};

export interface SelectableTableProps {
    modelIdentifier: string;
    tableProps: MaterialTableProps<any>;
    contextMenuType?: string;

    renderDetails?: (row: any) => JSX.Element;
    tableRef?: (Ref: any) => void;
    updateSelection?: (item: any) => void;
    keyboardNavigation: boolean;
    deleteDataByKey?: (flag: boolean) => void;
}

const SelectableTable = (props: SelectableTableProps) => {
    const [table, setTable] = useState<any>({ dataManager: {} });
    const [selectedRow, setSelectedRow] = useState<any>(undefined);
    const [sortedData, setSortedData] = useState<any[]>([]);
    const [selectedPosition, setSelectedPosition] = useState<number>(-1);

    // the range between position of first selected certificate and position of last selected certificate
    // if the position of the first selected certificate is bigger than position of last selected certificate then the range is negative, otherwise is positive.
    // 0 for one selected certificate or none
    const [range, setRange] = useState<number>(0);

    // Context menu
    const [contextMenuX, setContextMenuX] = useState<number>(0);
    const [contextMenuY, setContextMenuY] = useState<number>(0);

    useEffect(() => {
        // Update current selection if data has been changed from the outside (CEBAS-1636)
        if (selectedRow) {
            if (Array.isArray(table.dataManager.data)) {
                let findRow = findRowFromOutside();
                if (Array.isArray(findRow) && findRow.length === 0) {
                    findRow = undefined;
                }
                updateSelectedRow(findRow ? findRow : undefined);
            }
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.modelIdentifier, table, table.dataManager, table.dataManager.data]);

    useEffect(() => {
        if (props.keyboardNavigation) {
            if (
                document.getElementsByClassName('MuiTable-root') &&
                document.getElementsByClassName('MuiTable-root')[0]
            ) {
                let scrollableDiv = document.getElementsByClassName('MuiTable-root')[0].parentElement;
                scrollableDiv && scrollableDiv.setAttribute('tabindex', '-1');
            }
            setSortedData(getSortedData(table.dataManager.sortedData));
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [table.dataManager.data]);

    function findRowFromOutside(): any {
        if (Array.isArray(selectedRow)) {
            return selectedRow.filter(item => checkRowByModelIdentifier(table.dataManager.data, item));
        }
        return checkRowByModelIdentifier(table.dataManager.data, selectedRow);
    }

    function updateSelectedRowUsingCTRL(row: any) {
        // eslint-disable-next-line eqeqeq
        if (selectedRow == undefined) {
            updateSelectedRow(row);
        } else if (selectedRow === row) {
            updateSelectedRow(null);
        } else {
            let rows: any[] = [];
            if (Array.isArray(selectedRow)) {
                rows.push(...selectedRow);
                if (rows.includes(row)) {
                    rows = rows.filter(item => item !== row);
                } else {
                    rows.push(row);
                }
            } else {
                rows.push(selectedRow);
                rows.push(row);
            }
            updateSelectedRow(rows.length === 1 ? rows[0] : rows);
        }
        updateSelectedPosition(row);
        setRange(0);
    }

    function updateSelectedRowUsingShift(row: any) {
        // eslint-disable-next-line eqeqeq
        if (selectedRow == undefined) {
            updateSelectedRow(row);
            setRange(0);
        } else if (selectedRow === row) {
            updateSelectedRow(null);
            setRange(0);
        } else {
            let rows: any[] = [];
            let secondRowPosition =
                props.keyboardNavigation && row !== selectedRow ? sortedData.findIndex(item => item === row) : -1;
            setRange(secondRowPosition - selectedPosition);

            rows.push(
                ...(selectedPosition < secondRowPosition
                    ? sortedData.slice(selectedPosition, secondRowPosition + 1)
                    : sortedData.slice(secondRowPosition, selectedPosition + 1))
            );
            updateSelectedRow(rows);
        }
    }

    function updateSelectedPosition(row: any) {
        setSelectedPosition(
            props.keyboardNavigation && row !== selectedRow ? sortedData.findIndex(item => item === row) : -1
        );
    }

    function updateSelectedRow(newRow: any) {
        newRow = Array.isArray(newRow) && newRow.length === 1 ? newRow[0] : newRow;
        setSelectedRow(newRow);
        if (props.updateSelection) {
            props.updateSelection(newRow);
        }

        expandParentRow(newRow);
    }

    function expandParentRow(row: any) {
        // eslint-disable-next-line eqeqeq
        if (row != undefined && !Array.isArray(row) && row.parentId !== null) {
            for (var i in table.dataManager.data) {
                if (table.dataManager.data[i].id === row.parentId) {
                    table.dataManager.data[i].tableData.isTreeExpanded = true;
                }
            }
        }
    }

    function determineMaxHeight(): any {
        if (props.renderDetails && props.tableProps.options) {
            var maxHeight;
            const bodyHeightProp = props.tableProps.options.maxBodyHeight;
            if (typeof bodyHeightProp === 'string') {
                maxHeight = +bodyHeightProp.substring(0, bodyHeightProp.length - 2);
            } else {
                maxHeight = bodyHeightProp ? bodyHeightProp : 0;
            }
            return selectedRow && !Array.isArray(selectedRow) ? maxHeight - DETAILS_PANEL_HEIGHT : maxHeight;
        } else if (props.tableProps.options) {
            return props.tableProps.options['maxBodyHeight'];
        } else {
            return undefined;
        }
    }

    function getTableRef(tableRef: any) {
        setTable(tableRef);
        if (props.tableRef) {
            props.tableRef(tableRef);
        }
    }

    function onRowClick(event: any, row: any) {
        if (event) {
            let mEvent: MouseEvent = event;

            if (mEvent.screenX === 0) {
                // screenX is 0 for clicks from context menu handler
                // eslint-disable-next-line eqeqeq
                if (selectedRow == undefined) {
                    updateSelectedRow(row);
                    updateSelectedPosition(row);
                    setRange(0);
                } else if (row === selectedRow) {
                    // already selected
                } else if (Array.isArray(selectedRow)) {
                    let rows: any[] = [];
                    rows.push(...selectedRow);
                    if (rows.includes(row)) {
                        // already selected
                    } else {
                        updateSelectedRow(row);
                        updateSelectedPosition(row);
                    }
                } else {
                    updateSelectedRow(row);
                    updateSelectedPosition(row);
                }
            } else if (mEvent.ctrlKey) {
                mEvent.preventDefault();
                updateSelectedRowUsingCTRL(row);
            } else if (mEvent.shiftKey) {
                mEvent.preventDefault();
                updateSelectedRowUsingShift(row);
            } else {
                if (row === selectedRow) {
                    updateSelectedRow(null);
                    setRange(0);
                } else {
                    updateSelectedRow(row);
                    updateSelectedPosition(row);
                    setRange(0);
                }
            }
        }
    }

    function openContextMenu(evt: any) {
        if (props.contextMenuType) {
            // Change selection manually on rightclick
            const element = document.elementFromPoint(evt.clientX, evt.clientY) as HTMLElement;
            if (isWithinTR(element)) {
                element.click();
                setContextMenuX(evt.clientX);
                setContextMenuY(evt.clientY);
                evt.preventDefault();
            } else {
                closeContextMenu();
            }
        }
    }

    function isWithinTR(element: HTMLElement | null): boolean {
        if (element) {
            if (element.nodeName === 'TR' && element.classList.contains('MuiTableRow-hover')) {
                return true;
            }
            return isWithinTR(element.parentElement);
        }
        return false;
    }

    /**
     * Closes the context menu by resetting it's position.
     */
    function closeContextMenu() {
        setContextMenuX(0);
        setContextMenuY(0);
    }

    /**
     * Returns a context menu component if props.contextMenuType is provided.
     * The component is defined in {@link contextMenuComponents}.
     */
    function addContextMenu() {
        if (props.contextMenuType) {
            return React.createElement(contextMenuComponents[props.contextMenuType], {
                selection: selectedRow,
                table: table,
                posX: contextMenuX,
                posY: contextMenuY,
                closeContextMenu: closeContextMenu
            });
        } else {
            return '';
        }
    }

    function handleKeyPress(event: KeyboardEvent) {
        let targetElement = event.target as HTMLElement;
        if (targetElement && targetElement.tagName.toUpperCase() === 'INPUT') {
            // if focus is on filters or on any input from table don't handle the pressed key
            return;
        }

        if (event.key === 'ArrowUp') {
            if (event.ctrlKey && event.shiftKey) {
                event.preventDefault();
                setRange(0 - selectedPosition);
                updateSelectedRow(sortedData.slice(0, selectedPosition + 1));
            } else if (event.shiftKey) {
                event.preventDefault();
                let rangePosition = selectedPosition + range - 1;
                if (rangePosition >= 0) {
                    setRange(range - 1);
                } else {
                    ++rangePosition;
                }
                if (rangePosition < selectedPosition) {
                    updateSelectedRow(sortedData.slice(rangePosition, selectedPosition + 1));
                } else {
                    updateSelectedRow(sortedData.slice(selectedPosition, rangePosition + 1));
                }
            } else if (selectedPosition >= 1 || event.shiftKey) {
                updateSelectedRow(sortedData[selectedPosition - 1]);
                setSelectedPosition(selectedPosition - 1);
                setRange(0);
            } else if (Array.isArray(selectedRow)) {
                updateSelectedRow(sortedData[selectedPosition]);
                setRange(0);
            }
        }

        if (event.key === 'ArrowDown') {
            if (event.ctrlKey && event.shiftKey) {
                event.preventDefault();
                setRange(sortedData.length - selectedPosition - 1);
                updateSelectedRow(sortedData.slice(selectedPosition));
            } else if (event.shiftKey) {
                event.preventDefault();
                let rangePosition = selectedPosition + range + 1;
                if (rangePosition < sortedData.length) {
                    setRange(range + 1);
                } else {
                    --rangePosition;
                }
                if (rangePosition < selectedPosition) {
                    updateSelectedRow(sortedData.slice(rangePosition, selectedPosition + 1));
                } else {
                    updateSelectedRow(sortedData.slice(selectedPosition, rangePosition + 1));
                }
            } else if (selectedPosition + 1 < sortedData.length) {
                updateSelectedRow(sortedData[selectedPosition + 1]);
                setSelectedPosition(selectedPosition + 1);
                setRange(0);
            } else if (Array.isArray(selectedRow)) {
                updateSelectedRow(sortedData[selectedPosition]);
                setRange(0);
            }
        }

        if (event.ctrlKey && event.key.toUpperCase() === 'a'.toUpperCase()) {
            event.preventDefault();
            updateSelectedRow(sortedData);
            setSelectedPosition(-1);
            setRange(0);
        }

        // eslint-disable-next-line eqeqeq
        if (event.key === 'Delete' && selectedRow != undefined) {
            event.preventDefault();
            if (selectedRow || (Array.isArray(selectedRow) && selectedRow.length > 0)) {
                props.deleteDataByKey && props.deleteDataByKey(true);
            }
        }
    }

    function getSortedData(data: any[]): any[] {
        if (data === undefined) {
            return [];
        }

        let sortedData: any[] = [];
        for (let i = 0; i < data.length; i++) {
            sortedData.push(data[i]);
            let dataChildren = data[i].tableData.childRows;
            if (dataChildren != null) {
                sortedData.push(...getSortedData(dataChildren));
            }
        }

        return sortedData;
    }

    function renderOnKeyDown() {
        var opts: any = {};
        if (props.keyboardNavigation) {
            opts['onKeyDown'] = handleKeyPress;
            opts['tabIndex'] = -1;
            return opts;
        }
        return false;
    }

    function existsRowData(rowData: any): boolean {
        let exists = Array.isArray(selectedRow)
            ? checkRowByModelIdentifier(selectedRow, rowData)
            : selectedRow && selectedRow === rowData;

        return exists;
    }

    function checkRowByModelIdentifier(arrayToFind: any[], item: any): boolean {
        return arrayToFind.find((element: any) =>
            item ? item[props.modelIdentifier] === element[props.modelIdentifier] : false
        );
    }

    return (
        <div id='selectable-table' {...renderOnKeyDown()} onContextMenu={openContextMenu}>
            {addContextMenu()}
            <MaterialTable
                {...props.tableProps}
                tableRef={getTableRef}
                onRowClick={(event, selectedRow) => onRowClick(event, selectedRow)}
                options={{
                    ...props.tableProps.options,
                    maxBodyHeight: determineMaxHeight(),
                    overflowY: 'inherit',
                    rowStyle: rowData => ({
                        backgroundColor: existsRowData(rowData) ? '#EEEEEE' : '#FFFFFF',
                        height: '40px'
                    })
                }}
            />
            {props.renderDetails && !Array.isArray(selectedRow) && (
                <DetailsPanel currentSelection={selectedRow} renderDetails={props.renderDetails} />
            )}
        </div>
    );
};

export default SelectableTable;
