import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core';

import React from 'react';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

interface DialogProps {
    userName: string;
    isDeletionConfirmed: (isConfirmed: boolean) => void;
}

const ConfirmDeletionDialog = (props: DialogProps) => {
    const [{ user }] = useStateValue();
    const { t } = useTranslation();

    function cancel() {
        props.isDeletionConfirmed(false);
    }

    function submit() {
        props.isDeletionConfirmed(true);
    }

    return (
        <Dialog open={true}>
            <DialogTitle id='confirm-dialog-title'>{t('configurations_accounts_delete_confirm_title')}</DialogTitle>
            <DialogContent id='confirm-dialog-message'>
                {t(
                    user.userName === props.userName
                        ? 'configurations_accounts_delete_confirm_desc_own'
                        : 'configurations_accounts_delete_confirm_desc',
                    { user: props.userName }
                )}
            </DialogContent>
            <DialogActions>
                <Button id='confirm-dialog-cancel-btn' onClick={cancel} color='primary'>
                    {t('common_cancel')}
                </Button>
                <Button id='confirm-dialog-submit-btn' onClick={submit} color='primary' variant='contained'>
                    {t('configurations_accounts_delete_confirm_submit')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default ConfirmDeletionDialog;
