import React, { useEffect } from 'react';

import RolePriorityComponent from './rolepriority/RolePriorityComponent';
import SwitchConfigComponent from './switch/SwitchConfigComponent';
import AutoLogoutSwitchConfigComponent from './autologout/AutologoutSwitchConfigComponent';
import SwitchConfigProps from './switch/SwitchConfigProps';
import { USER_CONFIG } from './store/ConfigKeys';
import { useStateValue } from '../state/State';
import { useTranslation } from 'react-i18next';
import { useUserConfigService } from './store/UserConfigStore';

var translate: Function;

const UserConfigComponent = () => {
    const [{ config, lastUpdateTime, user }, dispatch] = useStateValue();
    const userConfigService = useUserConfigService();
    const { t } = useTranslation();
    translate = t;

    useEffect(() => {
        userConfigService.refresh();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [t, dispatch, lastUpdateTime, user.isAuthenticated]);

    return (
        <div className='user-configurations'>
            <div className='configurations-heading'>{t('configurations_user_heading')}</div>
            {createImportValidationWidget()}
            {createAutoDeleteWidget()}
            {createAutoSelectionWidget()}
            {config[USER_CONFIG.AUTO_CERT_UPDATE] && user.isAuthenticated && createAutoStartUpSelectionWidget()}
            {config[USER_CONFIG.AUTOLOGOUT] && user.isAuthenticated && createAutologoutWidget()}
            {config[USER_CONFIG.CERT_SELECTION].configValue === 'automatic' ? <RolePriorityComponent /> : ''}
        </div>
    );
};

function createAutoSelectionWidget() {
    const valuesMap = new Map();
    valuesMap.set(true, 'automatic');
    valuesMap.set(false, 'manual');

    const props: SwitchConfigProps = {
        title: translate('configurations_auto_cert_selection_title'),
        description: translate('configurations_auto_cert_selection_desc'),
        configKey: USER_CONFIG.CERT_SELECTION,
        dialogEnabled: false,
        values: valuesMap
    };
    return <SwitchConfigComponent {...props} />;
}

function createAutoStartUpSelectionWidget() {
    const valuesMap = new Map();
    valuesMap.set(true, 'true');
    valuesMap.set(false, 'false');
    const props: SwitchConfigProps = {
        title: translate('configurations_auto_cert_update_title'),
        description: translate('configurations_auto_cert_update_desc'),
        configKey: USER_CONFIG.AUTO_CERT_UPDATE,
        dialogEnabled: false,
        values: valuesMap
    };
    return <SwitchConfigComponent {...props} />;
}

function createImportValidationWidget() {
    const props: SwitchConfigProps = {
        title: translate('configurations_extended_validation_title'),
        description: translate('configurations_extended_validation_desc'),
        dialogEnabled: true,
        configKey: USER_CONFIG.VALIDATE_CERTS
    };
    return <SwitchConfigComponent {...props} />;
}

function createAutoDeleteWidget() {
    const props: SwitchConfigProps = {
        title: translate('configurations_delete_certs_title'),
        description: translate('configurations_delete_certs_desc'),
        dialogEnabled: false,
        configKey: USER_CONFIG.DELETE_EXPIRED_CERTS
    };
    return <SwitchConfigComponent {...props} />;
}

function createAutologoutWidget() {
    const props: SwitchConfigProps = {
        title: translate('configurations_autologout_title'),
        description: translate('configurations_autologout_desc'),
        dialogEnabled: false,
        configKey: USER_CONFIG.AUTOLOGOUT
    };
    return <AutoLogoutSwitchConfigComponent {...props} />;
}

export default UserConfigComponent;
