import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core';

import React from 'react';
import { useTranslation } from 'react-i18next';

interface UpdateCanceledDialogProps {
    closeDialog: () => void;
}

const UpdateCanceledDialog = (props: UpdateCanceledDialogProps) => {
    const { t } = useTranslation();

    function close(): void {
        props.closeDialog();
    }

    return (
        <Dialog open={true}>
            <DialogTitle id='update-canceled-dialog-title' className='blue'>
                {t('common_info')}
            </DialogTitle>
            <DialogContent id='update-canceled-dialog-content'>
                {t('certificates_update_canceled_dialog_message')}
            </DialogContent>
            <DialogActions>
                <Button id='update-canceled-dialog-close-btn' onClick={close} color='primary' variant='outlined'>
                    {t('common_close')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default UpdateCanceledDialog;
