import React, { useState } from 'react';

import ConfirmAutologoutDialog from './ConfirmAutologoutDialog';
import { StateActionType } from '../../state/StateAction';
import Switch from '@material-ui/core/Switch';
import SwitchConfigProps from '../switch/SwitchConfigProps';
import { useAccountService } from '../../account/AccountService';
import { useStateValue } from '../../state/State';
import { useUserConfigService } from '../store/UserConfigStore';

const AutologoutSwitchConfigComponent = (props: SwitchConfigProps) => {
    // App state
    const [{ config }, dispatch] = useStateValue();
    const appConfig = config[props.configKey];

    const userConfigService = useUserConfigService();
    const accountService = useAccountService();

    const [showConfirmAutologoutDialog, setShowConfirmAutologoutDialog] = useState<boolean>(false);
    var isChecked = (props.values ? props.values.get(true) : 'true') === appConfig.configValue;

    function updateConfig(event: object, checked: boolean) {
        let value = getParsedValue(checked);
        if (value === 'false') {
            setShowConfirmAutologoutDialog(true);
        } else {
            appConfig.configValue = 'true';
            userConfigService.updateAutologoutConfig(appConfig);
            dispatch({
                type: StateActionType.UPDATE_CONFIG,
                data: config
            });
            accountService.refreshUserSession();
        }
    }

    function closeAutologoutDialog(confirmed: boolean) {
        if (confirmed) {
            appConfig.configValue = 'false';
            userConfigService.updateAutologoutConfig(appConfig);
            dispatch({
                type: StateActionType.UPDATE_CONFIG,
                data: config
            });
        }
        setShowConfirmAutologoutDialog(false);
    }

    function getParsedValue(checked: boolean) {
        if (props.values) {
            return props.values.get(checked);
        } else {
            return Boolean(checked).toString();
        }
    }

    return (
        <div className='switch-config-component'>
            {showConfirmAutologoutDialog && <ConfirmAutologoutDialog closeDialog={closeAutologoutDialog} />}
            <Switch
                id={'switch-' + appConfig.configKey.toLowerCase()}
                className='config-switch'
                checked={isChecked}
                onChange={updateConfig}
            />
            <span id={'switch-heading-' + appConfig.configKey.toLowerCase()} className='config-heading'>
                {props.title}
            </span>
            <div id={'switch-description-' + appConfig.configKey.toLowerCase()} className='config-description'>
                {props.description}
            </div>
        </div>
    );
};

export default AutologoutSwitchConfigComponent;
