type mapType = Record<string, string>;
const updateSteps: mapType = {
    RETRIEVE_BACKEND_IDENTIFIERS: 'certificates_update_step_retrieve_backend_ids',
    RETRIEVE_BACKENDS: 'certificates_update_step_retrieve_backends',
    UPDATE_NON_VSM_CERTIFICATES: 'certificates_update_step_retrieve_non_vsm_ecu_certificates',
    RETRIEVE_PERMISSIONS: 'certificates_update_step_retrieve_permissions',
    CREATE_CSRS: 'certificates_update_step_create_csrs',
    CREATE_ENHANCED_CSRS: 'certificates_update_step_create_enhanced_rights_csrs',
    DOWNLOAD_DIAGNOSTIC_CERTIFICATES: 'certificates_update_step_download_diagnostic_certs',
    DOWNLOAD_ENHANCED_CERTIFICATES: 'certificates_update_step_download_enhanced_certs',
    DOWNLOAD_OTHER_CERTIFICATES: 'certificates_update_step_download_other_certs',
    DOWNLOAD_LINK_CERTIFICATES: 'certificates_update_step_download_link_certs',
    COLLECTING_TIME_AND_SECOCIS_CSRS_NOT_CREATED_BASED_ON_PERMISSIONS:
        'certificates_update_step_time_secois_no_permissions',
    DOWNLOAD_CERTIFICATES: 'certificates_update_step_download_certificates',
    UPDATE_BACKENDS: 'certificates_update_step_backends',
    updateRequestingRemainingCert: 'certificates_update_step_requesting_other_certs',
    updateStopCreateEnhancedCSR: 'certificates_update_step_enhanced_rights_csr_created',
    updateStartDownloadEnhancedCSR: 'certificates_update_step_enhanced_rights_download_start',
    updateStopDownloadEnhancedRightsCert: 'certificates_update_step_enhanced_rights_download_stop',
    updateStartDownloadLinkCertificates: 'certificates_update_step_link_download_start',
    updateStopDownloadLinkCertificates: 'certificates_update_step_link_download_stop',
    updateRequestBackendIdentifiers: 'certificates_update_step_request_backend_ids',
    startNonVsmCertificatesDownload: 'certificates_update_step_start_downloading_non_vsm_ecu_certificates',
    stopNonVsmCertificatesDownload: 'certificates_update_step_stop_downloading_non_vsm_ecu_certificates',
    updateReceiveBackendIdentifiers: 'certificates_update_step_download_backend_ids',
    updateRequestingBackendCertChain: 'certificates_update_step_request_backends',
    updateDownloadRootBackendStop: 'certificates_update_step_download_roots_backends_stop',
    updateStartDownloadUserPermissions: 'certificates_update_step_download_permissions_start',
    updateStopDownloadUserPermissions: 'certificates_update_step_download_permissions_stop',
    updateReceiveDiagnosticCert: 'certificates_update_step_receive_diagnostic_certs',
    updateStartCreateEnhancedCSR: 'certificates_update_step_create_enhanced_rights_csrs_start',
    collectingTimeAndSecocisCSRsNotCreatedBasedOnPermissionsStep: 'certificates_update_step_time_secois_no_permissions',
    updateRequestingDiagnosticCert: 'certificates_update_step_request_diagnostic_certs',
    updateStartCreateCSRsProcess: 'certificates_update_step_create_csrs_start',
    updateStopCreateCSRsProcess: 'certificates_update_step_create_csrs_stop',
    updateStartDownloadCertificates: 'certificates_update_step_download_certificates_start',
    updateStopDownloadCertificates: 'certificates_update_step_download_certificates_stop',
    updateStartUpdateBackendIdentifiers: 'certificates_update_step_start_update_backend_identifiers',
    updateStopUpdateBackendIdentifiers: 'certificates_update_step_stop_update_backend_identifiers'
};

export function getTranslationForStep(stepAsString: string): string {
    if (stepAsString === undefined) {
        return '';
    }
    let translated = updateSteps[stepAsString];
    if (translated === undefined) {
        console.log('No translation for step: ' + stepAsString);
    }
    return translated;
}
