/* eslint-disable @typescript-eslint/no-unused-vars */

import { API, APICallback, useAPI } from '../../common/API';

import { CertificateData } from '../../certificates/CertificateData';
import { Column } from '@material-table/core';
import { ConfigData } from '../../state/models/ConfigData';
import { StateActionType } from '../../state/StateAction';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

export interface UserConfigService {
    refresh: () => Promise<void>;
    refreshBackground: () => Promise<void>;
    update: (config: ConfigData) => Promise<void>;
    updateAutologoutConfig: (config: ConfigData) => void;
    getPageSize: (callback: APICallback) => Promise<void>;
    setPageSize: (pageSize: number) => Promise<void>;
    getCertsColumnVisibility: (callback: APICallback) => Promise<void>;
    getCertsColumnOrder: (callback: APICallback) => Promise<void>;
    setCertsColumnVisibility: (certificatesTable: any) => void;
    setCertsColumnOrder: (table: any, oldPos: number, newPos: number, callback: APICallback) => void;
}

export const useUserConfigService = (): UserConfigService => {
    const [, dispatch] = useStateValue();
    const { t } = useTranslation();
    const API = useAPI();

    return {
        refresh: () =>
            API.getNoProgress('/configurations', t('configurations_refresh_process'), {
                success: response => {
                    const parsedData = getConfigFrom(response.data);
                    dispatch({
                        type: StateActionType.UPDATE_CONFIG,
                        data: parsedData
                    });
                }
            }),
        refreshBackground: () =>
            API.getBackground('/configurations', {
                success: response => {
                    const parsedData = getConfigFrom(response.data);
                    dispatch({
                        type: StateActionType.UPDATE_CONFIG,
                        data: parsedData
                    });
                }
            }),
        update: (config: ConfigData) => API.putBackground('/configurations/' + config.id, config, {}),
        updateAutologoutConfig: (config: ConfigData) => updateAutologoutConfig(API, config),
        getPageSize: (callback: APICallback) => API.getBackground('/configurations/maxRowsPerPage', callback),
        setPageSize: (pageSize: number) =>
            API.postBackground('/configurations/maxRowsPerPage', pageSize, {}, { 'Content-Type': 'application/xml' }),
        getCertsColumnVisibility: (callback: APICallback) =>
            API.getBackground('/configurations/certificatesColumnVisibility', callback),
        getCertsColumnOrder: (callback: APICallback) =>
            API.getBackground('/configurations/certificatesColumnOrder', callback),
        setCertsColumnVisibility: (table: any) => setCertsColumnVisibility(API, table),
        setCertsColumnOrder: (table: any, oldPos: number, newPos: number, callback: APICallback) =>
            setCertsColumnOrder(API, callback, table, oldPos, newPos)
    };
};

function getConfigFrom(input: Array<ConfigData>): Object {
    var output: any = {};
    input.forEach(item => {
        output[item.configKey] = {
            id: item.id,
            configKey: item.configKey,
            configValue: item.configValue
        };
    });
    return output;
}

const updateAutologoutConfig = (API: API, config: ConfigData) => {
    let enablement = {
        enable: config.configValue === 'true'
    };
    API.putBackground('/configurations/autologout', enablement, {});
};

function setCertsColumnVisibility(API: API, certificatesTable: any) {
    var visibility: any = {};
    const columns = certificatesTable.dataManager.columns;
    columns.forEach((column: Column<CertificateData>) => {
        if (column.field) {
            visibility[column.field] = !column.hidden;
        }
    });
    API.postBackground('/configurations/certificatesColumnVisibility', { message: JSON.stringify(visibility) }, {});
}

function setCertsColumnOrder(API: API, callback: APICallback, table: any, oldPos: number, newPos: number) {
    let columns = table.dataManager.columns;

    // get all columns which are not hidden, for the moment drag and drop work only for columns shown in table
    // this is also the index in UI, not the real index from table.dataManager.columns
    let notHiddenColumns = columns.filter((column: Column<CertificateData>) => {
        return !column.hidden;
    });
    let movedColumn = notHiddenColumns[oldPos].field;
    let destinationColumn = notHiddenColumns[newPos].field;

    // get the real index by column name, index from table.dataManager.columns
    let realSourceIndex = columns.findIndex((column: Column<CertificateData>) => column.field === movedColumn);
    let realDestinationIndex = columns.findIndex(
        (column: Column<CertificateData>) => column.field === destinationColumn
    );

    // shift the columns
    columns.splice(realDestinationIndex, 0, columns.splice(realSourceIndex, 1)[0]);
    const columnOrder = columns.map((column: Column<CertificateData>) => column.field);

    API.postBackground('/configurations/certificatesColumnOrder', { message: columnOrder.join() }, callback);
}
