import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core';

import React from 'react';
import { useTranslation } from 'react-i18next';

interface DialogProps {
    closeCallback: (isConfirmed: boolean) => void;
}

const ConfirmRecoveryDialog = (props: DialogProps) => {
    const { t } = useTranslation();

    function cancel() {
        props.closeCallback(false);
    }

    function submit() {
        props.closeCallback(true);
    }

    return (
        <Dialog id='dialog-password-recovery' open={true}>
            <DialogTitle id='dialog-password-recovery-title' className='blue'>
                {t('dialog_forgot_password_title')}
            </DialogTitle>
            <DialogContent id='dialog-password-recovery-message'>{t('dialog_forgot_password_content')}</DialogContent>
            <DialogActions>
                <Button id='dialog-password-recovery-cancel-btn' onClick={cancel} color='primary'>
                    {t('common_cancel')}
                </Button>
                <Button id='dialog-password-recovery-submit-btn' onClick={submit} color='primary' variant='contained'>
                    {t('dialog_forgot_password_submit_btn')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default ConfirmRecoveryDialog;
