import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core';

import React from 'react';
import { useTranslation } from 'react-i18next';

interface AutologoutDialogProps {
    closeDialog: (confirmed: boolean) => void;
}

const ConfirmAutologoutDialog = (props: AutologoutDialogProps) => {
    const { t } = useTranslation();

    function cancel() {
        props.closeDialog(false);
    }

    function submit() {
        props.closeDialog(true);
    }

    return (
        <Dialog open={true}>
            <DialogTitle id='confirm-autologout-dialog-title'>
                {t('configurations_autologout_confirm_title')}
            </DialogTitle>
            <DialogContent id='confirm-autologout-dialog-message'>
                {t('configurations_autologout_warning_deactivating')}
                <ul>
                    <li>{t('configurations_autologout_warning_no_physical_access')}</li>
                    <li>{t('configurations_autologout_warning_no_unlimited_access')}</li>
                    <li>{t('configurations_autologout_warning_no_malicious_software')}</li>
                    <li>{t('configurations_autologout_warning_terms_conditions')}</li>
                </ul>
                {t('configurations_autologout_warning_for_task')}
            </DialogContent>
            <DialogActions>
                <Button id='confirm-autologout-dialog-cancel-btn' onClick={cancel} color='primary'>
                    {t('common_cancel')}
                </Button>
                <Button id='confirm-autologout-dialog-submit-btn' onClick={submit} color='primary' variant='contained'>
                    OK
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default ConfirmAutologoutDialog;
