import React, { useState } from 'react';

import ActiveEnhancedRightsTable from './ActiveEnhancedRightsTable';
import CertSelectEnhancedRights from '../selection/CertSelectEnhancedRights';
import { CertType } from '../selection/CertType';
import { useSelectionUpdate } from '../selection/SelectionUpdate';
import { useTranslation } from 'react-i18next';

interface EnhancedRightsSelectionProps {
    activeCertificates: string[];
    shouldLoadOnStartUp: boolean;
}

const EnhancedRightsSelection = (props: EnhancedRightsSelectionProps) => {
    const [updateKey, setUpdateKey] = useState<number>(0);
    const [reloadOptions, setReloadOptions] = useState<boolean>(false);
    const [updatenumber, setUpdateNumber] = useState<number>(0);
    const [table, setTable] = useState<any>();
    const selectionUpdate = useSelectionUpdate();
    const { t } = useTranslation();

    function certificateSelected(id: string) {
        selectionUpdate.activateCert(id, { success: response => refresh() });
    }

    /**
     * Refreshes both child components.
     */
    function refresh() {
        if (table) {
            table.onQueryChange();
        }
        setUpdateKey(updateKey + 1);
        setReloadOptions(true);
        setUpdateNumber(updatenumber + 1);
    }

    function getIdentifiersForEnhancedRightsCertificate(): Map<string, string> {
        let map = new Map();

        map.set(t('certificates_issuer_title'), 'issuer')
            .set(t('certificates_holder_title'), 'baseCertificateID')
            .set(t('certificates_services_title'), 'services')
            .set(t('certificates_target_ecu_title'), 'targetECU')
            .set(t('certificates_target_vin_title'), 'targetVIN')
            .set(t('certificates_valid_to_title'), 'validToDateTime');

        return map;
    }

    return (
        <div className='manual-selection-dialog-tab-content' id='enhanced-rights-content'>
            <div style={{ zIndex: 20 }}>
                <CertSelectEnhancedRights
                    type={CertType.EnhancedRights}
                    title='certificates_type_enhanced_rights'
                    activate={certificateSelected}
                    currentlyActiveCerts={props.activeCertificates}
                    updateKey={updateKey}
                    modelidentifiers={getIdentifiersForEnhancedRightsCertificate()}
                    shouldLoadOnStartUp={props.shouldLoadOnStartUp}
                    doUpdate={reloadOptions}
                    updateNumber={updatenumber}
                />
            </div>
            <ActiveEnhancedRightsTable setTable={setTable} refreshView={refresh} />
        </div>
    );
};

export default EnhancedRightsSelection;
