/* eslint-disable @typescript-eslint/no-unused-vars */

import { API, APICallback, useAPI } from '../common/API';

import { AxiosResponse } from 'axios';
import SessionTimeSingleton from '../state/SessionTimeSingleton';
import { StateActionType } from '../state/StateAction';
import { UpdateType } from '../certificates/update/UpdateCertificates';
import { getUserData } from '../state/models/UserData';
import { useStateValue } from '../state/State';
import { useTranslation } from 'react-i18next';

export interface AccountService {
    login: (username: string, password: string, callback: APICallback) => void;
    onlineLogin: (updateType?: UpdateType) => void;
    register: (username: string, password: string, callback: APICallback) => void;
    deleteUserByUsername: (username: string, callback: APICallback) => void;
    xentryLogout: () => void;
    getUserData: (callback: APICallback) => Promise<void>;
    getLoginErrors: (callback: APICallback) => Promise<void>;
    logoutFromPki: () => Promise<void>;
    refreshUserSession: () => void;
}

const triggerOnline = (API: API, action: string, username: string, password: string, callback: APICallback) => {
    const base64Pw = new Buffer(password).toString('base64');
    API.postHandledErrorCodes('/users/login', action, { userName: username, userPassword: base64Pw }, callback, [406]);
};

const triggerOnlineLogin = (updateType?: UpdateType) => {
    let type: string = updateType === UpdateType.FULL ? 'fullUpdate=true' : 'differentialUpdate=true';

    // ZZ server forwards to Daimler login. Proxy setting doesn't handle forwarding so it need's to be done manually
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
        // dev code
        window.location.assign('https://localhost:61000/users/login/oauth?' + type);
    } else {
        // production code
        window.location.assign(window.location.protocol + '//' + window.location.host + '/users/login/oauth?' + type);
    }
};

const triggerXentryLogout = (API: API, action: string, dispatchFunction: Function) => {
    API.post(
        '/users/logout/oauth',
        action,
        {},
        {
            success: response => {
                const userData = getUserData('', '', false, false, false, false, false, 0);
                dispatchFunction({
                    type: StateActionType.UPDATE_USER,
                    data: userData
                });
                triggerUpdate(dispatchFunction);
            }
        }
    );
};

const triggerRegistration = (API: API, action: string, username: string, password: string, callback: APICallback) => {
    const base64Pw = new Buffer(password).toString('base64');
    const requestData = { userName: username, userPassword: base64Pw };
    API.post('/users/register/oauth', action, requestData, callback);
};

const triggerSessionRefresh = (API: API, action: string) => {
    API.postBackground('/users/currentUser/timer/reset', action, {
        success: response => SessionTimeSingleton.getInstance().notifyReset(response.data.remainingSessionSeconds)
    });
};

export const useAccountService = (): AccountService => {
    const [, dispatch] = useStateValue();
    const { t } = useTranslation();
    const API = useAPI();

    return {
        login: (username: string, password: string, callback: APICallback) =>
            triggerOnline(API, t('login_process'), username, password, callback),
        onlineLogin: (updateType?: UpdateType) => triggerOnlineLogin(updateType),
        register: (username: string, password: string, callback: APICallback) =>
            triggerRegistration(API, t('register_process'), username, password, callback),
        xentryLogout: () => triggerXentryLogout(API, t('logout_process'), dispatch),
        deleteUserByUsername: (username: string, callback: APICallback) =>
            API.deleteHandledErrorCodes('/users?username=' + username, callback, [404]),
        getUserData: (callback: APICallback) => API.get('/users/currentUser', t('user_data_update_process'), callback),
        getLoginErrors: (callback: APICallback) => API.getBackground('/users/login/errors', callback),
        logoutFromPki: () => API.getBackground('/configurations/pkiLogoutUrl', {}),
        refreshUserSession: () => triggerSessionRefresh(API, t('user_session_refresh'))
    };
};

function triggerUpdate(dispatchFunction: Function) {
    dispatchFunction({ type: StateActionType.TRIGGER_UPDATE, data: new Date() });
}
