import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core';

import React from 'react';
import { useTranslation } from 'react-i18next';

interface IntegrityCheckResultProps {
    totalNumberCerts: number;
    failedChecks: number;
    closeDialog: (event: any) => void;
}

const IntegrityCheckResultDialog = (props: IntegrityCheckResultProps) => {
    const { t } = useTranslation();
    return (
        <Dialog open={true}>
            <DialogTitle id='integrity-check-dialog-title' className='blue'>
                {t('certificates_check_integrity_dialog_title')}
            </DialogTitle>
            <DialogContent id='integrity-check-dialog-content'>
                {t('certificates_check_integrity_dialog_content', {
                    total: props.totalNumberCerts,
                    invalid: props.failedChecks,
                    valid: props.totalNumberCerts - props.failedChecks
                })}
            </DialogContent>
            <DialogActions>
                <Button
                    id='integrity-check-dialog-close-btn'
                    onClick={props.closeDialog}
                    color='primary'
                    variant='outlined'
                >
                    {t('common_close')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default IntegrityCheckResultDialog;
