import React, { useEffect, useState } from 'react';

import { useAPI } from '../common/API';
import { useTranslation } from 'react-i18next';

const Version = React.memo(function Version() {
    const { t } = useTranslation();
    const [version, setVersion] = useState(t('common_placeholder_loading')) as any;

    const API = useAPI();

    const fetchVersionAndUpdateState = async (): Promise<void> => {
        API.getBackground('/configurations/applicationVersion', {
            success: response => setVersion(response.data)
        });
    };

    // Fetch data once after mounting
    useEffect(() => {
        fetchVersionAndUpdateState();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <div style={{ display: 'inline' }}>
            <span id='btn-version' className='app-version'>
                {version}
            </span>
        </div>
    );
});

export default Version;
