import { CertificateData, CertificateType } from './CertificateData';
import React, { useEffect, useState } from 'react';

import { Certificate } from 'tls';
import { Column } from '@material-table/core';
import { IntegrityCheckDataHolder } from '../state/models/IntegrityCheckDataHolder';
import IntegrityCheckInfo from './integrity/IntegrityCheckInfo';
import RangeDateTimeFilter from '../common/table/filter/RangeDateTimeFilter';
import StarIcon from '@material-ui/icons/Star';
import { USER_CONFIG } from '../configurations/store/ConfigKeys';
import { formatTimestamp } from '../common/DateFormat';
import { useStateValue } from '../state/State';
import { useTranslation } from 'react-i18next';
import { useUserConfigService } from '../configurations/store/UserConfigStore';

export enum CertColumn {
    Subject = 'subject',
    PkiRole = 'pkirole',
    UserRole = 'userRole',
    Issuer = 'issuer',
    SerialNo = 'serialNo',
    TargetEcu = 'targetECU',
    TargetVin = 'targetVIN',
    Status = 'status',
    ValidTo = 'validTo',
    ValidFrom = 'validFrom',
    Validity = 'validityStrengthColor',
    UniqueEcuId = 'uniqueECUID',
    SpecialEcu = 'specialECU',
    SubjectPubKey = 'subjectPublicKey',
    BaseCertId = 'baseCertificateID',
    IssuerSerialNo = 'issuerSerialNumber',
    Skid = 'subjectKeyIdentifier',
    Akid = 'authorityKeyIdentifier',
    Services = 'services',
    Nonce = 'nonce',
    Signature = 'signature',
    TargetSkid = 'targetSubjectKeyIdentifier',
    ZkNumber = 'zkNo',
    EcuPackageTs = 'ecuPackageTs',
    LinkCertTs = 'linkCertTs',
    PkiKnown = 'pkiKnown',
    PkiState = 'pkiState'
}

interface CertificateColumnsProps {
    isColumnShifted: boolean;
    setIsColumnShifted: (columnShifted: boolean) => void;
}

export const useColumns = (props: CertificateColumnsProps): Column<CertificateData>[] => {
    const [{ config, lastUpdateTime, integrityCheckData }] = useStateValue();
    const { t, i18n } = useTranslation();
    const userConfigService = useUserConfigService();

    const columnTitleIds: string[] = [
        'certificates_subject_title',
        'certificates_pki_role_title',
        'certificates_user_role_title',
        'certificates_issuer_title',
        'certificates_serial_no_title',
        'certificates_target_ecu_title',
        'certificates_target_vin_title',
        'certificates_status_title',
        'certificates_valid_to_title',
        'certificates_valid_from_title',
        'certificates_validity_title',
        'certificates_unique_ecu_id_title',
        'certificates_special_ecu_title',
        'certificates_subject_public_key_title',
        'certificates_base_certificate_id_title',
        'certificates_issuer_serial_no_title',
        'certificates_skid_title',
        'certificates_akid_title',
        'certificates_services_title',
        'certificates_nonce_title',
        'certificates_signature_title',
        'certificates_target_skid_title',
        'certificates_zk_no_title',
        'certificates_ecu_package_ts_title',
        'certificates_link_cert_ts_title',
        'certificates_pki_known_title',
        'certificates_pki_state_title'
    ];

    const [columns, setColumns] = useState<Column<any>[]>([
        {
            // SUBJECT
            title: t(columnTitleIds[0]),
            field: CertColumn.Subject,
            hidden: false,
            render: (row: CertificateData) => renderSubjectWithIcon(row, integrityCheckData),
            width: 'unset'
        },
        {
            // PKI ROLE
            title: t(columnTitleIds[1]),
            field: CertColumn.PkiRole,
            hidden: false,
            cellStyle: { minWidth: '140px' },
            render: (row: CertificateData) => cropText(row.pkirole, 20),
            width: 'unset'
        },
        {
            // USER ROLE
            title: t(columnTitleIds[2]),
            field: CertColumn.UserRole,
            hidden: false,
            cellStyle: { minWidth: '115px' },
            render: (row: CertificateData) => cropText(row.userRole, 15),
            width: 'unset'
        },
        {
            // ISSUER
            title: t(columnTitleIds[3]),
            field: CertColumn.Issuer,
            hidden: false,
            width: 'unset'
        },
        {
            // SERIAL NO
            title: t(columnTitleIds[4]),
            field: CertColumn.SerialNo,
            hidden: false,
            cellStyle: { minWidth: '100px' },
            width: 'unset'
        },
        {
            // TARGET ECU
            title: t(columnTitleIds[5]),
            field: CertColumn.TargetEcu,
            hidden: false,
            cellStyle: { minWidth: '100px' },
            render: (row: CertificateData) => cropText(row.targetECU, 10),
            width: 'unset'
        },
        {
            // TARGET VIN
            title: t(columnTitleIds[6]),
            field: CertColumn.TargetVin,
            hidden: false,
            cellStyle: { minWidth: '110px' },
            render: (row: CertificateData) => cropText(row.targetVIN, 18),
            width: 'unset'
        },
        {
            // STATUS
            title: t(columnTitleIds[7]),
            field: CertColumn.Status,
            hidden: false,
            width: 'unset'
        },
        {
            // VALID TO
            title: t(columnTitleIds[8]),
            field: CertColumn.ValidTo,
            type: 'datetime',
            filterComponent: RangeDateTimeFilter,
            hidden: false,
            cellStyle: { minWidth: '140px' },
            render: (row: CertificateData) => renderDate(row.type, row.validTo),
            width: 'unset'
        },
        {
            // VALID FROM
            title: t(columnTitleIds[9]),
            field: CertColumn.ValidFrom,
            cellStyle: { minWidth: '140px' },
            type: 'datetime',
            filterComponent: RangeDateTimeFilter,
            hidden: true,
            render: (row: CertificateData) => renderDate(row.type, row.validFrom),
            width: 'unset'
        },
        {
            // VALIDITY
            title: t(columnTitleIds[10]),
            field: CertColumn.Validity,
            lookup: {
                green: t('certificates_validity_filter_high'),
                yellow: t('certificates_validity_filter_medium'),
                red: t('certificates_validity_filter_low')
            },
            hidden: false,
            render: (row: CertificateData) => renderValidity(row),
            width: 'unset'
        },
        {
            // UNIQUE ECU ID
            title: t(columnTitleIds[11]),
            field: CertColumn.UniqueEcuId,
            cellStyle: { minWidth: '100px' },
            hidden: true,
            render: (row: CertificateData) => cropText(row.uniqueECUID, 10),
            width: 'unset'
        },
        {
            // SPECIAL ECU
            title: t(columnTitleIds[12]),
            field: CertColumn.SpecialEcu,
            hidden: true,
            width: 'unset'
        },
        {
            // SUBJECT PUBLIC KEY
            title: t(columnTitleIds[13]),
            field: CertColumn.SubjectPubKey,
            hidden: true,
            cellStyle: { minWidth: '110px' },
            render: (row: CertificateData) => cropText(row.subjectPublicKey, 15),
            width: 'unset'
        },
        {
            // BASE CERTIFICATE ID
            title: t(columnTitleIds[14]),
            field: CertColumn.BaseCertId,
            hidden: true,
            render: (row: CertificateData) => cropText(row.baseCertificateID, 10),
            width: 'unset'
        },
        {
            // ISSUER SERIAL NO
            title: t(columnTitleIds[15]),
            field: CertColumn.IssuerSerialNo,
            hidden: true,
            width: 'unset'
        },
        {
            // SUBJECT KEY IDENTIFIER
            title: t(columnTitleIds[16]),
            field: CertColumn.Skid,
            hidden: true,
            cellStyle: { minWidth: '115px' },
            render: (row: CertificateData) => cropText(row.subjectKeyIdentifier, 15),
            width: 'unset'
        },
        {
            // AUTHORITY KEY IDENTIFIER
            title: t(columnTitleIds[17]),
            field: CertColumn.Akid,
            hidden: true,
            cellStyle: { minWidth: '115px' },
            render: (row: CertificateData) => cropText(row.authorityKeyIdentifier, 15),
            width: 'unset'
        },
        {
            // SERVICES
            title: t(columnTitleIds[18]),
            field: CertColumn.Services,
            hidden: true,
            render: (row: CertificateData) => cropText(row.services, 10),
            width: 'unset'
        },
        {
            // NONCE
            title: t(columnTitleIds[19]),
            field: CertColumn.Nonce,
            hidden: true,
            render: (row: CertificateData) => cropText(row.nonce, 15),
            width: 'unset'
        },
        {
            // SIGNATURE
            title: t(columnTitleIds[20]),
            field: CertColumn.Signature,
            hidden: true,
            render: (row: CertificateData) => cropText(row.signature, 10),
            width: 'unset'
        },
        {
            // TARGET SUBJECT KEY IDENTIFIER
            title: t(columnTitleIds[21]),
            field: CertColumn.TargetSkid,
            hidden: true,
            render: (row: CertificateData) => cropText(row.targetSubjectKeyIdentifier, 10),
            width: 'unset'
        },
        {
            // ZK Number
            title: t(columnTitleIds[22]),
            field: CertColumn.ZkNumber,
            hidden: true,
            width: 'unset'
        },
        {
            // ECU Package TS
            title: t(columnTitleIds[23]),
            field: CertColumn.EcuPackageTs,
            type: 'datetime',
            filterComponent: RangeDateTimeFilter,
            hidden: true,
            cellStyle: { minWidth: '140px' },
            render: (row: CertificateData) => renderDate(row.type, row.ecuPackageTs),
            width: 'unset'
        },
        {
            // Link Cert TS
            title: t(columnTitleIds[24]),
            field: CertColumn.LinkCertTs,
            type: 'datetime',
            filterComponent: RangeDateTimeFilter,
            hidden: true,
            cellStyle: { minWidth: '140px' },
            render: (row: CertificateData) => renderDate(row.type, row.linkCertTs),
            width: 'unset'
        },
        {
            // PKI known
            title: t(columnTitleIds[25]),
            field: CertColumn.PkiKnown,
            hidden: true,
            render: (row: CertificateData) => row.pkiKnown != null && String(row.pkiKnown),
            width: 'unset'
        },
        {
            // PKI state
            title: t(columnTitleIds[26]),
            field: CertColumn.PkiState,
            hidden: true,
            render: (row: CertificateData) => cropText(row.pkiState, 15),
            width: 'unset'
        }
    ]);

    // Update the column order and visibility
    useEffect(() => {
        if (props.isColumnShifted) {
            props.setIsColumnShifted(false);
        }
        userConfigService.getCertsColumnVisibility({ success: response => updateColumnVisibility(response.data) });
        userConfigService.getCertsColumnOrder({ success: response => updateColumnOrder(response.data.message) });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [lastUpdateTime, props.isColumnShifted]);

    // Update column titles when the app language has been changed
    useEffect(() => {
        const translatedColumns = columns.slice(0);
        for (var i = 0; i < translatedColumns.length; i++) {
            translatedColumns[i].title = t(columnTitleIds[i]);
        }
        setColumns(translatedColumns);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [i18n.language]);

    // If manual selection is enabled the row styling is changed
    useEffect(() => {
        // Update the styling of the rows if manual selection is enabled
        if (config[USER_CONFIG.CERT_SELECTION].configValue === 'manual') {
            columns[0].render = row => renderSubjectWithIcon(row, integrityCheckData);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [config[USER_CONFIG.CERT_SELECTION]]);

    // If integrity check data exists
    useEffect(() => {
        columns[0].render = row => renderSubjectWithIcon(row, integrityCheckData);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [integrityCheckData]);

    /**
     * Updates the visibility of the columns in the state based on the passed input.
     *
     * @param input the visibility of the columns as object
     */
    function updateColumnVisibility(input: any) {
        const visibility = JSON.parse(input.message);
        columns.forEach((column: Column<Certificate>) => {
            if (column.field) {
                column.hidden = !visibility[column.field];
            }
        });
    }

    /**
     * Updates the order of the columns in the state based on the passed input.
     *
     * @param input the order as a string
     */
    function updateColumnOrder(input: string) {
        const order: string[] = input.split(',');
        columns.forEach((column: any) => {
            if (column.field && column.tableData) {
                column.tableData.columnOrder = order.indexOf(column.field);
            }
        });
    }

    /**
     * Decides whether the passed row should be rendered as active for testing.
     * Based on the user config and the passed row.
     *
     * @param row the current row
     */
    function shouldShowTestingSelection(row: CertificateData): boolean {
        return config[USER_CONFIG.CERT_SELECTION].configValue === 'manual' && row.activeForTesting === true;
    }

    function hasIntegrityErrors(row: CertificateData, integrityCheckData?: IntegrityCheckDataHolder): boolean {
        if (integrityCheckData) {
            return integrityCheckData[row.id] !== undefined;
        }
        return false;
    }

    function renderSubjectWithIcon(row: CertificateData, integrityCheckData?: IntegrityCheckDataHolder): JSX.Element {
        return (
            <span className='certificate-icon-subject-cell'>
                {hasIntegrityErrors(row, integrityCheckData) ? (
                    <IntegrityCheckInfo row={row} integrityCheckData={integrityCheckData} />
                ) : (
                    ''
                )}
                <img
                    src={new CertificateData(row).getIconForType()}
                    alt='Icon representing certificate type'
                    className='certificate-icon'
                />
                {shouldShowTestingSelection(row) ? (
                    <span className='green bold'>
                        <StarIcon style={{ height: '15px' }} />
                        {row.subject}
                    </span>
                ) : (
                    cropSubject(row, 15)
                )}
            </span>
        );
    }

    return columns;
};

function cropSubject(row: CertificateData, length: number): string {
    if (row.type === 'VIRTUAL_FOLDER') {
        return row.subject;
    } else {
        return cropText(row.subject, length);
    }
}

function cropText(content: string | undefined, length: number): string {
    if (!content || content === undefined) {
        return '';
    }

    if (content && content.length > length) {
        return content.substr(0, length) + '...';
    }

    return content;
}

function renderDate(type: string, timestamp: number | string): string {
    if (type === CertificateType.VIRTUAL_FOLDER) {
        return '';
    } else {
        return formatTimestamp(timestamp, false);
    }
}

export function renderValidity(row: CertificateData) {
    const icon = new CertificateData(row).getValidityIcon();
    if (icon && icon.length > 0) {
        return <img src={icon} alt='Icon representing certificate validity' />;
    }
}
