import React, { useState } from 'react';

import Button from '@material-ui/core/Button';
import CloudOffIcon from '@material-ui/icons/CloudOff';
import LoginDialog from './LoginDialog';
import MenuItem from '@material-ui/core/MenuItem';
import OfflineLoginDialog from './OfflineLoginDialog';
import PersonIcon from '@material-ui/icons/Person';
import Popover from '@material-ui/core/Popover';
import RegistrationDialog from './RegistrationDialog';
import SessionTimer from './sessiontime/SessionTimer';
import { useAccountService } from './AccountService';
import { useStateValue } from '../state/State';
import { useTranslation } from 'react-i18next';

const menuId: string = 'navigation-menu';

const UserHandling = React.memo(function UserHandling() {
    const [{ user, isXentry }] = useStateValue();
    const { t } = useTranslation();
    const [{ certsUpdateProcess }] = useStateValue();
    const accountService = useAccountService();

    const [isOfflineLoginDialogOpen, setOfflineLoginDialogOpen] = useState(false);
    const [isUserMenuOpen, setUserMenuOpen] = useState(false);
    const [anchor, setAnchor] = useState();

    function handleClick(event: any) {
        setAnchor(event.currentTarget);
        setUserMenuOpen(!isUserMenuOpen);
    }

    function handleClose(event: any) {
        if (anchor === event.target) {
            return;
        }
        setUserMenuOpen(false);
    }

    function offlineLoginCallback() {
        setOfflineLoginDialogOpen(false);
    }

    function startOfflineLogin() {
        setOfflineLoginDialogOpen(true);
        setUserMenuOpen(false);
    }

    function startOnlineLogin() {
        accountService.onlineLogin();
        setUserMenuOpen(false);
    }

    function getLogout() {
        // if xentry mode or user is not online authenticated: user is not forwarded to backend logout page
        // instead a popup is shown that shows a message "you are logged out"
        if (isXentry || !user.isAuthenticatedAgainstBackend) {
            return (
                <MenuItem id='btn-logout' onClick={triggerLogout}>
                    {t('navigation_btn_user_logout')}
                </MenuItem>
            );
        }
        // OIDC logout flow: show link and follow forward to GAS logout page
        // this flow is required by the spec. Error case: not via popup but
        // just the result will be shown
        else {
            return (
                <div className='logout-link'>
                    <a href='/users/logout/oauth'>
                        <MenuItem id='btn-logout'>{t('navigation_btn_user_logout')}</MenuItem>
                    </a>
                </div>
            );
        }
    }

    function triggerLogout() {
        accountService.xentryLogout();
        setUserMenuOpen(false);
    }

    function getNavigationButton(buttonId: string) {
        return (
            <span>
                <Button
                    id={buttonId}
                    color='primary'
                    variant='text'
                    aria-owns={anchor ? menuId : undefined}
                    aria-haspopup='true'
                    onClick={handleClick}
                    disabled={certsUpdateProcess.running}
                >
                    {renderIcon()}
                    {user.isAuthenticated ? user.displayName : t('navigation_btn_user_login')}
                </Button>
            </span>
        );
    }

    function renderIcon() {
        if (user.isAuthenticated && !user.isAuthenticatedAgainstBackend) {
            return <CloudOffIcon className='btn-icon-left' />;
        }
        return <PersonIcon className='btn-icon-left' />;
    }

    function getUserHandlingUi() {
        return (
            <div className='user-handling'>
                {isXentry
                    ? getNavigationButton('navigation-btn-account-xentry')
                    : getNavigationButton('navigation-btn-account')}
                {!isXentry && (
                    <Popover
                        id={menuId}
                        anchorEl={anchor}
                        open={isUserMenuOpen}
                        onClose={handleClose}
                        anchorOrigin={{
                            vertical: 'bottom',
                            horizontal: 'center'
                        }}
                        transformOrigin={{
                            vertical: 'top',
                            horizontal: 'center'
                        }}
                    >
                        {user.isAuthenticated
                            ? getLogout()
                            : [
                                  <MenuItem id='btn-offline-login' key='btn-offline-login' onClick={startOfflineLogin}>
                                      {t('navigation_btn_user_offline_login')}
                                  </MenuItem>,
                                  <MenuItem id='btn-online-login' key='btn-online-login' onClick={startOnlineLogin}>
                                      {t('navigation_btn_user_online_login')}
                                  </MenuItem>
                              ]}
                    </Popover>
                )}
                <SessionTimer />
                {isOfflineLoginDialogOpen && <OfflineLoginDialog callback={offlineLoginCallback} />}
                <LoginDialog />
                <RegistrationDialog />
            </div>
        );
    }

    return <span>{isXentry && !user.isAuthenticated ? '' : getUserHandlingUi()}</span>;
});

export default UserHandling;
