import { APICallback } from '../../../common/API';
import { UseCase } from './UseCase';
import { useCertificatesService } from '../../CertificatesService';

export interface SelectionUpdate {
    activateCertForVsmUsecase: (selectionId: string) => Promise<void>;
    deactivateCertForVsmUsecase: (selectionId: string) => Promise<void>;
    activateCertForRpUsecase: (selectionId: string) => Promise<void>;
    deactivateCertForRpUsecase: (selectionId: string) => Promise<void>;
    activateCert: (selectionId: string, callback?: APICallback) => Promise<void>;
    deactivateCert: (selectionId: string, callback?: APICallback) => Promise<void>;
}

export const useSelectionUpdate = (): SelectionUpdate => {
    const certificatesService = useCertificatesService();

    return {
        activateCertForVsmUsecase: (selectionId: string) =>
            certificatesService.activateForUsecase(selectionId, UseCase.Vsm),
        deactivateCertForVsmUsecase: (selectionId: string) =>
            certificatesService.deactivateForUsecase(selectionId, UseCase.Vsm),
        activateCertForRpUsecase: (selectionId: string) =>
            certificatesService.activateForUsecase(selectionId, UseCase.Rp),
        deactivateCertForRpUsecase: (selectionId: string) =>
            certificatesService.deactivateForUsecase(selectionId, UseCase.Rp),
        activateCert: (selectionId: string, callback?: APICallback) =>
            certificatesService.activateForTesting(selectionId, callback),
        deactivateCert: (selectionId: string, callback?: APICallback) =>
            certificatesService.deactivateForTesting(selectionId, callback)
    };
};
