import { Dialog, DialogContent, DialogTitle } from '@material-ui/core';

import React from 'react';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';

const ServerOfflineDialog = () => {
    const [{ isServerOffline }] = useStateValue();
    const { t } = useTranslation();

    return (
        <Dialog id='dialog-server-offline' open={isServerOffline}>
            <DialogTitle id='dialog-server-offline-title' className='blue'>
                {t('dialog_server_offline_title')}
            </DialogTitle>
            <DialogContent id='dialog-server-offline-message'>{t('dialog_server_offline_message')}</DialogContent>
        </Dialog>
    );
};

export default ServerOfflineDialog;
