import React, { useEffect, useState } from 'react';
import { mapArrayToString, initFromConfig as parseFrom } from './RoleData';

import ArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import ArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import Button from '@material-ui/core/Button';
import { Column } from '@material-table/core';
import SelectableTable from '../../common/table/SelectableTable';
import { USER_CONFIG } from '../store/ConfigKeys';
import { defaultTableHeaderStyle } from '../../common/table/ZenzefiTable';
import { move } from './RowMover';
import { useStateValue } from '../../state/State';
import { useTranslation } from 'react-i18next';
import { useUserConfigService } from '../store/UserConfigStore';

const RolePriorityComponent = () => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [{ config }] = useStateValue();
    const priorityConfig = config[USER_CONFIG.USER_ROLE];
    const { t } = useTranslation();
    const userConfigService = useUserConfigService();

    const [roles, setRoles] = useState(parseFrom(priorityConfig));
    const [selectedRow, setSelectedRow] = useState<any>();
    const [isUpBtnDisabled, setUpBtnDisabled] = useState(true);
    const [isDownBtnDisabled, setDownBtnDisabled] = useState(true);
    const [updateWithTimeout, setUpdateWithTimeout] = useState<any>();

    let columns: Column<any>[] = [
        { title: t('configurations_priority_table_nr'), field: 'pos', width: '70px' },
        { title: t('configurations_priority_table_role'), field: 'name' }
    ];

    useEffect(() => {
        setRoles(parseFrom(priorityConfig));
    }, [priorityConfig]);

    function moveUp() {
        const currentPos: number = selectedRow.pos;
        moveRowFromTo(currentPos - 1, currentPos - 2);
    }

    function moveDown() {
        const currentPos: number = selectedRow.pos;
        moveRowFromTo(currentPos - 1, currentPos);
    }

    function moveRowFromTo(from: number, to: number) {
        const updatedItems = move(from, to, roles);

        // Update order of roles
        setRoles(updatedItems);
        updateBtnStates(updatedItems[to]);

        // Delayed update of config on server to prevent spam.
        clearTimeout(updateWithTimeout);
        setUpdateWithTimeout(
            setTimeout(function () {
                const updatedConfigValue = mapArrayToString(updatedItems);
                priorityConfig.configValue = updatedConfigValue;
                userConfigService.update(priorityConfig);
            }, 1500)
        );
    }

    function updateBtnStates(row: any): void {
        setSelectedRow(row);
        setUpBtnDisabled(row == null || row.pos < 2);
        setDownBtnDisabled(row == null || row.pos === roles.length);
    }

    return (
        <div className='role-priority-component'>
            <div className='config-heading'>{t('configurations_priority_title')}</div>
            <div className='config-description'>{t('configurations_priority_desc')}</div>
            <div>
                <div className='roles-table'>
                    <SelectableTable
                        keyboardNavigation={false}
                        modelIdentifier='name'
                        updateSelection={updateBtnStates}
                        tableProps={{
                            columns: columns,
                            options: {
                                filtering: false,
                                search: false,
                                draggable: false,
                                sorting: false,
                                showTitle: false,
                                paging: false,
                                toolbar: false,
                                headerStyle: defaultTableHeaderStyle
                            },
                            data: roles
                        }}
                    />
                </div>
                <div className='move-buttons'>
                    <Button
                        id='move-up-btn'
                        color='primary'
                        disableFocusRipple={true}
                        variant='outlined'
                        disabled={isUpBtnDisabled}
                        onClick={moveUp}
                    >
                        <ArrowUpIcon />
                    </Button>
                    <Button
                        id='move-down-btn'
                        color='primary'
                        disableFocusRipple={true}
                        variant='outlined'
                        disabled={isDownBtnDisabled}
                        onClick={moveDown}
                    >
                        <ArrowDownIcon />
                    </Button>
                </div>
            </div>
        </div>
    );
};

export default RolePriorityComponent;
