import AccountsComponent from './accounts/AccountsComponent';
import PkiScopeConfigComponent from './pkiscope/PkiScopeConfigComponent';
import ProxyComponent from './proxy/ProxyComponent';
import React from 'react';
import { useTranslation } from 'react-i18next';

const GeneralConfigComponent = () => {
    const { t } = useTranslation();

    return (
        <div className='general-configurations'>
            <div className='configurations-heading'>{t('configurations_general_heading')}</div>
            <ProxyComponent initialHostValueState={false} />
            <AccountsComponent />
            <PkiScopeConfigComponent />
        </div>
    );
};

export default GeneralConfigComponent;
