import '../../App.css';

import DeleteCertificate from './delete/DeleteCertificate';
import ExportCertificate from './export/ExportCertificate';
import ImportCertificates from './import/ImportCertificates';
import IntegrityCheck from './integrity/IntegrityCheck';
import ManualSelection from './manualselection/ManualSelection';
import React from 'react';
import RefreshCertificates from './refresh/RefreshCertificates';
import RestoreCertificates from './restore/RestoreCertificates';
import UpdateCertificates from './update/UpdateCertificates';

export interface CertificatesToolbarProps {
    selection: any;
    refreshCallBack: () => void;
    deleteDataByKey: (flag: boolean) => void;
    deleteKeyPressed: boolean;
}

export const CertificatesToolbar = (props: CertificatesToolbarProps) => {
    return (
        <div className='certificates-toolbar'>
            <RefreshCertificates refreshCallBack={props.refreshCallBack} />
            <UpdateCertificates refreshCallBack={props.refreshCallBack} />
            <ImportCertificates refreshCallBack={props.refreshCallBack} />
            <ExportCertificate selection={props.selection} />
            <RestoreCertificates refreshCallBack={props.refreshCallBack} />
            <IntegrityCheck />
            <DeleteCertificate
                selection={props.selection}
                refreshCallBack={props.refreshCallBack}
                deleteKeyPressed={props.deleteKeyPressed}
                deleteDataByKey={props.deleteDataByKey}
            />
            <ManualSelection />
        </div>
    );
};

export default CertificatesToolbar;
