import React, { useState } from 'react';

import { Button } from '@material-ui/core';
import { CertificateType } from '../CertificateData';
import ExportDialog from './ExportDialog';
import ExportIcon from '@material-ui/icons/OpenInNew';
import { useTranslation } from 'react-i18next';

export interface ExportCertProps {
    selection: any;
    blockClickAway: () => void;
    closeMenu: (refresh: boolean) => void;
}

const isDisabled = function (selection: any): boolean {
    // null and undefined checks are allowed with "=="
    // eslint-disable-next-line eqeqeq
    return selection == undefined || selection.type === CertificateType.VIRTUAL_FOLDER
        || selection.state === 'SIGNING_REQUEST';
};

const ExportCertificateContextMenu = (props: ExportCertProps) => {
    const [isDialogOpen, setDialogOpen] = useState<boolean>(false);
    const { t } = useTranslation();

    function closeDialogAndMenu() {
        setDialogOpen(false);
        props.closeMenu(false);
    }

    function openDialog() {
        props.blockClickAway();
        setDialogOpen(true);
    }

    return (
        <span>
            {isDialogOpen && <ExportDialog closeDialog={closeDialogAndMenu} certificate={props.selection} />}
            {!Array.isArray(props.selection) && (
                <Button
                    id='context-menu-export-cert'
                    className='certificate-context-menu-item'
                    onClick={openDialog}
                    disabled={isDisabled(props.selection)}
                >
                    <ExportIcon className='btn-icon-left' />
                    {t('certificates_export_btn')}
                </Button>
            )}
        </span>
    );
};

export default ExportCertificateContextMenu;
