import { StateAction, StateActionType } from './StateAction';

import { isConfigDataArray } from './models/ConfigData';
import { isMessageAlertData } from './models/MessageAlertData';
import { isMessageData } from './models/MessageData';
import { isProcessData } from './models/ProcessData';
import { isUpdateProcessData } from './models/UpdateProcessData';
import { isUserData } from './models/UserData';

export const StateReducer = (state: string[], action: StateAction) => {
    switch (action.type) {
        case StateActionType.UPDATE_USER:
            if (isUserData(action.data)) {
                return {
                    ...state,
                    user: action.data
                };
            } else {
                throwErrorWithMessage('UserData', action.data);
            }
            break;

        case StateActionType.UPDATE_PROCESS:
            if (isProcessData(action.data)) {
                return {
                    ...state,
                    process: action.data
                };
            } else {
                throwErrorWithMessage('ProcessData', action.data);
            }
            break;

        case StateActionType.UPDATE_MESSAGE:
            if (isMessageData(action.data)) {
                return {
                    ...state,
                    message: action.data
                };
            } else {
                throwErrorWithMessage('MessageData', action.data);
            }
            break;

        case StateActionType.UPDATE_MESSAGE_ALERT:
            if (isMessageAlertData(action.data)) {
                return {
                    ...state,
                    messageAlert: action.data
                };
            } else {
                throwErrorWithMessage('MessageAlertData', action.data);
            }
            break;

        case StateActionType.UPDATE_CONFIG:
            if (isConfigDataArray(action.data)) {
                return {
                    ...state,
                    config: action.data
                };
            } else {
                throwErrorWithMessage('ConfigData', action.data);
            }
            break;

        case StateActionType.UPDATE_XENTRY:
            if (typeof action.data === 'boolean') {
                return {
                    ...state,
                    isXentry: action.data
                };
            } else {
                throwErrorWithMessage('IsXentry', action.data);
            }
            break;

        case StateActionType.UPDATE_INTEGRITY_CHECK_DATA:
            if (action.data) {
                return {
                    ...state,
                    integrityCheckData: action.data.integritycheckerrormap
                };
            } else {
                throwErrorWithMessage('Integrity Check Data', action.data);
            }
            break;

        case StateActionType.UPDATE_SERVER_OFFLINE:
            if (typeof action.data === 'boolean') {
                return {
                    ...state,
                    isServerOffline: action.data
                };
            } else {
                throwErrorWithMessage('IsServerOffline', action.data);
            }
            break;

        case StateActionType.UPDATE_CERT_UPDATE_STATUS:
            if (isUpdateProcessData(action.data)) {
                return {
                    ...state,
                    certsUpdateProcess: action.data
                };
            } else {
                throwErrorWithMessage('UpdateCertificatesProcessData', action.data);
            }
            break;

        case StateActionType.UPDATE_INTEGRITY_CHECK_EXISTENCE:
            if (typeof action.data === 'boolean') {
                return {
                    ...state,
                    isIntegrityCheckExisting: action.data
                };
            } else {
                throwErrorWithMessage('IntegrityCheckExistence', action.data);
            }
            break;

        case StateActionType.TRIGGER_UPDATE:
            if (action.data instanceof Date) {
                return {
                    ...state,
                    lastUpdateTime: action.data
                };
            } else {
                throwErrorWithMessage('TriggerUpdate', action.data);
            }
            break;

        default:
            throw new Error('StateReducer in default case: ' + action.type);
    }
};

function throwErrorWithMessage(type: string, input: string) {
    throw new Error('Tried to update ' + type + ' with wrong input data: ' + JSON.stringify(input));
}
