import React, { ChangeEvent } from 'react';

interface ImportCertProps {
    onChangeCallback: (event: ChangeEvent<HTMLInputElement>) => void;
    refCallback: (input: any) => void;
}

const ImportCertificateComponent = (props: ImportCertProps) => {
    return (
        <input
            id='import-certificate-input'
            ref={props.refCallback}
            type='file'
            multiple
            accept='.crt,.p12'
            name='files[]'
            value=''
            onChange={e => props.onChangeCallback(e)}
            style={{ display: 'none' }}
        />
    );
};

export default ImportCertificateComponent;
