import { CertificateData, CertificateType } from '../CertificateData';
import React, { useState } from 'react';

import { Button } from '@material-ui/core';
import ExportDialog from './ExportDialog';
import ExportIcon from '@material-ui/icons/OpenInNew';
import { useTranslation } from 'react-i18next';

export interface ExportCertProps {
    selection: CertificateData;
}

const ExportCertificate = (props: ExportCertProps) => {
    const [isDialogOpen, setDialogOpen] = useState<boolean>(false);
    const { t } = useTranslation();

    function closeDialog() {
        setDialogOpen(false);
    }

    function isDisabled(): boolean {
        return (
            // null and undefined checks are allowed with "=="
            // eslint-disable-next-line eqeqeq
            props.selection == undefined ||
            Array.isArray(props.selection) ||
            props.selection.type === CertificateType.VIRTUAL_FOLDER ||
            props.selection.state === 'SIGNING_REQUEST'
        );
    }

    return (
        <span>
            {isDialogOpen && <ExportDialog closeDialog={closeDialog} certificate={props.selection} />}
            <Button
                id='certificates-export-btn'
                color='primary'
                variant='outlined'
                className='certificates-toolbar-button'
                onClick={e => setDialogOpen(true)}
                disabled={isDisabled()}
            >
                <ExportIcon className='btn-icon-left' />
                {t('certificates_export_btn')}
            </Button>
        </span>
    );
};

export default ExportCertificate;
