import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core';

import React from 'react';
import { useTranslation } from 'react-i18next';

interface RestoreCertificatesDialogProps {
    closeDialog: (confirmed: boolean) => void;
}

const RestoreCertificatesDialog = (props: RestoreCertificatesDialogProps) => {
    const { t } = useTranslation();

    function restoreCertificates(): void {
        props.closeDialog(true);
    }

    function close(): void {
        props.closeDialog(false);
    }

    return (
        <Dialog id='restore-certificates-dialog' open={true}>
            <DialogTitle id='restore-certificates-dialog-title' className='blue'>
                {t('certificates_restore_confirm_dialog_title')}
            </DialogTitle>
            <DialogContent id='restore-certificates-dialog-content'>
                {t('certificates_restore_confirm_dialog_content')}
            </DialogContent>
            <DialogActions>
                <Button id='restore-certificates-dialog-cancel-btn' onClick={close} color='primary' variant='outlined'>
                    {t('common_cancel')}
                </Button>
                <Button
                    id='restore-certificates-dialog-restore-btn'
                    onClick={restoreCertificates}
                    color='primary'
                    variant='contained'
                >
                    {t('certificates_restore_btn_dialog')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default RestoreCertificatesDialog;
