import Button from '@material-ui/core/Button';
import Language from './Language';
import React from 'react';
import { useTranslation } from 'react-i18next';

const LanguageSelection = React.memo(function LanguageSelection() {
    const { t, i18n } = useTranslation();

    function switchLanguage() {
        if (i18n.language === Language.english) {
            i18n.changeLanguage(Language.german);
        } else {
            i18n.changeLanguage(Language.english);
        }
    }

    return (
        <Button id='navigation-btn-language' color='primary' variant='text' onClick={switchLanguage}>
            {t('navigation_btn_switch_language')}
        </Button>
    );
});

export default LanguageSelection;
