import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Select, TextField } from '@material-ui/core';
import { CertificateData, CertificateType } from '../CertificateData';
import React, { useEffect, useState } from 'react';

import { useExportCertificate } from './ExportHook';
import { useTranslation } from 'react-i18next';

interface ExportDialogProps {
    certificate: CertificateData;
    closeDialog: Function;
}

export enum ExportType {
    CONFIG_5 = 'Configurator 5',
    C_ARRAY = 'C Array',
    CA_NOE = 'CANoe'
}

const ExportDialog = (props: ExportDialogProps) => {
    const [selection, setSelection] = useState<ExportType>(ExportType.CONFIG_5);
    const [copiedMessage, setCopiedMessage] = useState<string>('');
    const [certificateValue, setCertificateValue] = useState<string>('');

    const { t } = useTranslation();
    var typeToExport: Map<ExportType, string> = useExportCertificate(props.certificate.id);

    useEffect(() => {
        const currentValue = typeToExport.get(selection);
        if (currentValue) {
            setCertificateValue(currentValue);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [typeToExport]);

    /**
     * Copies the current certificate export value to the system's clipboard
     * and shows a success notification.
     */
    function copyToClipboard() {
        if (navigator.clipboard) {
            navigator.clipboard.writeText(certificateValue);
            setCopiedMessage(t('certificates_export_dialog_copy_success'));
        } else {
            setCopiedMessage(t('certificates_export_dialog_copy_failed'));
        }
    }

    function close() {
        props.closeDialog();
    }

    /**
     * Handles selection changes in the export type dropdown.
     * Updates the export certificate textfield based on the selected type.
     *
     * @param event the selection change event
     */
    const exportTypeChanged = (event: React.ChangeEvent<{ value: any }>) => {
        setSelection(event.target.value);
        setCopiedMessage('');

        const certValue = typeToExport.get(event.target.value);
        if (certValue != null) {
            setCertificateValue(certValue);
        }
    };

    /**
     * Validates whether the 'backend SKID' table row should be shown
     * based on the passed certificate's type.
     *
     * @param certificate the current certificate
     */
    function shouldShowBackendSkid(certificate: CertificateData): boolean {
        switch (certificate.type) {
            case CertificateType.DIAGNOSTIC:
            case CertificateType.ECU:
            case CertificateType.TIME:
            case CertificateType.VARIANT_CODING:
                return true;
            default:
                return false;
        }
    }

    return (
        <Dialog open={true}>
            <DialogTitle id='export-dialog-title' className='blue'>
                {t('certificates_export_dialog_title')}
            </DialogTitle>
            <DialogContent>
                <table id='export-certificate-table'>
                    <tbody>
                        <tr>
                            <td id='export-dialog-subject-label' className='bold'>
                                {t('certificates_export_dialog_subject')}
                            </td>
                            <td id='export-dialog-pki-role-label' className='bold'>
                                {t('certificates_export_dialog_pki_role')}
                            </td>
                        </tr>
                        <tr className='export-certificate-table-value'>
                            <td id='export-dialog-subject'>{props.certificate.subject}</td>
                            <td id='export-dialog-pki-role'>{props.certificate.pkirole}</td>
                        </tr>
                        <tr>
                            <td id='export-dialog-serial-no-label' className='bold'>
                                {t('certificates_export_dialog_serial_no')}
                            </td>
                            <td id='export-dialog-user-role-label' className='bold'>
                                {t('certificates_export_dialog_user_role')}
                            </td>
                        </tr>
                        <tr className='export-certificate-table-value'>
                            <td id='export-dialog-serial-no'>{props.certificate.serialNo}</td>
                            <td id='export-dialog-user-role'>{props.certificate.userRole}</td>
                        </tr>
                        {shouldShowBackendSkid(props.certificate) && (
                            <>
                                <tr>
                                    <td id='export-dialog-bskid-label' colSpan={2} className='bold'>
                                        {t('certificates_export_dialog_bskid')}
                                    </td>
                                </tr>
                                <tr className='export-certificate-table-value'>
                                    <td id='export-dialog-bskid' colSpan={2}>
                                        {props.certificate.authorityKeyIdentifier}
                                    </td>
                                </tr>
                            </>
                        )}
                    </tbody>
                </table>
                <div className='export-certificate-value-container'>
                    {t('certificates_export_dialog_type_select_label')}
                    <Select
                        id='export-dialog-type-select'
                        native
                        value={selection}
                        onChange={event => exportTypeChanged(event)}
                    >
                        <option value={ExportType.CONFIG_5}>{ExportType.CONFIG_5}</option>
                        <option value={ExportType.C_ARRAY}>{ExportType.C_ARRAY}</option>
                        <option value={ExportType.CA_NOE}>{ExportType.CA_NOE}</option>
                    </Select>
                    <TextField
                        id='certificate-export-value'
                        value={certificateValue}
                        style={{ width: '100%', marginTop: '10px' }}
                        InputProps={{ readOnly: true }}
                    />
                    <div id='export-dialog-copied-message' style={{ minHeight: '20px' }} className='blue font-size-90'>
                        {copiedMessage}
                    </div>
                </div>
            </DialogContent>
            <DialogActions>
                <Button id='export-dialog-close-btn' onClick={close} color='primary' variant='outlined'>
                    {t('common_close')}
                </Button>
                <Button id='export-dialog-copy-btn' onClick={copyToClipboard} color='primary' variant='contained'>
                    {t('certificates_export_dialog_copy_btn')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default ExportDialog;
